import nearley from "nearley"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { isProd } from "../../core/NodeEnv"
import {
  DoNotSendErrorFlag,
  NonRetriableErrorFlag
} from "../../core/error/ErrorFlags"
import { gt0 } from "../../fe/Number"
import { Query } from "./Query"
import grammar from "./grammar"

const logger = lazy(() => mkLogger("query.QueryParser"))

/**
 * @throws on invalid
 */
export function parseQuery_(input: string): Query {
  try {
    const result = new nearley.Parser(nearley.Grammar.fromCompiled(grammar))
      .feed(input.trim())
      .finish()

    if (result.length > 1 && !isProd) {
      logger().throw("parseQuery(): ambiguous grammar!", { input })
    }
    if (result[0] == null) {
      throw new Error("invalid query")
    }
    logger().debug("parseQuery()", { input, result: result[0] })
    return result[0]
  } catch (err: any) {
    const col = err.token?.col
    const msg = `Query syntax error${
      gt0(col) ? " around column " + col : ""
    }: "${input}"`
    logger().warn("parseQuery(): bad query", { msg, err })
    throw new Error(msg + NonRetriableErrorFlag + DoNotSendErrorFlag)
  }
}
