import e from "express"
import { DateTime } from "luxon"
import { delimiter, resolve, sep } from "path"
import { mkLogger } from "../../core/Logger"
import { isProd, isTest } from "../../core/NodeEnv"
import { stdoutWrite } from "../../core/StdoutWrite"
import { libraryDirPosixFile } from "../../core/dir/LibraryDirs"
import { picturesDir } from "../../core/dir/PicturesDir"
import { ee } from "../../core/event/EventEmitter"
import { disableCache, wrap } from "../../core/express/Handlers"
import { BaseFile } from "../../core/fs/BaseFile"
import { getVideoToolDetails_ } from "../../core/img/Video"
import { b, t } from "../../core/licensing/Licensing"
import { isMac, isWin } from "../../core/platform/Platform"
import { isDocker } from "../../core/platform/isDocker"
import { pathToLibraryAsset } from "../../core/settings/PathToLibraryAsset"
import { Settings } from "../../core/settings/Settings"
import {
  libraryHasSettings,
  readSettings
} from "../../core/settings/SettingsIO"
import { systemUserAgent } from "../../core/version/VersionCheck"
import { compactBlanks, isEmpty } from "../../fe/Array"
import { blank, mapNotBlank, notBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { fmtIsoDate } from "../../fe/Date"
import { fmtDuration } from "../../fe/FmtDuration"
import { stringify } from "../../fe/JSON"
import { Later } from "../../fe/Later"
import { map, mapOr } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { pick } from "../../fe/Object"
import { thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { Thunk, ThunkMaybe } from "../../fe/Thunk"
import { GB, fmtBytes } from "../../fe/Units"
import { toA } from "../../fe/toA"
import { toS } from "../../fe/toS"
import {
  LibrarySettings,
  librarySettings,
  saveLibrarySettings
} from "../LibrarySettings"
import {
  SuggestedLibraryDir,
  suggestedLibraryDirs
} from "../SuggestedLibraryDir"
import { render } from "./PugHelpers"

const logger = mkLogger("web.SettingsRouter")

function samplePath(): string {
  return pathToLibraryAsset(DateTime.now(), BaseFile.for("photo.jpg"))!.join(
    sep
  )
}

export class SettingsRouter {
  constructor(
    readonly libraryDir: ThunkMaybe<BaseFile> = libraryDirPosixFile,
    readonly settings: Thunk<LibrarySettings> = librarySettings,
    readonly libraryDirSuggester: Later<
      SuggestedLibraryDir[]
    > = suggestedLibraryDirs
  ) {}

  router(): e.Router {
    return e
      .Router()
      .get("/setting", (_, res) => res.redirect("/settings"))
      .get("/settings", wrap("GET /settings", this.get.bind(this)))
      .post("/settings", wrap("POST /settings", this.post.bind(this)))
  }

  async get(req: e.Request, res: e.Response) {
    const { err, libraryDir } = req.body
    try {
      await readSettings()
      const s = await thenOrTimeoutError(this.requestSettings(req), 1000)
      return await thenOrTimeoutError(
        this.renderSettings(req, res, s, libraryDir, err),
        1000
      )
    } catch (error) {
      logger.warn(
        "requestSettings() took too long. Redirecting to the health check page.",
        error
      )
      return res.redirect("/health")
    }
  }

  async renderSettings(
    req: e.Request,
    res: e.Response,
    settings: LibrarySettings,
    libraryDirFromRequest: Maybe<string>,
    err?: string
  ) {
    // Don't use any cached values for volumes, suggested paths, tool settings, ...
    ee().emit("clearCache")

    let libraryDirSuggestions = await this.libraryDirSuggester()
    // don't add the library path from request if there was an error
    if (toS(err).match(/choose a different directory/i) == null) {
      const s = await SuggestedLibraryDir.for(libraryDirFromRequest)
      if (s != null) {
        libraryDirSuggestions.unshift(s)
      }
    }
    if (isEmpty(libraryDirSuggestions)) {
      libraryDirSuggestions = undefined as any
    }

    const libraryDir = this.libraryDir()
    if (blank(err) && isDocker()) {
      // Validate libraryDir exists on Docker:
      if (libraryDir == null || (await libraryDir.isNotDirectory())) {
        err = `PS_LIBRARY_DIR, ${
          libraryDir?.nativePath ?? "/ps/library"
        }, doesn't exist: please add it as a bind mount.`
      }
    }

    const videoToolDetails = await getVideoToolDetails_(
      // Allow tests to pretend there's no ffmpeg:
      isProd ? undefined : (req.query as any)
    )

    const picturesPath = await picturesDir()

    const best = await b()

    const model = {
      err,
      ...settings,
      welcome: mapOr(
        isProd ? undefined : req.query.welcome,
        isTrue,
        () => !libraryHasSettings()
      ),
      title: libraryDir == null ? "Welcome!" : "Settings",
      videoToolDetails: videoToolDetails,
      picturesPath,
      jsonScanPaths: map(Settings.scanPaths.value, stringify),
      delimiter,
      exampleScanPath: [
        picturesPath,
        isWin
          ? "D:\\PHOTOS"
          : isMac
          ? "/Volumes/External HD/Pictures"
          : "/mnt/nas/homes/photos"
      ].join(delimiter),
      currentLibraryDir: map(libraryDir, ea => ea.nativePath),
      minFreeAvailable: fmtBytes(Settings.minDiskFreeGb.valueOrDefault * GB),
      suggestedLibraryDirs: libraryDirSuggestions,
      samplePath: samplePath(),
      autoUpdateCheck: Settings.autoUpdateCheck.valueOrDefault,
      allowUserAgent: Settings.allowUserAgent.valueOrDefault,
      reportErrors: Settings.reportErrors.valueOrDefault,
      // This is the actual current subscription tier:
      subTier: await t(),
      subTrial: isTrue(best?.l?.trial),
      subEmail: best?.l?.sub,
      subExpiresInDuration: map(best?.l?.exp, exp =>
        fmtDuration(exp.getTime() - Date.now(), 1)
      ),
      subExpiresAt: map(best?.l?.exp, fmtIsoDate),
      userAgent: await systemUserAgent()
    }
    logger.info("rendering settings", model)

    // Force-reload the page with the back button so they don't see stale
    // settings
    disableCache(res)
    await render(req, res, "settings", model)
    return
  }

  async post(req: e.Request, res: e.Response): Promise<any> {
    const body = req.body
    const libraryDir =
      mapNotBlank(
        body.libraryDirRadio === "libraryDirCustom"
          ? body.libraryDir
          : body.libraryDirRadio,
        resolve
      ) ?? Settings.libraryDir.valueOrDefault
    const s = await this.requestSettings(req)
    const fail = (error: any) =>
      this.renderSettings(req, res, s, libraryDir, error)

    if (blank(libraryDir)) {
      return fail("Please choose a directory for your library.")
    }

    logger.info("POST", { libraryDir, settings: s, body })
    // This allows specs to see what express gave us:
    if (isTest) {
      stdoutWrite({ post: body, settings: s, method: req.method, libraryDir })
    }

    try {
      const result = await saveLibrarySettings(libraryDir, s)
      if ("error" in result) {
        logger.info("error saving library settings", { result })
        return fail(result.error)
      } else {
        logger.info(
          "saved library settings. Waiting for bookkeeping to finish...",
          result
        )
        await result.ready
        logger.info("ready. Redirecting to /splash...", result)
      }
    } catch (err) {
      return fail(err)
    }
    return res.redirect("/splash")
  }

  private async requestSettings(req: e.Request): Promise<LibrarySettings> {
    const { scan, scanPath, copyAssets, reportErrors } = req.body
    const s = this.settings()
    if (notBlank(scan)) {
      s.scanAllDrives = scan === "scanAllDrives"
      // req.body might just be a string or a string[] (!!)
      s.scanPath = compactBlanks(toA(scanPath))
    }
    if (notBlank(copyAssets)) {
      s.copyAssets = copyAssets === "copyEnabled"
    }
    s.reportErrors = isTrue(reportErrors)
    s.autoUpdateCheck = req.body.autoUpdateCheck
    logger.info("requestSettings()", {
      req_body: pick(
        req.body,
        "scan",
        "scanPath",
        "copyAssets",
        "autoUpdateCheck",
        "allowUserAgent",
        "reportErrors"
      ),
      result: s
    })
    return librarySettings(s)
  }
}
