import { sendErrorToast, wrapWithToast } from "./ServerToastHelpers"

const logger = lazy(() => mkLogger("express.Handlers"))

export function wrap(name: string, handler: RequestHandler): RequestHandler {
  return async (req: Request, res: Response, next: NextFunction) =>
    time(name, () =>
      wrapWithToast({ f: () => handler(req, res, next), response: res })
    )
}

export function getSeed(req: Request): Maybe<number> {
  return toSeed(req.query.seed)
}

export function getFit(req: Request): ThumbFit {
  return ThumbFits.validOrElse(toS(req.query.fit), ThumbFits.square)
}

export function getSeedOrRedirect(req: Request, res: Response): Maybe<number> {
  const seed = toSeed(req.query.seed)
  if (seed == null) {
    let u = requestedUri(req)
    // normalize tag path:
    if (u.path === "/tag/") u = u.with({ path: "/tag" })
    u.query.append("seed", toS(prngSeed()))
    res.redirect(u.toString())
  }
  return seed
}

export function jsonOrRedirect(
  obj: Maybe<Redirect | Replace | Obj>,
  response: Response
) {
  if (obj == null) {
    return sendErrorToast(
      new WrappedError(
        "Internal error: jsonOrRedirect(null)" + InternalErrorFlag,
        {
          fatal: false,
          doNotSend: true
        }
      ),
      response,
      404
    )
  }
  for (const k of [XLocationRedirect, XLocationReplace]) {
    const v = (obj as any)[k]
    if (notBlank(v)) {
      logger().debug("jsonOrRedirect(): " + k, v)
      response.setHeader(k, v)
      response.status(HttpStatus.SeeOther)
    }
  }
  return response.json(obj)
}

export function extractTagPath(path: string): string[] {
  // Ignore "/tag" and strip "^/tag/" prefix:
  const trimmed = path.replace(/^(?:\/api)?\/tag\/?/, "")
  const tagPath = trimmed.split("/").map(decodeURIComponent)
  const result = blank(trimmed) ? [] : tagPath
  logger().info("extractTagPath()", { path, result, trimmed, tagPath })
  return result
}

// export function requestLocale(req: Request): Maybe<string> {
//   return mapNotBlank(requestLang(req), lc2locale)
// }

export function requestLang(req: Request): Maybe<string> {
  const s = req.headers["accept-language"]
  return extractLang(Array.isArray(s) ? s.join(",") : s)
}

// export function extractLocale(acceptLanguage: Maybe<string>): Maybe<string> {
//   return mapNotBlank(extractLang(acceptLanguage), lc2locale)
// }

export function extractLang(acceptLanguage: Maybe<string>): Maybe<string> {
  return opt(acceptLanguage)
    .filter(notBlank)
    .map(toS)
    .map(ea => ea.split(","))
    .map(compactBlanks)
    .map(arr => arr.map(ea => ea.split(";").map(s => s.trim())))
    .flatMap(arr =>
      // At this point we have pairs of [lang, quality] (where quality is either missing, and defaults to 1, or is "q=0.6")
      greatestBy(arr, ([, quality]) => firstGt0(extractFloat(quality), 1)!)
    )
    .map(([lang]) => lang)
    .get()
}

/**
 * A query request param is "truthy" if it exists and the value is not falsy.
 */
export function truthyQuery(req: Request, name: string): boolean {
  return mapOr(
    req.query[name],
    ea => !isFalse(ea),
    () => false
  )
}

export function disableCache(res: Response) {
  res.setHeader(
    // https://developer.mozilla.org/en-US/docs/Web/HTTP/Headers/Cache-Control
    "Cache-Control",
    "no-store, max-age=0"
  )
  res.setHeader("Pragma", "no-cache")
  res.setHeader("Expires", "Fri, 01 Jan 1990 00:00:00 GMT")
}
