import { ServerToast } from "./ServerToast"

export const XLocationRedirect = "X-Location-Redirect"

export interface Redirect {
  [XLocationRedirect]: string
  toast?: ServerToast
}

export function mkRedirect(url: string, toast?: ServerToast): Redirect {
  return { [XLocationRedirect]: url, toast }
}

export const XLocationReplace = "X-Location-Replace"

export interface Replace {
  [XLocationReplace]: string
  toast?: ServerToast
}

export function mkReplace(url: string, toast?: ServerToast): Replace {
  return { [XLocationReplace]: url, toast }
}
