import { IncomingHttpHeaders } from "http"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { StringValued } from "../../fe/Object"
import { toA } from "../../fe/toA"
import { equalsIgnoreCase, stripQuotes } from "../String"
import { Settings } from "../settings/Settings"

/**
 * @see https://developer.mozilla.org/en-US/docs/Web/HTTP/Headers/Forwarded
 */
export function parseForwarded(forwarded: Maybe<string | string[]>) {
  if (blank(forwarded)) return {}
  let s = toA(forwarded)
    .join(";")
    .trim()
    .replace(/^[,;]/, "")
    .replace(/[,;]$/, "")
  // Forwarded may be separated by commas or semicolons. Values may be quoted,
  // and then they may contain commas or semicolons.
  // by=<identifier>;for=<identifier>;host=<host>;proto=<http|https>
  const result: StringValued = {}
  while (s.length > 0) {
    const kIdx = s.indexOf("=")
    if (kIdx < 0) break
    const k = stripQuotes(s.slice(0, kIdx).trim().toLowerCase())
    s = s.slice(kIdx + 1).trim()
    if (/^["']/.test(s)) {
      const vIdx = /['"]\s*(?:[;,]|$)/.exec(s.slice(1))?.index
      if (vIdx == null) break
      const v = stripQuotes(s.slice(0, vIdx + 2).trim())
      result[k] = v
      console.log("quoted", { k, v, s, vIdx })
      s = s.slice(vIdx + 3).trim()
    } else {
      const vIdx = /[;,]|$/.exec(s)?.index
      if (vIdx == null) break
      const v = s.slice(0, vIdx).trim()
      result[k] = v
      console.log("unquoted", { k, v, s, vIdx })
      s = s.slice(vIdx + 2).trim()
    }
  }

  return result
}

function isEmptyArray(ea: any): boolean {
  return Array.isArray(ea) && ea.length === 0
}

function str(ea: string | string[] | undefined | null) {
  return blank(ea) || isEmptyArray(ea) ? undefined : toA(ea).join(";")
}

export function isHttpsRequest(h: IncomingHttpHeaders): boolean {
  if (h == null || Settings.trustProxy.valueOrDefault === "false") return false
  return (
    equalsIgnoreCase(
      "https",
      parseForwarded(h.forwarded).proto ??
        str(h["x-forwarded-protocol"]) ??
        str(h["x-url-scheme"])
    ) ||
    str(h["front-end-https"]) === "on" ||
    str(h["x-forwarded-ssl"]) === "on"
  )
}

export function isAjaxRequest(h: Maybe<IncomingHttpHeaders>): boolean {
  return h?.["content-type"] === "application/json"
}
