import { notBlank } from "../Blank"

export interface ToastPlusOnly {
  toastPlusOnly: true
}

export function isToastPlusOnly(o: any) {
  return o?.toastPlusOnly === true
}

export interface ServerToastComponent {
  text: string
  details?: string
  onClick?: (closeToast: () => void) => any

  button1Text?: string
  button1Class?: string
  button1Title?: string
  button1OnClick?: (closeToast: () => void) => any

  button2Text?: string
  button2Class?: string
  button2Title?: string
  button2OnClick?: (closeToast: () => void) => any
}

export const DismissAnchor = "#dismiss"

/**
 * These are sent as JSON by the server (hence the Omit to elide the onClick
 * functions)
 */
export interface ServerToast
  extends Omit<
    ServerToastComponent,
    "onClick" | "button1OnClick" | "button2OnClick"
  > {
  type?: "success" | "error" | "warning" | "info" | "default"
  uuid?: string
  onlyShowUuidOnce?: boolean
  onClickUrl?: string
  button1Url?: string
  button2Url?: string
  timeout?: false | number
}

export function isServerToast(o: any): o is ServerToast {
  return notBlank(o?.text)
}
