import { lazy } from "../../core/Lazy"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { readFileType_ } from "../fs/FileType"
import { PosixFile } from "../fs/PosixFile"
import { mkLogger } from "../Logger"
import { Settings } from "../settings/Settings"
import { shim1 } from "../Shim1"
import { isMimeTypeSupportedByBrowser } from "../tags/MimeTypes"
import { withImageCache_ } from "./ImageCache"
import { includePreviewTags_ } from "./IncludePreviewTags"
import { sharpRenderOptions } from "./Sharp"
import { sharpReadable_, toSharp } from "./SharpReadable"

const logger = lazy(() => mkLogger("img.PrepareFileForBrowser"))

export interface PrepFileForBrowserArgs {
  nativePath: string
  userAgent?: string
  sha?: string
  mimetype?: string
}

export const prepFileForBrowser = shim1({
  name: "img.prepFileForBrowser",
  impl: _prepFileForBrowser
})

export async function _prepFileForBrowser(
  info: PrepFileForBrowserArgs
): PromiseMaybe<string> {
  try {
    const file = PosixFile.for(info.nativePath)
    if (await file.notExists()) return
    const mimetype =
      info.mimetype ?? (await readFileType_(file.nativePath))?.mime
    if (isMimeTypeSupportedByBrowser(mimetype, info.userAgent)) {
      return file.nativePath
    }
    logger().info("prepFileForBrowser(): non-browser-supported mimetype", {
      file,
      info
    })
    // 20220502: we have to tell sharpReadable to ignore rotation, or images get
    // rotated twice:
    return (
      await withImageCache_(file, "web", ".jpg", async dest => {
        const sr = await sharpReadable_({ src: file })
        if (sr == null)
          throw new Error("prepFileForBrowser(): can't read " + file.nativePath)
        await toSharp(sr).jpeg(sharpRenderOptions()).toFile(dest.nativePath)
        if (Settings.includePreviewTags.valueOrDefault) {
          await includePreviewTags_(file, [dest])
        }
      })
    )?.nativePath
  } catch (error) {
    logger().warn("prepFileForBrowser() failed", { error })
    return
  }
}
