import { partition } from "../../core/Array"
import { TagRoots } from "../../fe/api/Tag"
import { uniqBy } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { blank, notBlankOr } from "../../fe/Blank"
import { isFalse } from "../../fe/Boolean"
import { strEnum, StrEnumKeys } from "../../fe/StrEnum"
import { toS } from "../../fe/toS"
import { queryTermToFuzzyDate } from "./DateQuery"
import { Clause, isOrClause, isTerm, Query, queryToString, Term } from "./Query"

function sortQueries(arr: Query[]): Query[] {
  return sortBy(uniqBy(arr, queryToString), ea => [
    isTerm(ea) ? 0 : isOrClause(ea) ? 1 : 2,
    queryToString(ea).replace(/[(]+/g, "")
  ])
}

export function normalizeQuery(q: Query): Query {
  if (isTerm(q)) return normalizeTerm(q)
  const children = q.queries.map(normalizeQuery)
  if (children.length === 0) throw new Error("empty query")
  if (children.length === 1) return children[0]

  const [terms, clauses] = partition(children, isTerm) as [Query[], Clause[]]
  const [inlineable, remainders] = partition(clauses, ea => ea.type === q.type)
  for (const ea of inlineable) {
    terms.push(...ea.queries)
  }

  const result = {
    type: q.type,
    queries: sortQueries([...terms, ...remainders])
  }

  if (result.queries.every(ea => isTerm(ea) && ea.not)) {
    return normalizeQuery({
      type: q.type === "and" ? "or" : "and",
      queries: sortQueries(result.queries.map(ea => ({ ...ea, not: false })))
    })
  } else {
    return result
  }
}

export const AssetTermBoolNs = strEnum("deleted", "excluded", "liked", "hidden")
export const AssetTermDateNs = strEnum("date", "updated")

const AssetTermBoolAliases = {
  delete: AssetTermBoolNs.deleted,
  trash: AssetTermBoolNs.deleted,
  trashed: AssetTermBoolNs.deleted,

  exclude: AssetTermBoolNs.excluded,
  remove: AssetTermBoolNs.excluded,
  removed: AssetTermBoolNs.excluded,

  like: AssetTermBoolNs.liked,
  fav: AssetTermBoolNs.liked,
  fave: AssetTermBoolNs.liked,
  faved: AssetTermBoolNs.liked,
  favorite: AssetTermBoolNs.liked,
  favourite: AssetTermBoolNs.liked,

  hide: AssetTermBoolNs.hidden,
  archive: AssetTermBoolNs.hidden,
  archived: AssetTermBoolNs.hidden
}

const TagTermAliases = {
  kw: TagRoots.Keywords,
  keyword: TagRoots.Keywords,
  keywords: TagRoots.Keywords,
  dir: TagRoots.fs,
  directory: TagRoots.fs,
  folder: TagRoots.fs
}

export const AssetTermNs = strEnum(
  ...AssetTermDateNs.values,
  ...AssetTermBoolNs.values
)

export type AssetTermNamespace = StrEnumKeys<typeof AssetTermNs>

export interface AssetTerm extends Term {
  ns: AssetTermNamespace
}

export function isAssetTerm(t: Query): t is AssetTerm {
  return isTerm(t) && AssetTermNs.includes(normalizeTerm(t).ns)
}

/**
 * Convert fave => faved:true
 */
export function normalizeTerm(t: Term): Term {
  if (blank(t.ns) && t.term.match(/^\d{4}[-\d]*$/) != null) {
    const fd = queryTermToFuzzyDate(t.term)
    if (fd != null) {
      return { ns: "date", term: t.term }
    }
  }

  if (toS(t.ns).toLowerCase() === "when") {
    return normalizeTerm({
      ...t,
      ns: AssetTermDateNs.date,
      term: t.term.replace(/\//g, "-")
    })
  }

  let ns = notBlankOr(t.ns, t.term).toLowerCase()
  ns = (AssetTermBoolAliases as any)[ns] ?? ns
  if (AssetTermBoolNs.includes(ns)) {
    // default to true:
    let b = !isFalse(t.term)
    if (t.not === true) b = !b
    return {
      ns,
      // no op: not applicable.
      // no not: we negated b already ^^
      term: toS(b)
    }
  }
  if (AssetTermNs.includes(ns)) {
    return { ...t, ns }
  }

  if (ns === "before" || ns === "after") {
    if (t.op != null) {
      throw new Error(ns + ": doesn't support operators")
    }
    const gt = (ns === "after" && t.not !== true) || true === t.not
    return { ns: AssetTermDateNs.date, op: gt ? ">" : "<", term: t.term }
  }

  if (ns !== t.term.toLowerCase()) {
    // A namespaced TagTerm: if we have an alias, use it:
    return {
      not: t.not,
      op: t.op,
      ns: (TagTermAliases as any)[ns] ?? ns,
      term: t.term
    }
  } else {
    return t
  }
}
