import e from "express"
import { mkLogger } from "../../core/Logger"
import { wrap } from "../../core/express/Handlers"
import { isLocalhost } from "../../core/net/ip"
import { gt0, toInt } from "../../fe/Number"
import { AssetFile } from "../model/AssetFile"

const logger = mkLogger("web.OpenAssetFileRouter")

export class OpenAssetFileRouter {
  constructor(readonly openFileService: (path: string) => any) {}
  router(): e.Router {
    return e
      .Router()
      .get(
        "/api/openAssetFile/:assetFileId",
        wrap("OpenAssetFileRouter.assetFile", this.assetFile.bind(this))
      )
  }
  async assetFile(req: e.Request, res: e.Response) {
    logger.info("assetFile(): got request", {
      url: req.originalUrl,
      ip: req.ip
    })
    // Only allow on localhost:
    if (!isLocalhost(req.ip)) {
      logger.info("Non-localhost request", { url: req.originalUrl, ip: req.ip })
      return res.sendStatus(400) // Bad Request
    }
    const assetFileId = toInt(req.params.assetFileId)
    if (!gt0(assetFileId)) {
      logger.info("Missing assetFileId", req.originalUrl)
      return res.sendStatus(400) // Bad Request
    }
    const af = AssetFile.ops().findOneBy({ id: assetFileId })
    if (af == null) {
      logger.info("Invalid assetFileId", req.originalUrl)
      return res.sendStatus(400) // Bad Request
    }
    const file = await af.posixFile_()
    if (file == null || (await file.clear().notExists())) {
      logger.info("File not found", { url: req.originalUrl, af, file })
      return res.sendStatus(400) // Bad Request
    }
    logger.info("Requesting openFileService(" + file.nativePath + ")...", {
      url: req.originalUrl,
      ip: req.ip
    })
    this.openFileService(file.nativePath)
    return res.sendStatus(204) // no content
  }
}
