import e from "express"
import { mkLogger } from "../../core/Logger"
import { submitTask_ } from "../../core/event/SharedState"
import { truthyQuery, wrap } from "../../core/express/Handlers"
import {
  send404Toast,
  sendServerToast
} from "../../core/express/ServerToastHelpers"
import { prepFileForBrowser } from "../../core/img/PrepFileForBrowser"
import { Previews } from "../../core/img/Previews"
import { map } from "../../fe/Maybe"
import { gt0, toInt } from "../../fe/Number"
import { pick } from "../../fe/Object"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"

const logger = mkLogger("web.ImgActualRouter")

// "Image Actual" references
// https://www.quora.com/What-does-it-mean-when-soldiers-say-actual-in-a-radio-conversation
// and yes it is because we watched Battlestar Galactica recently

// NOTE: DON'T HIT EXIFTOOL, AND DON'T HIT SHARP. NOBODY GOT TIME FOR THAT.

export class ImgActualRouter {
  constructor(readonly previews: Previews) {}

  router(): e.Router {
    return e
      .Router()
      .get(
        "/img/:assetId/:assetFileId/actual(.*)?",
        wrap("ImgActualRouter.assetImg", this.assetFileImg.bind(this))
      )
      .get(
        "/img/:assetId/actual(.*)?",
        wrap("ImgActualRouter.assetImg", this.assetImg.bind(this))
      )
      .get(
        "/dl/:assetId/:assetFileId",
        wrap("ImgActualRouter.assetFileImg", this.assetFileImg.bind(this))
      )
  }

  async assetImg(req: e.Request, response: e.Response) {
    const assetId = toInt(req.params.assetId)
    if (!gt0(assetId)) {
      return sendServerToast({
        response,
        toast: {
          text: "Missing assetId in request",
          type: "warning"
        },
        httpStatusCode: 400
      })
    }
    const af = AssetFile.ops().findOneBy({ assetId, shown: 1 })
    if (af == null) {
      logger.info("Unknown assetId", req.originalUrl)
      return this.repairAssetLaterAndSendPreview(assetId, req, response)
    } else {
      return this.sendAssetFile(req, response, af)
    }
  }

  async assetFileImg(req: e.Request, response: e.Response) {
    const assetId = toInt(req.params.assetId)
    if (!gt0(assetId)) {
      return send404Toast({
        response,
        text: `Request was missing "assetId"`,
        httpStatusCode: 400
      })
    }
    const assetFileId = toInt(req.params.assetFileId)
    if (!gt0(assetFileId)) {
      return send404Toast({
        response,
        text: `Request was missing "assetFileId"`,
        httpStatusCode: 400
      })
    }
    const af = AssetFile.ops().findOneBy({ id: assetFileId!, assetId })
    if (af == null) {
      return send404Toast({
        response,
        text: "Bad request: no such asset file " + assetFileId
      })
    } else {
      return this.sendAssetFile(req, response, af)
    }
  }

  async sendAssetFile(req: e.Request, response: e.Response, af: AssetFile) {
    const assetId = af.assetId!
    const pf = await af.posixFile_()
    const download =
      req.path.split("/").includes("dl") || truthyQuery(req, "dl")

    if (pf == null || (await pf.isEmpty())) {
      if (download) {
        return send404Toast({
          response,
          text: "Asset file " + af.uri + " was missing"
        })
      } else {
        return this.repairAssetLaterAndSendPreview(assetId, req, response)
      }
    }

    if (download) {
      logger.info("Sending download", {
        assetId,
        af,
        nativePath: pf.nativePath
      })
      // deepcode ignore PT: <bug in snyk>
      return response.download(pf.nativePath, pf.base)
    }

    const userAgent = req.headers["user-agent"]

    const nativePath = await prepFileForBrowser({
      nativePath: pf.nativePath,
      userAgent,
      ...pick(af, "width", "height", "sha", "mimetype", "rotation", "mimetype")
    })

    if (nativePath == null) {
      logger.warn("cannot send file (null result from prepFileForBrowser)", {
        assetId,
        af
      })
      void requestAssetRepair(assetId)
      return send404Toast({
        response,
        text: `Failed to get browser-friendly preview for Asset ${assetId}.`,
        details: "I'll try to fix it."
      })
    } else {
      logger.info("sendAssetFile(): sending supported file: " + nativePath)
      // deepcode ignore PT: <snyk bug>
      return response.sendFile(nativePath, err =>
        map(err, error =>
          logger.warn("failed to send file", { error, assetId, af })
        )
      )
    }
  }

  /**
   * Ask sync to repair this asset or decommission it, and send a smaller
   * preview in the mean time
   */
  async repairAssetLaterAndSendPreview(
    assetId: number,
    _req: e.Request,
    response: e.Response
  ) {
    void requestAssetRepair(assetId)
    const file = await this.previews.ap(assetId).largestFileForReducer("fit")
    if (file != null) {
      response.sendFile(file.nativePath)
    } else {
      Asset.ops().upsertOne({ id: assetId, shown: false })
      send404Toast({
        response,
        text: `Asset ${assetId} is missing preview images.`,
        details: "I'll try to fix it, but it will be hidden for now."
      })
    }
  }
}

export function requestAssetRepair(assetId: number) {
  return submitTask_("repairAsset", {
    assetId,
    forceSync: true,
    forceRebuildPreviews: true
  })
}
