import { mkLogger } from "../core/Logger"
import { StdoutWrite } from "../core/StdoutWrite"
import { setupLibraryDirs_ } from "../core/dir/LibraryDirs"
import { errorToS } from "../core/error/Error"
import { PosixFile } from "../core/fs/PosixFile"
import { Settings } from "../core/settings/Settings"
import {
  writeLibrarySettings_,
  writeSystemSettings_
} from "../core/settings/SettingsIO"
import { systemSettingsFile } from "../core/settings/SystemSettings"
import { blank } from "../fe/Blank"
import { eql } from "../fe/Eql"
import { lazy } from "../fe/Lazy"
import { toS } from "../fe/toS"
import { restartLibrary_ } from "./RestartLibrary"

export interface LibrarySettings {
  scanAllDrives: boolean
  scanPath: string[]
  copyAssets: boolean
  autoUpdateCheck: boolean
  allowUserAgent: boolean
  reportErrors: boolean
}

const logger = mkLogger("LibrarySettings")

export function librarySettings(
  overrides: Partial<LibrarySettings> = {}
): LibrarySettings {
  return logger.tap({
    msg: "librarySettings",
    level: "info",
    result: {
      scanAllDrives: Settings.scanAllDrives.valueOrDefault,
      scanPath: Settings.scanPaths.values,
      copyAssets: Settings.copyAssetsToLibrary.valueOrDefault,
      autoUpdateCheck: Settings.autoUpdateCheck.valueOrDefault,
      allowUserAgent: Settings.allowUserAgent.valueOrDefault,
      reportErrors: Settings.reportErrors.valueOrDefault,
      ...overrides
    }
  })
}

type SaveLibraryError = {
  error: string
  /**
   * Will be resolved when the prior library has been restored
   */
  ready?: Promise<any>
}

type SaveLibrarySuccess = {
  msg: string
  /**
   * Will be resolved when the new library is set up
   */
  ready: Promise<any>
}

export type SaveLibraryResult = SaveLibraryError | SaveLibrarySuccess

export function importSettings(s: Partial<LibrarySettings>) {
  Settings.scanAllDrives.setValueIfDefined(s.scanAllDrives)
  Settings.scanPaths.setValueIfDefined(s.scanPath)
  Settings.copyAssetsToLibrary.setValueIfDefined(s.copyAssets)
  Settings.autoUpdateCheck.setValueIfDefined(s.autoUpdateCheck)
  Settings.allowUserAgent.setValueIfDefined(s.allowUserAgent)
  Settings.reportErrors.setValueIfDefined(s.reportErrors)
}

/**
 * @param newLibraryDir must be non-blank
 * @return error or info messages for the user
 */
export async function saveLibrarySettings(
  newLibraryDir: string,
  settings: LibrarySettings
): Promise<SaveLibraryResult> {
  // Fix https://gitlab.com/mceachen/photostructure/issues/58
  const newLibraryRoot = PosixFile.forMaybe(
    toS(newLibraryDir).trim()
  )?.resolve()

  const priorSettings = librarySettings()
  const priorLibraryDir = Settings.libraryDir.valueOrDefault
  const libraryRoot = newLibraryRoot ?? PosixFile.forMaybe(priorLibraryDir)

  let ready: Promise<any> = Promise.resolve()

  const scheduleRollback = lazy(() => {
    ready = ready.then(async () => {
      try {
        importSettings(priorSettings)
        Settings.libraryDir.value = priorLibraryDir
        await writeSystemSettings_()
        if (!blank(priorLibraryDir))
          await writeLibrarySettings_(priorLibraryDir)
        await restartLibrary_()
      } catch (error) {
        logger.warn("Failed to roll back to prior settings", {
          priorSettings,
          priorLibraryDirValue: priorLibraryDir,
          error
        })
      }
    })
  })

  if (libraryRoot == null) {
    return { error: "Please choose a directory for your library.", ready }
  }

  const libraryDirChanged = libraryRoot?.nativePath !== priorLibraryDir

  if (libraryDirChanged) {
    try {
      // Before we do anything else (like import settings), make sure the new
      // library directory can be created with sufficient permissions:
      await setupLibraryDirs_(libraryRoot)
    } catch (error) {
      return {
        error:
          "Failed to set up " +
          libraryRoot +
          ". Please choose a different directory for your library. " +
          errorToS(error as any)
      }
    }

    logger.info("Library directory changed, asking sync to shut down", {
      prior: priorLibraryDir,
      new: newLibraryRoot?.nativePath
    })
    // We do this as soon as possible so sync can be quiesscent during the
    // migration to the new library:
    StdoutWrite.shutdownSync()
  }

  try {
    importSettings(settings)
    const librarySettingsChanged = !eql(priorSettings, librarySettings())
    if (null == (await writeLibrarySettings_(libraryRoot.nativePath))) {
      scheduleRollback()
      return {
        error:
          "Cannot write to " +
          libraryRoot.nativePath +
          ". Please choose a different directory for your library.",
        ready
      }
    }

    Settings.libraryDir.value = libraryRoot.nativePath
    try {
      await writeSystemSettings_()
    } catch (err) {
      scheduleRollback()
      logger.warn("Failed to write system settings", err)
      return {
        error:
          "Couldn't write your system settings to " +
          systemSettingsFile() +
          ": " +
          errorToS(err),
        ready
      }
    }

    logger.info("saveLibrarySettings()", {
      libraryDirChanged,
      librarySettingsChanged,
      settings,
      priorSettings,
      currentSettings: librarySettings()
    })
    if (libraryDirChanged) {
      // NOTE! We _don't_ wait for the restart to complete, as the settings
      // page doesn't have a way to explain what's going on. The splash page
      // _does_, through, so let's get them to splash asap:
      ready = ready.then(() => restartLibrary_({ readSettings: false }))
    }
    return { msg: "Saved", ready }
  } catch (error) {
    // this catch block is a catch-all, and shouldn't normally happen (prior
    // throwable functions should have already had errors caught)
    scheduleRollback()
    logger.error("Failed to save settings", { error })
    return { error: "Failed to save settings: " + errorToS(error), ready }
  }
}
