import { arrayEndsWith } from "./Array"
import { stripPrefix } from "./String"

const ReservedDomainSuffixes = [
  "example.com",
  "example.net",
  "example.org",
  "example.edu",
  ".example",
  ".local"
].map(ea => stripPrefix(ea, ".").split("."))

export function isExampleDomain(domain: string): boolean {
  const parts = domain.toLowerCase().split(".")
  return ReservedDomainSuffixes.some(suffix => arrayEndsWith(parts, suffix))
}
