import { setInterval } from "timers"
import { lazy } from "../../core/Lazy"
import { ShortCommandTimeoutMs } from "../../core/ShortCommandTimeoutMs"
import { ServerSentEventsRouter } from "../../core/express/ServerSentEventsRouter"
import { mkEventStream } from "../../core/web/EventSource"
import { isPaused } from "../../core/work/IsPaused"
import { isEmpty } from "../../fe/Array"
import { eql } from "../../fe/Eql"
import { ApiProgressPath } from "../../fe/app/ApiProgressPath"
import { ProgressRateMs } from "../model/Progress"
import { ProgressProvider } from "./ProgressProvider"

export class ProgressRouter extends ServerSentEventsRouter<undefined> {
  readonly #timer: NodeJS.Timeout
  constructor(readonly updatesProvider: ProgressProvider) {
    super(ApiProgressPath)
    this.#timer = setInterval(() => this.#sendUpdates(), ProgressRateMs())
    this.onEnds.push(() => clearInterval(this.#timer))
  }

  override firstEvents() {
    return [] // < no-op
  }

  private readonly updateData = lazy(
    () => this.updatesProvider.updates(),
    ProgressRateMs()
  )

  async #sendUpdates() {
    if (
      this.ended ||
      isEmpty(this.streams) ||
      // no need to hit the db if we're paused and we've told the user:
      (isPaused() && this.updateData.prior() != null)
    ) {
      return
    }

    try {
      const prior = await this.updateData.prior()
      const updates = await this.updateData.refresh()
      if (updates == null) {
        this.logger.warn("Failed to fetch updates")
        return
      }

      if (eql(prior, updates)) {
        this.updateData.setTTL(
          Math.min(
            ShortCommandTimeoutMs,
            this.updateData.ttl() ?? ProgressRateMs()
          ) + 500
        )
        // no-op, same update.
        return
      }

      // we may have ended while we were fetching state:
      if (this.ended) return
      this.updateData.setTTL(ProgressRateMs())
      const payload = mkEventStream({ data: updates })
      for (const ea of this.streams) {
        ea.write(payload)
      }
    } catch (err) {
      this.logger.warn("Failed to send updates", err)
    }
  }
}
