import { WriteTags } from "exiftool-vendored"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { mapAsync } from "../../core/async/Promise"
import { writeTags_ } from "../../core/tags/ExifTool"
import { AssetFile } from "./AssetFile"

const logger = lazy(() => mkLogger("model.WriteAssetTags"))

export async function writeAssetTags_(assetId: number, t: WriteTags) {
  const assetFiles = AssetFile.ops().findBy({ assetId })
  return mapAsync({
    name: "writeAssetTags",
    arr: assetFiles,
    f: af => writeAssetFileTags_(af, t)
  })
}

async function writeAssetFileTags_(af: AssetFile, t: WriteTags) {
  const pf = await af.posixFile_()
  if (pf == null || true !== (await pf?.exists())) {
    logger().info("Skipping AssetFile:" + af.id + " (file is missing)", {
      writeTags: t
    })
    return
  }
  return writeTags_(pf, t, af.mimetype)
}
