import e from "express"
import { mkLogger } from "../../core/Logger"
import { firstGt0 } from "../../core/Number"
import { ShortCommandTimeoutMs } from "../../core/ShortCommandTimeoutMs"
import { isRecentMs } from "../../core/date/Date"
import {
  extractTagPath,
  getFit,
  getSeed,
  jsonOrRedirect,
  wrap
} from "../../core/express/Handlers"
import { send404Toast } from "../../core/express/ServerToastHelpers"
import { Settings } from "../../core/settings/Settings"
import { percentDecode } from "../../core/uri/URI"
import { compact, isNotEmpty, mapNotEmptyOr } from "../../fe/Array"
import { Maybe } from "../../fe/MaybeTypes"
import { toInt } from "../../fe/Number"
import { ThumbFit } from "../../fe/ThumbFit"
import { Replace, mkReplace } from "../../fe/api/Redirect"
import {
  ApiTagGallery,
  ChildTag,
  ChildTagCriteria,
  ChildTags,
  TagAssets,
  TagAssetsCriteria,
  ThumbsPerSample,
  nextAssetsUrl,
  nextChildTagsUrl
} from "../../fe/api/Tag"
import { mkTagFullPath } from "../../fe/app/FullPaths"
import { toS } from "../../fe/toS"
import { Library } from "../Library"
import { Asset } from "../model/Asset"
import { Tag } from "../model/Tag"

const logger = mkLogger("ApiTagRouter")

const ChildTagDefaultLimit = 4

interface RequestContext {
  tagId: number
  tagPath: string[]
  fit: ThumbFit
  seed?: number
}

function wrapApi<T>({
  name,
  f
}: {
  name: string
  f: (req: e.Request, tag: Tag, ctx: RequestContext) => Maybe<T>
}): e.RequestHandler {
  return wrap(name, async (req: e.Request, response: e.Response) => {
    const tagId = toInt(req.params.tagId)
    const tagPath = extractTagPath(req.path).map(percentDecode)
    const seed = getSeed(req)
    const fit = getFit(req)
    const tag = mapNotEmptyOr(
      tagPath,
      tp => Tag.findByIdOrPath(tagId, tp),
      () => Tag.root()
    )
    const result =
      tag == null
        ? undefined
        : f(req, tag, { tagId: tag.id!, tagPath: tag.path, seed, fit })
    logger.debug(name, { result, tagPath, seed, url: req.url })
    return tag == null || result == null
      ? send404Toast({
          text: "Failed to find tag " + tagId ?? tagPath.join("/"),
          response
        })
      : jsonOrRedirect(result, response)
  })
}

export class ApiTagRouter {
  router(): e.Router {
    return e
      .Router()
      .get(
        "/api/tag*",
        wrapApi<ApiTagGallery | Replace>({
          name: "ApiTagRouter.tagGallery",
          f: this.tagGallery.bind(this)
        })
      )
      .get(
        nextAssetsUrl(":tagId" as any, {} as any),
        wrapApi<TagAssets>({
          name: "ApiTagRouter.tagAssets",
          f: this.tagAssets.bind(this)
        })
      )
      .get(
        nextChildTagsUrl(":tagId" as any, {} as any),
        wrapApi<ChildTags>({
          name: "ApiTagRouter.childTags",
          f: this.childTags.bind(this)
        })
      )
  }

  tagGallery(
    req: e.Request,
    tag: Tag,
    ctx: RequestContext
  ): Maybe<ApiTagGallery | Replace> {
    logger.debug("tagGallery()", { tag })

    if (ctx.seed == null) {
      logger.debug("tagGallery(): no seed, redirecting...")
      return mkReplace(mkTagFullPath(tag.path, req.query as any))
    }

    if (Settings.fastForwardEmptyTags.valueOrDefault) {
      const redirectTagId = tag.firstNonInterstitialTagId()
      if (redirectTagId !== tag.id) {
        const t = Tag.findById(redirectTagId)
        if (t != null) {
          return mkReplace(mkTagFullPath(t.path, req.query as any), {
            text: `Skipping to first non-empty child tag`,
            uuid: "redirect-to-tag",
            onlyShowUuidOnce: true,
            type: "info"
          })
        }
      }
    }
    const result: ApiTagGallery = {
      fullPath: req.originalUrl,
      fit: ctx.fit,
      ...tag.toApiTag(),
      ...this.childTags(req, tag, ctx),
      ...this.tagAssets(req, tag)
    }

    if (tag.id === 0) {
      if (0 === Asset.shownCount() ?? 0) {
        if (isRecentMs(Library.instance()?.start, ShortCommandTimeoutMs)) {
          result.startingUp = true
        } else {
          result.emptyLibrary = true
        }
      }
    }
    return result
  }

  // Child tags are only ever paged forward
  childTags(req: e.Request, tag: Tag, ctx: RequestContext): ChildTags {
    const thisOffset = toInt(toS(req.query.offset)) ?? 0
    const limit = toInt(toS(req.query.limit)) ?? ChildTagDefaultLimit
    logger.debug("childTags() got ", { thisOffset, limit, query: req.query })
    const crit: ChildTagCriteria = {
      tagId: tag.id!,
      offset: thisOffset,
      limit,
      seed: ctx.seed!
    }
    crit.limit = firstGt0(crit.limit, ChildTagDefaultLimit)!
    const childTagCount = tag.getChildrenCount()
    const children = childTagCount === 0 ? [] : tag.getPagedChildren(crit)
    logger.debug("childTags()", { crit, childTagCount, children })
    const nextOffset = children.length + thisOffset
    const nextChildTags: Maybe<ChildTagCriteria> =
      nextOffset < childTagCount
        ? {
            tagId: tag.id!,
            seed: ctx.seed!,
            limit,
            offset: nextOffset
          }
        : undefined
    const childTags: ChildTag[] = compact(
      children.map(child => {
        const assetIds = child.getRelatedAssetIds({
          seed: ctx.seed! + child.id!,
          limit: ThumbsPerSample
        })
        return isNotEmpty(assetIds)
          ? { ...child.toApiTag(), assetIds }
          : undefined
      })
    )

    return logger.tap({
      msg: "childTags result",
      result: {
        tagId: tag.id!,
        childTags,
        childTagCount,
        nextChildTags
      },
      meta: {
        nextOffset
      }
    })
  }

  // assets may be paged backward or forward.
  tagAssets(req: e.Request, tag: Tag): TagAssets {
    const crit: TagAssetsCriteria = {
      offset: toInt(toS(req.query.offset), { defaultValue: 0 })!
    }
    const assetIds = tag.getPagedAssetIds(crit)
    const nextAssets: Maybe<TagAssetsCriteria> =
      assetIds.length < ThumbsPerSample
        ? undefined
        : {
            offset: crit.offset + assetIds.length
          }
    return {
      tagId: tag.id!,
      assetIds,
      nextAssets
    }
  }
}
