import { Router } from "express"
import { lazy } from "../../core/Lazy"
import { firstNotBlank } from "../../fe/Blank"
import { secondMs } from "../../fe/Date"
import { map } from "../../fe/Maybe"
import { mkLogger } from "../Logger"
import { onError } from "../error/OnError"
import { HealthCheck } from "../health/HealthCheck"
import { logBroadcaster } from "../log/LogBroadcaster"
import { LogWriter } from "../log/LogWriter"
import { Average } from "../math/Average"
import { Settings } from "../settings/Settings"
import { HttpStatusIs } from "../web/HttpStatus"
import { onFinished } from "./OnFinished"
import { requestedUri } from "./RequestMeta"

export class RequestLogger {
  private readonly logger = mkLogger("web.RequestLogger")
  private responseTimes = new Average()

  private readonly accessLogger = Settings.logWebRequests.valueOrDefault
    ? new LogWriter(
        firstNotBlank(
          Settings.logWebDir.valueOrDefault,
          Settings.logDir.valueOrDefault
        )!,
        {
          processName: lazy(() => "webrequest")
        }
      )
    : undefined

  readonly router = Router().use((req, res, next) => {
    const start = Date.now()
    const method = req.method
    const protocol = `${req.protocol?.toUpperCase()}/${req.httpVersion}`
    const msg = `${method} ${requestedUri(req)} ${protocol}`
    const slowTimer =
      req.path === "/updates" || // < TODO rename to /sse/updates
      req.path === "/api/splash" || // < TODO rename to /sse/splash
      req.path === "/api/status" // < TODO rename to /sse/status
        ? undefined
        : setTimeout(
            () =>
              this.logger.warn("Request is still processing: " + msg, {
                elapsedMs: Date.now() - start
              }),
            (this.responseTimes.p84 ?? secondMs) * 4
          )

    onFinished(res, (error: Error | null) => {
      map(slowTimer, clearTimeout)
      if (error != null) onError(msg, { error })
      const ok =
        HttpStatusIs.success(res.statusCode) ||
        HttpStatusIs.redirect(res.statusCode)
      const elapsedMs = Date.now() - start

      const meta: any = {
        from_ip: req.ip,
        protocol,
        method,
        statusCode: res.statusCode,
        ok,
        elapsedMs,
        runState: HealthCheck.runState(),
        userAgent: req.headers["user-agent"]
      }
      if (error != null) {
        meta.error = error
      }
      this.responseTimes.push(elapsedMs)

      logBroadcaster()?.log(
        ok ? "info" : "warn",
        "HttpRequestLogger",
        msg,
        meta
      )

      this.accessLogger?.log("info", "", msg, meta)

      if (logBroadcaster.prior() == null && this.accessLogger == null) {
        this.logger.log(error != null || !ok ? "warn" : "debug", msg, meta)
      }
    })

    next()
  })
}
