import e from "express"
import { exit } from "../../core/async/Exit"
import { ee } from "../../core/event/EventEmitter"
import { wrap } from "../../core/express/Handlers"
import { referrer } from "../../core/express/RequestHeaders"
import {
  sendRedirectionToast,
  sendServerToast
} from "../../core/express/ServerToastHelpers"
import { clearReaddirCaches } from "../../core/fs/Readdir"
import { HealthCheck } from "../../core/health/HealthCheck"
import { clearLibraryDirSetting } from "../../core/settings/SettingsIO"
import { clearVolumesCache } from "../../core/volumes/Volumes"
import { secondMs } from "../../fe/Date"
import { delay } from "../../fe/Delay"
import { stringify } from "../../fe/JSON"
import { cleanup_ } from "../Cleanup"
import { restartLibrary_ } from "../RestartLibrary"
import { shutdown } from "../Shutdown"
import { dbModelRepair } from "../db/DbModelRepair"

const timeout = 2 * secondMs

export class AdminPostRouter {
  router(): e.Router {
    return e
      .Router()
      .post(
        "/admin/restart",
        wrap("AdminHealthRouter:restart", this.restart.bind(this))
      )
      .post(
        "/admin/bounce",
        wrap("AdminHealthRouter:bounce", this.bounce.bind(this))
      )
      .post(
        "/admin/refresh/:id",
        wrap("AdminHealthRouter:refresh", this.refresh.bind(this))
      )
      .post(
        "/admin/clear-library",
        wrap("AdminHealthRouter:clear-library", this.clearLibrary.bind(this))
      )
      .post(
        "/admin/clear-caches",
        wrap("AdminHealthRouter:clear-caches", this.clearCaches.bind(this))
      )
      .post(
        "/admin/recheck-tools",
        wrap("AdminHealthRouter:recheck-tools", this.recheckTools.bind(this))
      )
      .post(
        "/admin/repair-db",
        wrap("AdminHealthRouter:repair-db", this.repairDb_.bind(this))
      )
      .post(
        "/admin/run-maintenance",
        wrap("AdminHealthRouter:repair-db", this.maintenance_.bind(this))
      )
      .post(
        "/admin/shutdown",
        wrap("AdminHealthRouter:shutdown", this.shutdown.bind(this))
      )
  }

  async restart(_req: e.Request, response: e.Response, _next: e.NextFunction) {
    await restartLibrary_()
    sendRedirectionToast({
      response,
      toast: { text: "Restarted library...", timeout },
      url: "/splash"
    })
  }

  async refresh(req: e.Request, response: e.Response, _next: e.NextFunction) {
    const id = req.params.id
    const hc = HealthCheck.findById(id)
    if (hc == null)
      throw new Error("No health check found with id=" + stringify(id))
    const result = await hc.refresh()
    sendRedirectionToast({
      response,
      toast: { text: "Health check " + id + ": " + result.level, timeout },
      url: "/health"
    })
  }

  async bounce(_req: e.Request, response: e.Response, _next: e.NextFunction) {
    // don't immediately exit: wait a second for the response to be flushed.
    void delay(secondMs).then(() =>
      exit({ reason: "/bounce requested by " + _req.ip, status: 0 })
    )
    sendRedirectionToast({
      response,
      toast: {
        text: "Restarting webserver...",
        timeout: 15 * secondMs
      },
      url: "/splash"
    })
  }

  async clearLibrary(
    _req: e.Request,
    response: e.Response,
    _next: e.NextFunction
  ) {
    await clearLibraryDirSetting()
    void restartLibrary_()
    sendRedirectionToast({
      response,
      toast: {
        text: "Prior library was unset. Restarting...",
        timeout
      },
      url: "/splash"
    })
  }

  async clearCaches(
    req: e.Request,
    response: e.Response,
    _next: e.NextFunction
  ) {
    await clearVolumesCache({ deleteFileCache: true })
    await clearReaddirCaches()
    ee().emit("clearCache")
    if (referrer(req) === "/health") {
      sendRedirectionToast({
        response,
        toast: { text: "Cleared caches.", timeout },
        url: "/health"
      })
    } else {
      sendServerToast({
        response,
        toast: { text: "Cleared caches." },
        reload: true
      })
    }
  }

  async recheckTools(
    _req: e.Request,
    response: e.Response,
    _next: e.NextFunction
  ) {
    ee().emit("clearToolCache")
    sendRedirectionToast({
      response,
      toast: {
        text: "Cleared tool caches.",
        timeout
      },
      url: "/health"
    })
  }

  async repairDb_(
    _req: e.Request,
    response: e.Response,
    _next: e.NextFunction
  ) {
    void dbModelRepair()

    sendRedirectionToast({
      response,
      toast: {
        text: "Started library database repair...",
        timeout
      },
      url: "/splash"
    })
    return
  }

  async maintenance_(
    _req: e.Request,
    response: e.Response,
    _next: e.NextFunction
  ) {
    void cleanup_()

    sendRedirectionToast({
      response,
      toast: {
        text: "Started library maintenance jobs...",
        timeout
      },
      url: "/"
    })
    return
  }

  async shutdown(req: e.Request, response: e.Response, _next: e.NextFunction) {
    void delay(timeout).then(() => shutdown("requested by " + req.ip))
    sendServerToast({
      response,
      toast: {
        text: "Shutdown initiated."
      }
    })
    return
  }
}
