import { isNumber } from "../../fe/Number"
import { keys } from "../../fe/Object"
import { strEnum, StrEnumKeys } from "../../fe/StrEnum"
import { Args } from "../Args"
import { assetFileMetadata_ } from "../asset-file/AssetFileFactory"
import { shimmedFileSha_ } from "../fs/Hash"
import { imageHash_ } from "../img/ImageHash"
import { prepFileForBrowser } from "../img/PrepFileForBrowser"
import { buildAssetPreviews_ } from "../img/Previews"
import { _extractVideoFrame_ } from "../img/Video"
import { Shim1 } from "../Shim1"
import { _readRawTags, _readTags } from "../tags/ExifTool"
import { ping } from "./ping"

/**
 * These are all of the `shim`-ed functions.
 *
 * Note that they all may not be configured to run remotely--see
 * `WorkerFunctions` below.
 */
export const _AllWorkerFunctions = {
  _extractVideoFrame_,
  _readRawTags,
  _readTags,
  assetFileMetadata_,
  buildAssetPreviews_,
  imageHash_,
  ping,
  prepFileForBrowser,
  shimmedFileSha_
} satisfies Record<string, Shim1<any, any>>

/**
 * List of `shim`-ed functions that are
 */
export const WorkerFunctions = {
  _extractVideoFrame_, // TODO: is this really expensive?
  _readRawTags, // comment out so exiftool can just run from sync
  _readTags, // comment out so exiftool can just run from sync
  assetFileMetadata_,
  buildAssetPreviews_,
  imageHash_,
  ping,
  prepFileForBrowser,
  shimmedFileSha_ // < takes time to compute and slows down the main thread
} satisfies Record<string, Shim1<any, any>>

export type WorkRequests = typeof WorkerFunctions

export const WorkRequestMethods = strEnum(...keys(WorkerFunctions))
export type WorkRequestMethod = StrEnumKeys<typeof WorkRequestMethods>

// See event/SharedState
export interface WorkRequest<N extends WorkRequestMethod> {
  // simple number, because workers aren't shared across processes
  id: number
  method: N
  args: Args<WorkRequests[N]>
}

export function isWorkRequest(obj: any): obj is WorkRequest<any> {
  return (
    typeof obj === "object" &&
    WorkRequestMethods.has(obj?.method) &&
    isNumber(obj.id)
  )
}
