import e from "express"
import { mkLogger } from "../../core/Logger"
import { wrap } from "../../core/express/Handlers"
import { HealthCheck } from "../../core/health/HealthCheck"
import { p } from "../../core/licensing/Licensing"
import { Settings } from "../../core/settings/Settings"
import { HttpStatus } from "../../core/web/HttpStatus"
import { suggestedLibraryDirs } from "../SuggestedLibraryDir"
import { getPlansPick } from "./PlansRouter"
import { render } from "./PugHelpers"

export class WelcomeRouter {
  readonly logger = mkLogger("WelcomeRouter")
  router(): e.Router {
    return (
      e
        .Router()
        // "welcome" is just a friendly alias for "settings"
        .get("/welcome", wrap("GET /welcome", this.get.bind(this)))
        .post("/welcome", wrap("POST /welcome", this.post.bind(this)))
    )
  }

  async get(req: e.Request, res: e.Response) {
    const state = HealthCheck.runState()
    if (state !== "welcome") {
      return res.redirect(HttpStatus.Found, "/")
    } else {
      // start this work now:
      void suggestedLibraryDirs()
      return render(req, res, "welcome", {})
    }
  }

  async post(req: e.Request, res: e.Response) {
    // If they don't want us to pick plans, or we've got an OK license, redirect
    // to settings:
    if (!Settings.pickPlanOnWelcome.valueOrDefault || (await p())) {
      return res.redirect("/settings")
    } else {
      return getPlansPick(req, res)
    }
  }
}
