import e from "express"
import { Deferred } from "../../core/async/Deferred"
import { ServerSentEventsRouter } from "../../core/express/ServerSentEventsRouter"
import { HealthCheck } from "../../core/health/HealthCheck"
import { mkEventStream, ServerSentEvent } from "../../core/web/EventSource"
import { HealthCheckResult, HealthCheckSummary } from "../../fe/HealthChecks"
import { Maybe } from "../../fe/MaybeTypes"

const doneEvent: ServerSentEvent<"done"> = {
  event: "done",
  data: { done: true }
}

function mkResult(
  data: HealthCheckResult | HealthCheckSummary
): Maybe<ServerSentEvent<"result">> {
  return data == null
    ? undefined
    : {
        event: "result",
        data
      }
}

export class ApiHealthRouter extends ServerSentEventsRouter<"result" | "done"> {
  constructor() {
    super("/api/health")
  }

  override firstEvents(_req: e.Request, res: e.Response) {
    void this.sendResults(res)
    return []
  }

  async sendResults(res: e.Response) {
    const pending: HealthCheck[] = []
    const results: HealthCheckResult[] = []
    results.push(HealthCheck.summary())
    for (const hc of HealthCheck.all()) {
      const result = hc.toResultSync()
      results.push(result)
      if (result.level === "pending") {
        pending.push(hc)
      }
    }
    res.write(mkEventStream(...results.map(mkResult)))

    this.logger.debug("sendResults", {
      pending: pending.map(ea => ea.id),
      resolved: results.map(ea => ea.id)
    })

    for await (const result of Deferred.toAsyncIterable(
      pending.map(ea => ea.result())
    )) {
      const chunk = mkEventStream(
        mkResult(result),
        mkResult(HealthCheck.summary())
      )
      this.logger.debug("sendResults", { result, chunk })
      res.write(chunk)
    }

    // await HealthCheck.awaitSettled()

    res.write(mkEventStream(mkResult(HealthCheck.summary()), doneEvent), () =>
      this.endStream(res)
    )
  }
}
