import { mkLogger } from "../../core/Logger"
import { LastOneInWins } from "../../core/async/LastOneInWins"
import { submitTask_ } from "../../core/event/SharedState"
import { matchAndWriteRotation_ } from "../../core/img/MatchRotation"
import { Previews } from "../../core/img/Previews"
import { compact, isNotEmpty } from "../../fe/Array"
import { defer } from "../../fe/Defer"
import { stringify } from "../../fe/JSON"
import { gt0 } from "../../fe/Number"
import { isRotation } from "../../fe/Rotation"
import { Asset } from "./Asset"

const assetLock = defer(() => new LastOneInWins<number>("Asset"))

export async function setAssetRotation_(
  assetId: number,
  rotation: number,
  previews: Previews
) {
  if (!gt0(assetId)) {
    throw new Error("invalid asset " + stringify(assetId))
  }

  if (!isRotation(rotation)) {
    throw new Error(
      "setRotation(" + rotation + "): invalid rotation " + stringify(rotation)
    )
  }

  return assetLock().enqueue(assetId, async ac =>
    _setAssetRotation_(assetId, rotation, previews, ac.signal)
  )
}

async function _setAssetRotation_(
  assetId: number,
  rotation: number,
  previews: Previews,
  ac: AbortSignal
) {
  const start = Date.now()
  const log = mkLogger(
    "setAssetRotation(" + stringify({ assetId, rotation }) + ")"
  )

  const asset = Asset.ops().findById(assetId)
  if (asset == null) {
    return log.throw("Unknown assetId:" + assetId)
  }

  const repairAsset = defer(() => {
    void submitTask_("repairAsset", { assetId })
  })

  log.throwIfAborted(ac)

  // Determine what the correct rotation should be for each file. They were
  // looking at a preview image, so we'll use that as the exemplar.

  const exemplar = await previews.ap(assetId!).smallestFileForReducer("fit")
  if (exemplar == null) {
    repairAsset()
    return log.throw(
      "Missing previews for Asset " + assetId + ". Trying to repair.",
      {
        doNotSend: true,
        fatal: false,
        retriable: false
      }
    )
  }

  const assetFiles = asset.getAssetFiles()
  const shownAF = asset.getShown()
  if (shownAF == null) {
    repairAsset()
    return log.throw(
      "No primary asset file for Asset " + assetId + ". Trying to repair.",
      {
        doNotSend: true,
        fatal: false,
        retriable: false
      }
    )
  }

  const updatedAssetFiles = []

  for (const af of assetFiles) {
    try {
      const f = await af.posixFile()
      if (f == null || (await f.notExists())) {
        log.info("cannot rotate variation: file is missing", { f, af })
        continue
      }
      const updated = await matchAndWriteRotation_(exemplar, f, rotation)
      if (updated == null) {
        log.info("cannot rotate variation: matchRotation() failed", { f, af })
        continue
      }
      updatedAssetFiles.push(await af.upsertIfNeeded_(f.clear(), true))
    } catch (error) {
      log.warn("failed to update asset file", { af, error })
    }
  }

  compact(updatedAssetFiles)
  if (isNotEmpty(updatedAssetFiles)) {
    await previews
      .apb(assetId!, assetFiles, { forceRebuildPreviews: true })
      .build_()
  }
  log.info("setRotation(" + rotation + "): completed.", {
    elapsedMs: Date.now() - start,
    updatedAssetFiles
  })
  return updatedAssetFiles
}
