import { lazy } from "../core/Lazy"
import { mkLogger } from "../core/Logger"
import { isWebService } from "../core/ServiceNames"
import { StdoutWrite } from "../core/StdoutWrite"
import { ee } from "../core/event/EventEmitter"
import { HealthCheck } from "../core/health/HealthCheck"
import { Settings } from "../core/settings/Settings"
import { readSettings } from "../core/settings/SettingsIO"
import { Library } from "./Library"

const logger = lazy(() => mkLogger("library.RestartLibrary"))

export async function restartLibrary_(opts?: {
  readSettings?: boolean
}): Promise<{ ready: Promise<void> }> {
  if (isWebService()) {
    StdoutWrite.shutdownSync()
  }

  if (opts?.readSettings ?? true) {
    await readSettings.refresh()
  }

  let ready = Promise.resolve()

  logger().warn("Shutting down prior library...")
  try {
    ready = Library.endPriorInstance()
  } catch (error) {
    logger().warn("ending prior library failed:", { error })
  }

  // This invalidate (most) lazy() caches (like the library UID)
  ee().emit("clearCache")

  // Most health checks and other bits are watching the libraryDir for
  // changes. Tell them to refresh:
  Settings.libraryDir.broadcastChange()

  HealthCheck.reset()

  // Make sure the next library is created:
  const l = Library.instance()

  // This is done asynchronously. We want the settings page to wait for the moment when the splash screen will be showable, and not wait any longer:

  ready = ready.then(() => l?.ready)

  if (isWebService()) {
    ready = ready.then(async () => {
      const summary = await HealthCheck.awaitSettled()
      if (summary.state === "ready") {
        StdoutWrite.restartSync()
      } else {
        logger().error(
          "Restarted library, but health check summary was not ready",
          { summary }
        )
      }
    })
  }
  return { ready }
}
