import { Server } from "net"
import { addEndable, Endable, EndableRanks } from "../async/Endable"
import { closeStream } from "../fs/Streams"

export class EndableServer implements Endable {
  private _ended = false
  constructor(
    readonly name: string,
    readonly server: Server
  ) {
    addEndable(EndableRanks.service, this)
  }

  get ended() {
    return this._ended
  }

  end() {
    this._ended = true
    return closeStream(this.server)
  }
}
