import { defer } from "../../fe/Defer"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { mkLogger } from "../Logger"
import { shortFsStringSha } from "../StringHash"
import { mapAsyncSerial } from "../async/Promise"
import { configDir } from "../dir/ConfigDir"
import { libraryDataDirPosixFile } from "../dir/LibraryDirs"
import { broadcastEvent_ } from "../event/BroadcastEvent"
import { BaseFile } from "../fs/BaseFile"
import { L } from "./L"
import { k, m, vok } from "./Licensing"

const logger = defer(() => mkLogger("writeLicense"))

/**
 * Write license
 * @param t paseto token ("v2.public....")
 */
export async function writeLicense(str: string, timeoutMs: number) {
  // This throws if validation fails
  const pl = await vok(str, "candidate", timeoutMs)
  if (pl == null) {
    return logger().error("!ok", str)
  }
  // Only store the license if it's better than what we've got already for any
  // given directory:
  await saveIfBetter(pl, libraryDataDirPosixFile()?.join(k().d), timeoutMs)
  await saveIfBetter(
    pl,
    map(configDir(), ea => BaseFile.for(ea).join(k().d)),
    timeoutMs
  )

  // We always want the new license to have a chance to switch from LITE to PLUS:
  await m.refresh()
}

export async function licensesInDirectory(
  dir: BaseFile,
  timeoutMs: number
): Promise<L[]> {
  return (
    await mapAsyncSerial({
      name: "read",
      arr: dir.childFiles(),
      f: async ea => vok(await ea.readTextFile(), ea.nativePath, timeoutMs)
    })
  ).filter(ea => ea.ok && ea instanceof L) as L[]
}

export async function saveIfBetter(
  pl: L,
  dir: Maybe<BaseFile>,
  timeoutMs: number
) {
  if (dir == null) return
  const prior = await licensesInDirectory(dir, timeoutMs)
  if (prior.some(ea => ea.cmp(pl) >= 0)) {
    logger().info("saveIfBetter(): no-op for " + dir)
    return
  } else {
    const f = dir.join(shortFsStringSha(pl.s) + ".txt")
    try {
      await f.writeFile_(pl.s)
      void broadcastEvent_("reloadLicenses")
      logger().info("saveIfBetter(): wrote to " + f)
      return f
    } catch (error) {
      logger().error("saveIfBetter(): failed to save license to " + dir, error)
      return
    }
  }
}
