import e from "express"
import { mkLogger } from "../../core/Logger"
import { wrap } from "../../core/express/Handlers"
import { Previews } from "../../core/img/Previews"
import { isEmpty } from "../../fe/Array"
import { map } from "../../fe/Maybe"
import { gt0, toInt } from "../../fe/Number"
import { AssetFile } from "../model/AssetFile"

const logger = mkLogger("web.VideoRouter")

export class VideoRouter {
  constructor(readonly previews: Previews) {}

  router(): e.Router {
    return e
      .Router()
      .get("/video/:assetId", wrap("VideoRouter", this.videoImage.bind(this)))
  }

  async videoImage(
    req: e.Request,
    res: e.Response,
    _next: e.NextFunction
  ): Promise<any> {
    const params = req.params
    const assetId = toInt(params.assetId)
    // file deepcode ignore PT: <fetched by ID, not path>
    if (!gt0(assetId)) {
      logger.warn("Malformed request (missing assetId)", { params: req.params })
      return res.sendStatus(400) // Bad Request
    }
    // If we have a transcoded version, send it:
    const ap = this.previews.ap(assetId)
    const mp4 = ap.mp4()
    if (await mp4.exists()) {
      logger.info("streaming transcoded video", mp4)
      return res.sendFile(mp4.nativePath)
      // return streamFile(logger, req, res, mp4, "video/mp4")
    }

    const afs = AssetFile.ops().findBy({ assetId, shown: 1 })
    if (isEmpty(afs)) {
      logger.warn("Malformed request (bad assetId)", { params: req.params })
      return res.sendStatus(400) // Bad Request
    }

    const af = afs[0]
    const f = await map(af, ea => ea.posixFile_())
    if (f != null && af.isVideo && (await f.exists())) {
      return res.sendFile(f.nativePath)
      // return streamFile(logger, req, res, f, af.mimetype)
    } else {
      logger.warn("Shown assetFile isn't a video", af)
      return res.sendStatus(400) // Bad Request
    }
  }
}
