function orTerms(arr: any[]) {
  // console.log("orTerms", arr)
  const queries = compact([arr[0], ...arr[1].map((ea: any) => ea[1])])
  return (queries.length === 1) ? queries[0] : {type: "or", queries}
}

function andTerms(arr: any[]) {
  // console.log("andTerms", arr)
  const queries = compact([arr[0], ...arr[1].map((ea: any) => ea[2])])
  return (queries.length === 1) ? queries[0] : {type: "and", queries}
}

function groupedTerms(arr: any[]) {
  // console.log("groupedTerms", arr)
  const result = compact(arr[2])
  return (result.length === 1) ? result[0] : result
}

