import { strEnum, StrEnumKeys } from "../StrEnum"

export const SyncStatuses = strEnum("processing", "paused", "done")
export type SyncStatus = StrEnumKeys<typeof SyncStatuses>

export const RebuildingURI = "rebuilding://"
export const EmptyDeletedURI = "emptydeleted://"
export const EmptyRemovedURI = "emptyremoved://"

export function isRebuildProgress(obj?: { uri?: string }): boolean {
  return RebuildingURI === obj?.uri
}

export function noRecentAssetIdsProgress(obj?: { uri?: string }): boolean {
  return [EmptyDeletedURI, EmptyRemovedURI].includes(obj?.uri as any)
}

export interface Percents {
  /**
   * Percent of the found assets that have been processed
   */
  completePct?: number
  /**
   * Percent of the found assets that remain to be processed
   */
  incompletePct?: number
  /**
   * Percent of the current volume that has **not yet** been scanned. If
   * "scanning" isn't relevant for this task, like library rebuilding or
   * emptying trash, this should be 0.
   */
  scanningPct?: number
}

// Called "ProgressState" rather than "SyncState" because it sometimes holds
// "oes noes I'm paused" or "hey I'm starting now yay"
export interface ProgressState extends Percents {
  uri?: string
  /** aka mountpoint. "C:" or "/homes/matthew/Pictures" or "" */
  volume: string
  state?: SyncStatus
  /** heading/title for the progress panel */
  hed?: string
  /** deck (sentence or two description) */
  dek?: string[]
}

export interface ProgressStateWithAssets extends ProgressState {
  recentAssetIds: number[]
}

export const ProgressWithAssetsProps: (keyof ProgressStateWithAssets)[] = [
  "uri",
  "volume",
  "state",
  "hed",
  "dek",
  "completePct",
  "incompletePct",
  "scanningPct",
  "recentAssetIds"
]
