import { leastBy } from "../../core/Array"
import { Previews } from "../../core/img/Previews"
import { Settings } from "../../core/settings/Settings"
import { isVideoMimeType } from "../../core/tags/MimeTypes"
import { URI } from "../../core/uri/URI"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0, toGt0 } from "../../fe/Number"

export interface OpenGraphMetadata {
  property: string
  content: string
}

/**
 * @see https://forum.photostructure.com/t/image-thumbnail-in-link-preview/1950
 * @see https://ogp.me/
 */
export async function getAssetOpenGraph(
  reqUri: URI,
  assetId: Maybe<string | number>
): PromiseMaybe<OpenGraphMetadata[]> {
  assetId = toGt0(assetId)
  if (assetId == null || !gt0(Settings.openGraphTargetWidth.valueOrDefault))
    return

  const ap = Previews.instance()?.ap(assetId)
  if (ap == null) return // no library?

  const info = await ap.readInfo()
  if (info == null) return // missing metadata

  const result: OpenGraphMetadata[] = []

  if (isVideoMimeType(info.mimetype)) {
    result.push({
      property: "og:video",
      content: reqUri.with({ path: "/video/" + assetId }).toString()
    })
    const videoMimetype = (await ap.mp4().exists())
      ? "video/mp4"
      : info.mimetype
    result.push(
      { property: "og:video:type", content: videoMimetype },
      { property: "og:video:width", content: info.width.toString() },
      { property: "og:video:height", content: info.height.toString() }
    )
  } else {
    // HEY FUTURE ME: remember that iMessages (at least way back in 2023)
    // didn't handle an array of og:image tags, so we're only going to send
    // one--the one closest to the target width.
    const arr = ((await ap.widths("fit")) ?? []).map(width => ({
      width,
      path: "/img/" + assetId + "/fit/" + width
    }))

    if (info.mimetype === "image/jpeg") {
      arr.push({ width: info.width, path: "/img/" + assetId + "/actual" })
    }

    const best = leastBy(arr, ea =>
      Math.abs(ea.width - Settings.openGraphTargetWidth.valueOrDefault)
    )

    if (best != null) {
      result.push(
        {
          property: "og:image",
          content: reqUri.with({ path: best.path }).toString()
        },
        { property: "og:image:type", content: "image/jpeg" },
        { property: "og:image:width", content: best.width.toString() }
      )

      const bestHeight = best.width / (info.width / info.height)
      if (gt0(bestHeight)) {
        result.push({
          property: "og:image:height",
          content: Math.round(bestHeight).toString()
        })
      }
    }
  }
  return result
}
