import e from "express"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { mapEntries } from "../../core/Object"
import { version } from "../../core/Version"
import { wrap } from "../../core/express/Handlers"
import { requestedUri } from "../../core/express/RequestMeta"
import { sids } from "../../core/licensing/SystemIds"
import { writeLicense } from "../../core/licensing/WriteLicense"
import { Settings } from "../../core/settings/Settings"
import { mapNotBlank, mapNotBlankOr, notBlankOr } from "../../fe/Blank"
import { secondMs } from "../../fe/Date"
import { Obj } from "../../fe/Object"
import { toA } from "../../fe/toA"

const logger = lazy(() => mkLogger("web.PlansRouter"))

export class PlansRouter {
  router(): e.Router {
    return e
      .Router()
      .get("/plans/pick", wrap("GET /plans/pick", getPlansPick))
      .get("/plans/activate", wrap("GET /plans/activate", getPlansActivate))
      .get("/plans/billing", wrap("GET /plans/billing", getPlansBilling))
      .get("/plans/license", wrap("GET /plans/license", getPlansLicense))
  }
}

/**
 * Going to this:/plans/pick redirects to account.photostructure.com/plans/pick,
 * and after a license is made, redirects to this:/plans/license
 *
 * If there's a then=/.. query, add that to the redirect.
 */
export async function getPlansPick(req: e.Request, res: e.Response) {
  return await redirectToAccount({ req, res, remotePath: "/plans/pick" })
}
export async function getPlansActivate(req: e.Request, res: e.Response) {
  return await redirectToAccount({
    req,
    res,
    remotePath: "/",
    params: { skipIndex: true }
  })
}

export async function getPlansBilling(req: e.Request, res: e.Response) {
  return await redirectToAccount({ req, res, remotePath: "/" })
}

async function redirectToAccount({
  req,
  res,
  remotePath,
  params = {}
}: {
  req: e.Request
  res: e.Response<any, Record<string, any>>
  remotePath: string
  params?: Obj
}) {
  // This will capture the "then" param, so we don't need to add it again.
  const redirectToLibraryUrl = requestedUri(req).with({
    path: "/plans/license"
  })
  const uids = await sids({ timeoutMs: 4 * secondMs })
  const dest = new URL("https://account.photostructure.com" + remotePath)
  dest.searchParams.append("version", version)
  dest.searchParams.append("uids", uids.join(","))
  mapNotBlank(Settings.coupon.value, ea =>
    dest.searchParams.append("coupon", ea)
  )
  dest.searchParams.append(
    "redirectToLibraryUrl",
    redirectToLibraryUrl.toString()
  )
  mapEntries(params, (k, v) => dest.searchParams.append(k, v))
  logger().info("GET /plans/pick", {
    redirectToLibraryUrl: redirectToLibraryUrl.toString()
  })
  return res.redirect(dest.toString())
}

export async function getPlansLicense(req: e.Request, res: e.Response) {
  try {
    mapNotBlankOr(
      req.query.license,
      async ea => {
        await writeLicense(ea, 3 * secondMs)
        logger().info("getPlansLicense(): Saved new license", ea)
      },
      () => {
        logger().info(
          "getPlansLicense(): no license in request",
          req.originalUrl
        )
      }
    )
    // TODO: add a flash "yay, saved your license!"
  } catch (err) {
    logger().error("getPlansLicense(): Failed to save license", {
      err,
      url: req.originalUrl
    })
    // TODO: add "OH NOES"
  }
  const redirectTo = notBlankOr(toA(req.query.then)[0], "/settings")
  logger().info("getPlansLicense(): redirecting to " + redirectTo)
  res.redirect(redirectTo)
}
