import express from "express"
import { isAjaxRequest } from "../web/HttpHeaders"
import { HttpStatus } from "../web/HttpStatus"
import { sendRedirectionToast } from "./ServerToastHelpers"

export function redirectRouter(path: string) {
  return express
    .Router()
    .use((request: express.Request, response: express.Response) => {
      return isAjaxRequest(request.headers)
        ? sendRedirectionToast({
            response,
            redirectOrReplace: "redirect",
            url: "/splash"
          })
        : response.redirect(HttpStatus.Found, path)
    })
}
