import { NextFunction, Request, Response } from "express"
import { UnwrapLazy, lazy } from "../../core/Lazy"
import { later } from "../../fe/Delay"
import { stringify } from "../../fe/JSON"
import { WebSettings } from "../../fe/api/WebSettings"
import { version } from "../Version"
import { nowLocal } from "../date/Localtime"
import { ee } from "../event/EventEmitter"
import { m, t } from "../licensing/Licensing"
import { Settings, isProd } from "../settings/Settings"
import { URI } from "../uri/URI"
import { isPaused } from "../work/IsPaused"

