const UriDefaults = lazy(() => ({
  protocol: "http",
  host: Settings.hostname.valueOrDefault ?? "localhost",
  url: "/"
}))

/**
 * @return the full URL
 */
export function requestedUri(
  req: Request,
  defaults?: Partial<UnwrapLazy<typeof UriDefaults>>
) {
  const d = { ...UriDefaults(), ...defaults }
  return URI.parse(
    (req?.protocol ?? d.protocol) +
      "://" +
      (req?.headers?.host ?? d.host) +
      (req?.originalUrl ?? d.url)
  )
}

const tier = lazy(() => "lite")

async function setTier() {
  tier.set(await t())
  settingsJson.unset()
}

const versionSuffix = lazy(() => (isProd() ? "" : "+" + nowLocal()))

const settingsJson = lazy(() => {
  const obj: WebSettings = {
    ver: version + versionSuffix(),
    paused: isPaused(),
    ll_vh: Settings.lazyLoadExtraVh.valueOrDefault,
    tier: tier()
  }
  // return stringify(obj)
  return Buffer.from(stringify(obj)).toString("base64")
})

later(() => {
  ee().on("settingsChanged", () => settingsJson.unset())
  ee().on("reloadLicenses", setTier)
  m.watch(setTier)
  ee().on("pause", () => settingsJson.unset())
  ee().on("resume", () => settingsJson.unset())
})

export function addXSettingsHandler(
  _req: Request,
  res: Response,
  next: NextFunction
) {
  res.setHeader("X-Settings", settingsJson())
  next()
}
