import { blank } from "../../fe/Blank"
import { stringify } from "../../fe/JSON"
import { Maybe } from "../../fe/MaybeTypes"
import { uid } from "../UID"

/**
 * @see https://developer.mozilla.org/en-US/docs/Web/API/Server-sent_events/Using_server-sent_events#fields
 */
export interface ServerSentEvent<E = string, D = any> {
  /**
   * A string identifying the type of event described. If this is specified,
   * an event will be dispatched on the browser to the listener for the
   * specified event name; the website source code should use
   * addEventListener() to listen for named events. The onmessage handler is
   * called if no event name is specified for a message.
   *
   */
  event?: E
  data: D
  id?: string | number
  /**
   * Time in milliseconds for the browser to wait before attempting to reconnect.
   */
  retry?: number
}

export function mkEventStream<E = string, D = any>(
  ...arr: Maybe<ServerSentEvent<E, D>>[]
): string {
  let s = ""
  for (const evt of arr) {
    if (evt == null) continue
    if (!blank(evt.event)) s += "event: " + evt.event + "\n"
    s += "id: " + (evt.id ?? uid()) + "\n"
    s += "data: " + stringify(evt.data) + "\n"
    // two newlines separate events:
    s += "\n"
  }
  return s
}
