import e from "express"
import { mkLogger } from "../../core/Logger"
import { mapGt0 } from "../../core/Number"
import { mapAsync, thenMapOr } from "../../core/async/Promise"
import { untilTrue } from "../../core/async/until"
import { durationHMS } from "../../core/date/Date"
import { WrappedError } from "../../core/error/WrappedError"
import { submitTask_ } from "../../core/event/SharedState"
import { jsonOrRedirect, requestLang } from "../../core/express/Handlers"
import { wrapWithToast } from "../../core/express/ServerToastHelpers"
import { aspectRatio } from "../../core/img/Dimensions"
import { Previews } from "../../core/img/Previews"
import { l } from "../../core/licensing/Licensing"
import { isLocalhost } from "../../core/net/ip"
import { isDocker } from "../../core/platform/isDocker"
import { Settings } from "../../core/settings/Settings"
import { commandTimeoutMs } from "../../core/volumes/VolumeTtls"
import { compact, includesAny, isEmpty } from "../../fe/Array"
import { sortByInPlace } from "../../fe/ArraySort"
import { assetUrl } from "../../fe/AssetUrls"
import { toBoolean } from "../../fe/Boolean"
import { secondMs } from "../../fe/Date"
import { map, mapOr } from "../../fe/Maybe"
import { Maybe, MaybePromiseMaybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { toGt0, toInt } from "../../fe/Number"
import { entries, keys, omit, pick } from "../../fe/Object"
import { normalizeRotation } from "../../fe/Rotation"
import { ApiAsset, ApiAssetInfo, SimpleAsset } from "../../fe/api/Asset"
import { Replace, mkReplace } from "../../fe/api/Redirect"
import { BeforeAfterStreamLimit } from "../../fe/api/Tag"
import { toS } from "../../fe/toS"
import { PartialPojo } from "../db/PartialPojo"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"
import { AssetRevision } from "../model/AssetRevision"
import { setAssetRotation_ } from "../model/AssetRotation"
import { ChangedTag } from "../model/ChangedTag"
import { Tag } from "../model/Tag"
import { writeAssetTags_ } from "../model/WriteAssetTags"

const logger = mkLogger("web.ApiAssetRouter")

const AssetFieldsWithoutRevisions = ["hidden", "excludedAt", "deletedAt"]

export class ApiAssetRouter {
  constructor(readonly previews: Previews) {}

  wrapApi<T>(
    name: string,
    f: (
      req: e.Request,
      assetId: number,
      asset: Asset
    ) => Maybe<T> | MaybePromiseMaybe<T>
  ): e.RequestHandler {
    return (req: e.Request, response: e.Response, _next: e.NextFunction) =>
      wrapWithToast({
        response,
        f: async () => {
          const assetId = toGt0(req.params.assetId)
          if (assetId == null) return response.sendStatus(400) // Bad Request
          const result = await thenMapOr(
            Asset.ops().findById(assetId, {
              shown: 1
            } as any),
            asset => f(req, assetId, asset),
            () => this.onMissingAsset(req, assetId)
          )

          logger.debug(name, { result })
          if (result != null) {
            return jsonOrRedirect(result, response)
          }
          throw new WrappedError("Asset " + assetId + " wasn't found", {
            retriable: false,
            doNotSend: true,
            fatal: false
          })
        }
      })
  }

  router(): e.Router {
    return e
      .Router()
      .get(
        "/api/asset/:assetId/info",
        this.wrapApi("ApiAssetRouter.assetInfo", this.assetInfo.bind(this))
      )
      .get(
        "/api/asset/:assetId/streams",
        this.wrapApi("ApiAssetRouter.assetInfo", this.assetStreams.bind(this))
      )
      .get(
        "/api/asset/:assetId",
        this.wrapApi("ApiAssetRouter.asset", this.asset.bind(this))
      )
      .put(
        "/api/asset/:assetId",
        this.wrapApi("ApiAssetRouter.save", this.save.bind(this))
      )
      .put(
        "/api/asset/:assetId/resync",
        this.wrapApi("ApiAssetRouter.resync", this.resync.bind(this))
      )
  }

  async assetInfo(
    req: e.Request,
    assetId: number,
    asset: Asset
  ): PromiseMaybe<ApiAssetInfo> {
    const assetFiles = asset.getAssetFiles() as AssetFile[]
    const shown = asset.getShown() as Maybe<AssetFile>
    const shownPathP = map(shown, ea => ea.getNativePath())
    const showOpenFile = isLocalhost(req.ip) && !isDocker()

    if (Settings.resyncAssetOnVisit.valueOrDefault) {
      void submitTask_("repairAsset", {
        assetId,
        forceSync: false,
        forceRebuildPreviews: false
      })
    }

    if (shown == null || shownPathP == null) {
      logger.error("no shown AssetFile found for " + assetId, {
        asset,
        shown,
        shownPathP
      })
      return
    }
    logger.debug("assetInfo()", {
      assetId,
      shown,
      shownPathP,
      ip: req.ip,
      showOpenFile
    })

    const tagModels = asset.getTags() as Tag[]
    const tags = await mapAsync({
      name: "api.assetInfo.tags",
      arr: tagModels,
      f: tag => tag.toApiTag()
    })
    const files = await mapAsync({
      name: "api.assetInfo.files",
      arr: assetFiles,
      f: af => af.toApi(this.previews, shown.sha)
    })

    // Show the shown file first, shove missing files to the bottom, and sort by
    // path:
    sortByInPlace(files, ea => [!ea.shown, !ea.exists, ea.nativePath])

    return {
      assetId,
      capturedAtLocale: shown.capturedAtLocale(requestLang(req))!,
      ...pick(
        shown,
        "mimetype",
        "focalLength",
        "iso",
        "aperture",
        "shutterSpeed"
      ),
      durationHMS: map(shown.durationMs, durationHMS),
      fps: shown.fps,
      files,
      tags,
      showOpenFile
    }
  }

  async onMissingAsset(req: e.Request, assetId: number, asset?: Asset) {
    // if next, get the next-younger asset.
    const next = req.query.from === "next"
    logger.info("onMissingAsset()", {
      query: entries(req.query),
      params: entries(req.params),
      assetId
    })

    const priorAssetId = toInt(String(req.query.priorAssetId))

    const fromAssetId = mapOr(
      asset,
      ea => ea.id,
      () => priorAssetId
    )

    if (fromAssetId == null) {
      logger.warn("onMissingAsset(): no context asset available")
      return
    }

    const fromTime = await thenMapOr(
      asset,
      ea => ea.capturedAtLocal,
      () =>
        Asset.dbl.pluckFirstf(q =>
          q.select("capturedAtLocal").where({ id: fromAssetId })
        )
    )

    let q = Asset.shownUnhidden(
      Asset.query()
        .select("id")
        .limit(1)
        .whereNotIn("id", compact([assetId, priorAssetId]))
    )
    if (next) {
      q = q
        .andWhere("capturedAtLocal", "<", fromTime)
        .orderBy("capturedAtLocal", "desc")
    } else {
      q = q
        .andWhere("capturedAtLocal", ">", fromTime)
        .orderBy("capturedAtLocal", "asc")
    }

    const toAsset = Asset.dbl.pluckFirst(q)
    logger.warn("Asset redirect", {
      fromAssetId,
      priorAssetId,
      fromTime,
      toAsset
    })
    return mapGt0(toAsset, ea =>
      mkReplace(
        assetUrl({ assetId: ea, params: [req.query, req.params] as any }),
        {
          text: `Asset ${fromAssetId} is no longer available.`,
          type: "info"
        }
      )
    )
  }

  async asset(
    req: e.Request,
    assetId: number,
    asset: Asset
  ): PromiseMaybe<ApiAsset | Replace> {
    const shown = asset.getShown()
    if (shown == null) {
      logger.error(
        "no shown AssetFile found for " + assetId + ", disabling (for now)."
      )
      asset.upsert({ shown: false })
      logger.error("Asset un-shown.")
      void submitTask_("repairAsset", {
        assetId,
        forceSync: true,
        forceRebuildPreviews: true
      })
      return this.onMissingAsset(req, assetId, asset)
    }
    asset.getBeforeAfterId()

    const result: SimpleAsset = {
      ...asset.partialSimpleAsset(),
      name: shown.basename,
      width: shown.width,
      height: shown.height,
      rotation: shown.rotation ?? 0,
      capturedAtLocale: shown.capturedAtLocale(requestLang(req))!,
      aspectRatio: aspectRatio(shown)!,
      durationHMS: map(shown.durationMs, durationHMS),
      downloadable: await shown.originalDownloadable({ brief: true }),

      // TODO: FIX THESE FIELDS FOR https://forum.photostructure.com/t/tag-context-browsing/92
      contextTag: await asset.whenApiTag(),
      beforeAssetId: asset.beforeId,
      afterAssetId: asset.afterId
    }

    if (shown.isVideo) {
      const videoSources = await asset.videoSources()
      await asset.getExistingAssetFiles()
      if (isEmpty(videoSources)) {
        logger.warn(
          "no existing files for " +
            assetId +
            ", trying to repair, but redirecting user now."
        )
        void submitTask_("repairAsset", {
          assetId,
          forceSync: true,
          forceRebuildPreviews: true
        })
        // Missing existing files:
        return this.onMissingAsset(req, assetId, asset)
      }
      return {
        ...result,
        videoAttrs: asset.videoAttrs(),
        videoSources
      }
    } else {
      return {
        ...result,
        imgAttrs: await asset.fitAttrs(this.previews)
      }
    }
  }

  async assetStreams(req: e.Request, assetId: number, asset: Asset) {
    const limit = toInt(toS(req.query.limit), {
      defaultValue: BeforeAfterStreamLimit
    })!
    const streams = asset.getStreams(limit)
    return logger.tap({
      msg: "assetStreams",
      result: {
        assetId,
        assetStreams: await mapAsync({
          name: "assetStreams",
          arr: streams,
          f: ea => ea.toApi()
        })
      }
    })
  }

  async save(req: e.Request, assetId: number, asset: Asset) {
    if (await l()) {
      logger.warn("save(): disabled")
      return { showTryPlusToast: true }
    }
    const obj: PartialPojo<Asset> = {
      hidden: toBoolean(req.body.hidden),
      excludedAt: map(toBoolean(req.body.excluded), ea =>
        ea ? Date.now() : null
      ),
      deletedAt: map(toBoolean(req.body.deleted), ea =>
        ea ? Date.now() : null
      ),
      rating: map(toBoolean(req.body.liked), b =>
        b ? Settings.likeRating.valueOrDefault : null
      )
    }

    if (!isEmpty(keys(obj))) {
      logger.info("save()", { obj, body: req.body })

      // Add an asset revision if any fields covered by AssetRevisions changed:
      AssetRevision.insert(
        ...entries(omit(obj, ...AssetFieldsWithoutRevisions)).map(
          ([field, newValue]) => ({
            assetId,
            field,
            priorValue: (asset as any)?.[field],
            newValue
          })
        )
      )

      const rating =
        obj.deleted === null ? null : obj.deleted != null ? -1 : obj.rating
      if (rating !== undefined) {
        await writeAssetTags_(assetId, { Rating: rating })
      }
      // Recount tags if any change:
      if (includesAny(keys(obj), AssetFieldsWithoutRevisions)) {
        ChangedTag.onAssetRemoval(assetId)
        void submitTask_("runTagMaintenance")
      }
      asset.upsert(obj)
    }
    const result: Partial<SimpleAsset> = asset.partialSimpleAsset()

    const rotation = normalizeRotation(toInt(req.body.rotation))
    if (rotation != null && rotation !== 0) {
      await setAssetRotation_(assetId, rotation, this.previews)
      result.rotation = rotation
    }

    return result
  }

  async resync(_req: e.Request, assetId: number) {
    Asset.dbl.runf(q => q.where({ id: assetId }).update({ version: 0 }))

    const vById = () =>
      Asset.dbl.pluckFirstf(q => q.where({ id: assetId }).select("updateCount"))

    const prior = vById()

    // TODO: SITS: events should support responses (?)
    void submitTask_("repairAsset", {
      assetId,
      forceSync: true,
      forceRebuildPreviews: true
    })

    await untilTrue(async () => prior !== vById(), {
      timeoutMs: commandTimeoutMs(),
      intervalMs: secondMs
    })
    return { v: vById() }
  }
}
