import { lazy } from "../../core/Lazy"
import { retryOnReject } from "../../fe/AsyncRetry"
import { entries, values } from "../../fe/Object"
import { thenOrOnTimeout, thenOrTimeoutError } from "../../fe/ThenOrTimeout"
import { Args } from "../Args"
import { addDefaultJsonRevivers } from "../JsonSetup"
import { mkLogger } from "../Logger"
import { isWorkerService } from "../ServiceNames"
import { ending } from "../async/Endable"
import { isRetriableError } from "../error/ErrorTypes"
import { statTimeoutMs } from "../fs/StatTimeout"
import { Settings } from "../settings/Settings"
import { singleThreadMode } from "../work/MaxCpus"
import { onTimeout } from "../work/WorkTimeout"
import { RequestTask } from "../worker/RequestTask"
import { WorkerCluster } from "../worker/WorkerCluster"
import {
  WorkRequestMethod,
  WorkRequestMethods,
  WorkRequests,
  WorkerFunctions
} from "./WorkRequests"

const logger = lazy(() => mkLogger("worker.ShimDelegation"))

let id = 0

const defaultTimeouts = [
  [
    WorkRequestMethods.buildAssetPreviews_,
    Settings.taskTimeoutMs.valueOrDefault
  ]
] as const

// only exported for tests
export const workerMethodTimeouts = lazy(
  () => new Map<WorkRequestMethod, number>(defaultTimeouts)
)

async function onChild<M extends WorkRequestMethod>(
  method: M,
  ...args: Args<WorkRequests[M]>
) {
  let bc = (await WorkerCluster()).t
  if (bc.ended) {
    if (ending()) {
      logger().warn("onChild(): cluster is ended, running task myself", {
        method,
        args
      })
      return (WorkerFunctions[method] as any)(...(args as any))
    } else {
      logger().warn("onChild(): cluster ended unexpectedly. Restarting.", {
        method,
        args
      })
      bc = (await WorkerCluster.refresh()).t
    }
  }
  const timeoutMs = workerMethodTimeouts().get(method) ?? statTimeoutMs()
  const request = { id: id++, method, args }
  return retryOnReject(
    async () => {
      const t = new RequestTask(request)
      const p = bc.enqueueTask(t)
      void thenOrOnTimeout(p, timeoutMs / 3, () => {
        logger().warn("soft timeout servicing work request", {
          request,
          timeoutMs
        })
        onTimeout()
      })
      return thenOrTimeoutError(p, timeoutMs)
    },
    {
      maxRetries: Settings.maxRetries.valueOrDefault,
      timeoutMs,
      retryDelay: Settings.minDelayBetweenRetriesMs.valueOrDefault,
      errorIsRetriable: err => isRetriableError(err) !== false
    }
  )
}

export async function setupShimDelegates() {
  addDefaultJsonRevivers()
  // The worker service shouldn't be calling this method, but in case it does:
  if (singleThreadMode() || isWorkerService()) {
    logger().warn("in single-threaded mode")
    clearShims()
    await (await WorkerCluster.prior())?.end()
  } else {
    for (const [meth, func] of entries(WorkerFunctions)) {
      ;(func as any).setShim((args: any) => onChild(meth, args))
    }
  }
}

export function clearShims() {
  for (const func of values(WorkerFunctions)) {
    func.setShim(undefined)
  }
}
