import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { isWebService } from "../../core/ServiceNames"
import { StdoutWrite } from "../../core/StdoutWrite"
import { errorToS } from "../../core/error/Error"
import { NoLibraryErrorFlag } from "../../core/error/ErrorFlags"
import { HealthCheck } from "../../core/health/HealthCheck"
import { Settings } from "../../core/settings/Settings"
import { commandTimeoutMs } from "../../core/volumes/VolumeTtls"
import { RunStateEnum } from "../../fe/RunStateEnum"
import { Library } from "../Library"
import { restartLibrary_ } from "../RestartLibrary"
import { dbModelHealthCheck } from "../health/DbModelHealthCheck"
import { runMaintenanceTasks } from "../model/MaintenanceTasks"
import { assertValidDbInfo_, libraryDbInfoJsonFile } from "./DbModelSetup"
import { clearDbSetupErrors } from "./DbSetupErrors"

const logger = lazy(() => mkLogger("db.DbModelRepair"))

export async function dbModelRepair(): Promise<{
  noop: boolean
  op: string
  state: RunStateEnum
}> {
  const result = await _dbModelRepair()
  const summary = await HealthCheck.rerunSetup()
  return { ...result, state: summary.state }
}

async function _dbModelRepair() {
  const library = Library.instance()
  if (library == null) {
    throw new Error("No library set." + NoLibraryErrorFlag)
  }

  const janitor = await library.dbModelJanitor()

  if (janitor == null) {
    throw new Error(
      "No library set (missing dbModelJanitor)." + NoLibraryErrorFlag
    )
  }

  // Is there actually nothing to do? If maintenance and backup tasks run
  // fine, there's nothing to repair.

  clearDbSetupErrors()
  const hc = await dbModelHealthCheck().refresh()

  logger().info("dbModelRepair: health check result: ", hc)

  if (Settings.dbForceRecover.valueOrDefault) {
    HealthCheck.addLoadingMsg(`PS_DB_FORCE_REPAIR was set. Trying to repair.`)
  } else if (hc.level !== "ok") {
    HealthCheck.addLoadingMsg(
      `Database health check failed: ${hc.msg}. Trying to repair.`
    )
    Settings.dbForceRecover.envValue = true
    // don't run maintenance tasks if the db is broken!
  } else {
    try {
      HealthCheck.addLoadingMsg("Running maintenance tasks...")
      await runMaintenanceTasks({ force: true })
      HealthCheck.addLoadingMsg("Backing up the library database...")
      await janitor.forceBackup_()
      HealthCheck.addLoadingMsg("Validating the library database...")
      janitor.db.verify_()
      HealthCheck.addLoadingMsg(
        "No repair needed: maintenance tasks, backup, and integrity tests ran without error."
      )
      return logger().tap({
        msg: "dbModelRepair()",
        level: "info",
        result: { noop: true, op: "validated" }
      })
    } catch (error: any) {
      HealthCheck.addLoadingMsg(
        `Maintenance, backup, or integrity tasks failed: ${errorToS(
          error
        )}. Trying to repair.`
      )
      Settings.dbForceRecover.envValue = true
    }
  }

  if (isWebService()) {
    StdoutWrite.shutdownSync()
    // This should let us repair without sync getting bothered
  }

  const fslock = await library.dbFsLock()
  const acquired = await fslock.acquire_({
    timeoutMs: commandTimeoutMs() * 2,
    // the db fslock files are used to keep track of everyone with the db
    // open:
    releaseOnFailure: false
  })
  if (!acquired) {
    return logger().throw(
      "Failed to acquire exclusive lock on the library database. Shut down other processes before retrying."
    )
  }

  try {
    await assertValidDbInfo_()
  } catch (error: any) {
    HealthCheck.addLoadingMsg(
      `Library database setup was invalid: ${errorToS(
        error
      )}. Resetting and restarting.`
    )
    await libraryDbInfoJsonFile().unlink()
    Settings.dbForceRecover.envValue = true
  }
  // WAIT WUT? WHERE'S THE BEEF?

  // I'M GLAD YOU ASKED, FUTURE ME! Db repair is done automatically at
  // library startup. See DbModelSetup.ts and Db.migrate_()
  await restartLibrary_()
  const dbForceRepair = Settings.dbForceRecover.valueOrDefault
  Settings.dbForceRecover.unset()
  return logger().tap({
    msg: "dbModelRepair()",
    level: "info",
    result: { op: dbForceRepair ? "repaired" : "restarted", noop: false }
  })
}
