import { BatchCluster } from "batch-cluster"
import {
  BatchClusterObserver,
  batchClusterOptions
} from "../../core/BatchClusterObserver"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { ReadyStr } from "../../core/StdoutWrite"
import { EndableRanks } from "../../core/async/Endable"
import { spawn } from "../../core/child/ChildProcess"
import { pathToService } from "../../core/child/ChildService"
import { FatalErrorRe } from "../../core/error/ErrorFlags"
import { splitLines } from "../../core/fs/CRLF"
import { isProgressEvt } from "../../core/progress/ProgressEvt"
import { emitProgressEvt } from "../../core/progress/ProgressObserver"
import { maxCpus } from "../../core/work/MaxCpus"
import { secondMs } from "../../fe/Date"
import { stringify } from "../../fe/JSON"
import { clamp } from "../../fe/Number"
import { parseJSON } from "../../fe/ParseMaybe"
import { toS } from "../../fe/toS"
import { ChildServiceExitCommand } from "../child/ChildServiceExitCommand"
import { isPacked } from "../platform/IsPacked"
import { Settings } from "../settings/Settings"
import { cpuCount } from "../work/CpuInfo"
import { WorkRequest } from "./WorkRequests"
import { workerEnv } from "./WorkerEnv"

const logger = lazy(() => mkLogger("worker.cluster"))

export async function workerClusterPriorEnd() {
  return (await WorkerCluster.prior())?.t.closeChildProcesses()
}

export const WorkerCluster = lazy(async () => {
  const worker = await pathToService("worker")
  if (worker == null) {
    return logger().throw("Could not find worker.js")
  }
  logger().info("worker.js found at " + worker)

  const pingReq: WorkRequest<"ping"> = {
    id: -1,
    method: "ping",
    args: [{}]
  }

  const bc = new BatchCluster({
    processFactory: async () => {
      logger().info("Spawning new worker", {
        execPath: process.execPath,
        worker: worker.nativePath,
        // Why not maxConcurrentImports(), you ask? Because each import may do
        // several things in parallel, and we want enough child workers to
        // handle that effectively and not result in spurious timeouts due to
        // starvation on the worker queue.
        maxProcs: maxCpus()
      })
      return spawn(process.execPath, [worker.nativePath], 0, {
        env: await workerEnv()
      })
    },

    ...batchClusterOptions(maxCpus(), "worker.BatchCluster"),
    // worker doesn't emit to stderr!
    streamFlushMillis: 0,
    versionCommand: stringify(pingReq),
    healthCheckCommand: stringify(pingReq),
    taskTimeoutMillis: Settings.taskTimeoutMs.valueOrDefault,
    maxProcAgeMillis: Settings.taskTimeoutMs.valueOrDefault * 4,
    minDelayBetweenSpawnMillis: isPacked() ? secondMs : 1,
    pass: ReadyStr,
    fail: FatalErrorRe,
    exitCommand: ChildServiceExitCommand
  })
  bc.on("taskData", buf => {
    // this may have progress events. Publish those.
    for (const line of splitLines(toS(buf))) {
      const ea = parseJSON(line)
      if (isProgressEvt(ea)) emitProgressEvt(ea)
    }
  })

  // if we get soft timeouts, that may cause concurrency to be throttled:
  maxCpus.watchLater(newValue => {
    const maxProcs = clamp(1, cpuCount(), newValue ?? maxCpus())
    logger().warn("maxConcurrentImports changed", { maxProcs, newValue })
    return bc.setMaxProcs(maxProcs)
  })

  return new BatchClusterObserver("worker", bc, EndableRanks.first)
})
