import { CPUs, osFullName } from "../core/OperatingSystem"
import { procDeviceModel } from "../core/platform/isRaspberryPi"
import { isWebService } from "../core/ServiceNames"
import { Settings } from "../core/settings/Settings"
import { libraryHasSettings } from "../core/settings/SettingsIO"
import { runtimeMs } from "../core/StartTs"
import { userid, username } from "../core/UserInfo"
import { version } from "../core/Version"
import { CpuUsage } from "../core/work/CpuUsage"
import {
  maxConcurrentImports,
  sharpThreadsPerProcess
} from "../core/work/MaxCpus"
import { compact } from "../fe/Array"
