import { isTrue } from "../fe/Boolean"
import { fmtDuration } from "../fe/FmtDuration"
import { HealthCheckLevel } from "../fe/HealthChecks"
import { map, mapOr } from "../fe/Maybe"
import { PromiseMaybe } from "../fe/MaybeTypes"
import { fmtPct } from "../fe/Number"
import { thenMap } from "../fe/Promise"
import { stripSuffix } from "../fe/String"
import { Metric, plurMetric } from "../fe/Units"
import { Asset } from "./model/Asset"
import { AssetFile } from "./model/AssetFile"
import { Tag } from "./model/Tag"

export type DictEntryClass = "ok" | "warn" | "tt" | "fail" | "pending"

export interface DictEntry {
  term: string
  termURL?: string
  defn: string | Metric[]
  defnClass?: DictEntryClass
  defnTitle?: string
}

export async function libraryMetrics(): PromiseMaybe<Metric[]> {
  if (Asset.db() == null) return
  // The library may not be initialized for the `info` tool:
  const arr: Metric[] = []
  arr.push(plurMetric(Asset.shownCount(), "asset"))
  const mimeCounts = AssetFile.assetFileCountByMimeTypeRoots()
  for (const ea of mimeCounts) {
    const type = stripSuffix(ea.mimeTypeRoot, "/")
    // TODO: add the assetCount?
    arr.push(plurMetric(ea.assetFileCount, type + " file"))
  }
  arr.push(plurMetric(Tag.rows(), "tag"))
  return arr
}

const HealthCheckLevelDictEntryClass = {
  pending: "pending",
  ok: "ok",
  "no-library": "warn",
  warn: "warn",
  "stop-sync": "warn",
  error: "fail",
  disabled: "ok"
} as const satisfies Record<HealthCheckLevel, DictEntryClass>

export async function systemInformation(): Promise<DictEntry[]> {
  const best = await b()
  const tier = best?.l?.tier ?? "lite"
  const health = HealthCheck.summary()

  return compact([
    {
      term: "Version",
      defn: version
    },
    {
      term: "Edition",
      defn: EditionName()
    },
    {
      term: "Health checks",
      defnClass: HealthCheckLevelDictEntryClass[health.level],
      defnTitle: "Click to see all health checks",
      termURL: "/health",
      defn: health.msg[0]
    },
    {
      term: "Subscription",
      defnClass: tier,
      defn: tier,
      termURL: "/plans/billing?then=/about",
      defnTitle: "Click to manage your PhotoStructure subscription"
    },
    ...(isTrue(best?.ok)
      ? [
          {
            term: "Licensed to",
            defn: best?.l?.sub
          },
          {
            term: "Expires or renews",
            defn: mapOr(best?.l?.exp, ea => toIsoDate(ea), "--"),
            termURL: "/plans/billing?then=/about",
            defnTitle: "Click to manage your PhotoStructure subscription"
          }
        ]
      : []),
    { term: "OS", defn: osFullName() },
    { term: "CPUs", defn: CPUs() },
    {
      term: "System load",
      defn:
        map(CpuUsage.instance().busyPct(), ea => fmtPct(ea, 200) + " busy") ??
        "(unavailable)"
    },
    {
      term: "Concurrency",
      defn: `Target system use: ${Math.round(
        Settings.cpuBusyPercent.valueOrDefault
      )}% (${maxConcurrentImports()} concurrent imports, ${sharpThreadsPerProcess()} gfx/process)`
    },
    // We may be called by info.js:
    isWebService()
      ? { term: "Web uptime", defn: fmtDuration(runtimeMs()) }
      : undefined,
    mapNotBlank(procDeviceModel(), defn => ({ term: "Device", defn })),
    {
      term: "Current user",
      defn: (await username()) + " (userid: " + userid() + ")"
    },
    libraryHasSettings()
      ? {
          term: "Library path",
          defn: Settings.libraryDir.valueOrDefault,
          defnClass: "library"
        }
      : undefined,
    await thenMap(libraryMetrics(), metrics => ({
      term: "Library metrics",
      termURL: "https://photostructure.com/faq/metrics/",
      defn: metrics
    })),
    {
      term: "Log directory",
      defn: logDir()
    },
    {
      term: "Log level",
      termURL: "https://photostructure.com/faq/error-reports/#log-levels",
      defn: defaultLogLevel()
    }
  ]).filter(({ term, defn }) => notBlank(term) && notBlank(defn)) as any
}
