import _e from "express"
import { lazy } from "../../core/Lazy"
import { map } from "../../fe/Maybe"
import { XLocationRedirect, XLocationReplace } from "../../fe/api/Redirect"
import {
  DismissAnchor,
  ServerToast,
  ToastPlusOnly
} from "../../fe/api/ServerToast"
import { mkHomeFullPath } from "../../fe/app/FullPaths"
import { toErr } from "../../fe/toErr"
import { mkLogger } from "../Logger"
import { errorToS } from "../error/Error"
import { isFatalError, stripErrorFlags } from "../error/ErrorTypes"
import { l } from "../licensing/Licensing"
import { BooleanSetting } from "../settings/BooleanSetting"
import { isAjaxRequest } from "../web/HttpHeaders"
import { HttpStatus } from "../web/HttpStatus"
import { requestedUri } from "./RequestMeta"

const logger = lazy(() => mkLogger("express.ServerToastHelpers"))

export async function requirePlus(
  _req: _e.Request,
  res: _e.Response,
  next: _e.NextFunction
) {
  if (await l()) {
    const t: ToastPlusOnly = { toastPlusOnly: true }
    res.status(HttpStatus.Forbidden) // forbidden
    return res.json(t)
  } else {
    return next()
  }
}

export function requireEnabled(s: BooleanSetting) {
  return (_req: _e.Request, res: _e.Response, next: _e.NextFunction) => {
    if (!s.valueOrDefault) {
      return sendServerToast({
        response: res,
        toast: {
          text: "This has been disabled by your administrator.",
          details: `(see setting "${s.name}")`,
          type: "warning"
        },
        httpStatusCode: HttpStatus.Forbidden
      })
    } else {
      return next()
    }
  }
}

// Mostly useful for enforcing ServerToast types:

export function sendServerToast({
  response,
  toast,
  httpStatusCode,
  reload
}: {
  response: _e.Response
  toast: ServerToast
  httpStatusCode?: number
  reload?: boolean
}) {
  map(httpStatusCode, ea => response.status(ea))
  logger().warn("sendServerToast()", {
    requestedURI: requestedUri(response.req),
    toast,
    httpStatusCode
  })
  return response.json({ toast, reload })
}

export function sendRedirectionToast({
  response,
  toast,
  url,
  redirectOrReplace
}: {
  response: _e.Response
  url: string
  toast?: ServerToast
  redirectOrReplace?: "redirect" | "replace"
}) {
  if (!isAjaxRequest(response.req.headers)) {
    return response.redirect(url)
  } else {
    const key =
      redirectOrReplace === "replace" ? XLocationReplace : XLocationRedirect
    response.setHeader(key, url)
    logger().warn("sendRedirectionToast()", {
      requestedURI: requestedUri(response.req),
      toast,
      key,
      url
    })
    return response.json({ toast })
  }
}

export function sendWarningServerToast({
  response,
  toast,
  httpStatusCode,
  reload
}: {
  response: _e.Response
  toast: ServerToast
  httpStatusCode?: number
  reload?: boolean
}) {
  toast.type ??= "warning"
  return sendServerToast({ response, toast, httpStatusCode, reload })
}

export function send404Toast({
  text,
  details,
  response,
  httpStatusCode = 404
}: {
  text: string
  details?: string | undefined
  response: _e.Response
  httpStatusCode?: number | undefined
}) {
  return sendServerToast({
    response,
    toast: {
      text,
      details,
      type: "warning",
      button1Text: "Go home",
      button1Url: mkHomeFullPath() + DismissAnchor,
      button1Class: "info",
      onClickUrl: DismissAnchor,
      timeout: false
    },
    httpStatusCode
  })
}

export function sendErrorToast(
  err: Error,
  res: _e.Response,
  httpStatusCode?: number | 404
) {
  logger().warn("sendErrorToast", err)
  const details = stripErrorFlags(errorToS(err))
  sendServerToast({
    response: res,
    toast: {
      text:
        httpStatusCode === 404 ? "Not found" : "Well, darn, that didn't work",
      uuid: details,
      details,
      type: isFatalError(err) ? "error" : "warning",
      button1Text: "Return home",
      button1Url: "/" + DismissAnchor,
      button1Class: "info",
      onClickUrl: DismissAnchor
    },
    httpStatusCode: httpStatusCode ?? HttpStatus.ServiceUnavailable
  })
}

export function expressErrorHandlerWithToast(
  err: Error,
  _req: _e.Request,
  res: _e.Response,
  next: _e.NextFunction
) {
  err == null ? next() : sendErrorToast(err, res)
}

export async function wrapWithToast({
  f,
  response
}: {
  f: () => any
  response: _e.Response
}) {
  try {
    await f()
  } catch (err) {
    map(toErr(err), error => sendErrorToast(error, response))
  }
}
