import { ServerSentEventsRouter } from "../../core/express/ServerSentEventsRouter"
import { HealthCheck } from "../../core/health/HealthCheck"
import { version } from "../../core/Version"
import { ServerSentEvent } from "../../core/web/EventSource"
import { secondMs } from "../../fe/Date"
import { ApiSplashEvent, RunStateEnum, RunStates } from "../../fe/RunStateEnum"

const StateToMsg: Record<RunStateEnum, string> = {
  loading: " Loading...",
  welcome: " Welcome!",
  ready: " Ready",
  failed: " Something's not right. Gathering details..." // use ?
}

export class ApiSplashRouter extends ServerSentEventsRouter<ApiSplashEvent> {
  #interval: NodeJS.Timeout | null = null

  constructor() {
    super("/api/splash")
  }

  runStateEvent(
    state: RunStateEnum = HealthCheck.runState()
  ): ServerSentEvent<ApiSplashEvent> {
    const isLoading = state === RunStates.loading
    if (isLoading && this.streams.length > 0) {
      this.#interval ??= setInterval(() => {
        this.writeToStreams(this.runStateEvent())
      }, secondMs)
    } else if (this.#interval != null) {
      clearInterval(this.#interval)
      this.#interval = null
    }
    return this.logger.tap({
      msg: "runStateEvent()",
      level: "warn",
      result: isLoading
        ? {
            event: "msg",
            data: HealthCheck.loadingMsg() ?? StateToMsg[state]
          }
        : { event: "settled", data: StateToMsg[state] },
      meta: { state }
    })
  }

  override firstEvents() {
    return [{ event: "version", data: version } as const, this.runStateEvent()]
  }
}
