import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { isEmptyObj } from "../../fe/Object"
import { normalizeRotation } from "../../fe/Rotation"
import { mkLogger } from "../Logger"
import { time } from "../async/PromiseTimer"
import { PosixFile } from "../fs/PosixFile"
import { writeTags_ } from "../tags/ExifTool"
import {
  Actions,
  ensureHistoryRecords,
  mkHistoryRecords
} from "../tags/History"
import { rotationToWriteTag } from "../tags/Orientation"
import { imageHash } from "./ImageHash"
import { compareImageHashes } from "./ImageHashComparison"

const logger = lazy(() => mkLogger("img.MatchRotation"))

/**
 * @param exemplar the image that whose rotation we want to match
 * @param dest the image that we want to rotate to match `exemplar`
 * @return the rotation that should be applied to `dest` to match `exemplar`
 */
export async function matchRotation(exemplar: PosixFile, dest: PosixFile) {
  return time("img.matchRotation", async () => {
    const exemplarImageHash = await imageHash(exemplar)
    const destImageHash = await imageHash(dest)
    const imageHashComparison = compareImageHashes(
      exemplarImageHash,
      destImageHash
    )
    if (
      exemplarImageHash == null ||
      destImageHash == null ||
      imageHashComparison == null
    ) {
      logger().warn("failed to compute image hashes", { a: exemplar, b: dest })
      return
    }

    // https://docs.google.com/spreadsheets/d/1k8fA48w9buUAfsDEy9J-wTNR5GoTj4lZ93L3Qaw4JqM/edit?usp=sharing

    return logger().tap({
      level: "info",
      msg: "matchRotation()",
      result: {
        rotation: imageHashComparison.bRotation,
        exemplarImageHash,
        destImageHash,
        imageHashComparison
      },
      meta: {
        src: exemplar,
        dest
      }
    })
  })
}

/**
 * @param exemplar the image that whose rotation we want to match
 * @param dest the image that we want to rotate to match `exemplar`
 * @return the rotation that should be applied to `dest` to match `exemplar`
 */
export async function matchAndWriteRotation_(
  exemplar: PosixFile,
  dest: PosixFile,
  additionalRotation = 0
) {
  const r = await matchRotation(exemplar, dest)
  if (r == null) return
  const mimetype = r.destImageHash.mimetype
  if (blank(mimetype)) {
    logger().warn("matchRotation() returned null destImageHash.mimetype", {
      r,
      dest
    })
    return
  }
  // rotation to match exemplar is r.rotation.
  // EXIF orientation to match exemplar is r.rotation + r.destImageHash.rotation
  // additional rotation can just be added on.
  const newRotation = normalizeRotation(
    r.destImageHash.rotation + r.rotation + additionalRotation
  )
  logger().info("matchRotation() computed newRotation", {
    currentRotation: r.destImageHash.rotation,
    additionalRotation,
    rotationToMatch: r.rotation,
    newRotation
  })
  if (newRotation == null) {
    logger().warn("Can't compute final orientation", {
      newRotation,
      file: dest.nativePath,
      r
    })
    return
  }

  const writtenTags = rotationToWriteTag(newRotation, mimetype)
  if (isEmptyObj(writtenTags)) {
    logger().warn("rotationToWriteTag() returned null", {
      newRotation,
      mimetype: r.destImageHash.mimetype,
      file: dest.nativePath
    })
    return
  }
  const historyRecords = await ensureHistoryRecords(
    await dest.sidecar(),
    mkHistoryRecords(Actions.set, writtenTags)
  )
  await writeTags_(dest, writtenTags, mimetype)

  return logger().tap({
    level: "info",
    msg: "matchAndWriteRotation_()",
    result: {
      ...r,
      writtenTags,
      rotation: newRotation,
      historyRecords
    },
    meta: {
      exemplar,
      dest
    }
  })
}
