import process from "process"
import { delay } from "../../fe/Delay"
import { gt0 } from "../../fe/Number"
import { isTest } from "../NodeEnv"
import { shim1 } from "../Shim1"
import { exit } from "../async/Exit"
import { WrappedError } from "../error/WrappedError"
import { memoryUsageIsHigh } from "../health/MemoryHealthCheck"

export const ping = shim1({
  name: "worker.ping",
  async impl(args: {
    input?: string
    delay?: number
    throw?: {
      message: string
      errno?: number
      ignorable?: boolean
    }
    pretendMemUsageIsHigh?: boolean
  }): Promise<{ input: string; pid: number }> {
    if (gt0(args?.delay)) {
      await delay(args.delay)
    }

    if (isTest && true === args?.pretendMemUsageIsHigh) {
      memoryUsageIsHigh.set(true)
    }

    if (args?.throw != null) {
      throw new WrappedError(args.throw.message, args.throw)
    }

    if (memoryUsageIsHigh()) {
      void exit({ reason: "memory usage is high", status: 1 })
      throw new WrappedError("Memory usage is high", { fatal: true })
    }

    return {
      pid: process.pid,
      input: args?.input ?? "(no input)"
    }
  }
})
