import path from "path"
import process from "process"
import { AppName } from "../core/AppName"
import { lazy } from "../core/Lazy"
import { mkLogger } from "../core/Logger"
import { isProd } from "../core/NodeEnv"
import { ShortCommandTimeoutMs } from "../core/ShortCommandTimeoutMs"
import { wbrPath } from "../core/String"
import { mapAsync } from "../core/async/Promise"
import { homeDir } from "../core/dir/HomeDir"
import { isLibraryDir } from "../core/dir/IsLibraryDir"
import { picturesDir } from "../core/dir/PicturesDir"
import { env } from "../core/env/Env"
import { canRWXFirstExistingAncestor } from "../core/fs/Access"
import { BaseFile } from "../core/fs/BaseFile"
import { whyExcludedDirectoryRecursive } from "../core/fs/Excluded"
import { containsHiddenPathname, isNotHiddenPosixPath } from "../core/fs/Path"
import { childDirectories_ } from "../core/fs/Readdir"
import { statMaybe } from "../core/fs/Stat"
import { isDocker } from "../core/platform/isDocker"
import { Settings, SuggestedDirsEnvKey } from "../core/settings/Settings"
import { Volume } from "../core/volumes/Volume"
import { bestVolumeForPath, volumes } from "../core/volumes/Volumes"
import { compactBlanks, filterInPlace, isEmpty, uniqBy } from "../fe/Array"
import { sortBy } from "../fe/ArraySort"
import { blank } from "../fe/Blank"
import { Maybe } from "../fe/MaybeTypes"
import { gt0 } from "../fe/Number"
import { GB, fmtBytes } from "../fe/Units"

const logger = lazy(() => mkLogger("library.SuggestedLibraryDir"))

export class SuggestedLibraryDir {
  static async for(nativePath: Maybe<string>, vols?: Volume[]) {
    if (blank(nativePath)) return
    // (this calls actualPath for us)
    const vol = await bestVolumeForPath(nativePath, vols)
    if (vol == null) {
      logger().warn(
        "Skipping library directory suggestion: failed to find mountpoint volume",
        { nativePath }
      )
      return
    }
    const isLibrary = await isLibraryDir(nativePath)
    return new SuggestedLibraryDir(nativePath, isLibrary, vol)
  }

  readonly isCurrentLibrary: boolean

  constructor(
    readonly nativePath: string,
    readonly isLibrary: boolean,
    readonly vol: Volume
  ) {
    this.isCurrentLibrary = Settings.libraryDir.value === this.nativePath
  }

  async whyExcluded(
    minAvailableBytes: number = Settings.minDiskFreeGb.valueOrDefault * GB
  ) {
    if (this.isLibrary) return

    const stat = await statMaybe(this.nativePath, "trace")
    const log = {
      msg: "whyExcluded()",
      meta: {
        nativePath: this.nativePath,
        stat,
        availableBytes: this.availableBytes,
        minAvailableBytes
      }
    }
    if (containsHiddenPathname(this.nativePath)) {
      return logger().tap({ ...log, result: "contains hidden path" })
    }
    {
      const result = await whyExcludedDirectoryRecursive(
        BaseFile.for(this.nativePath)
      )
      if (result != null) {
        return logger().tap({ ...log, result })
      }
    }

    if (stat != null && !stat.isDirectory()) {
      return logger().tap({ ...log, result: "not a directory" })
    }

    if (!(await canRWXFirstExistingAncestor(this.nativePath))) {
      return logger().tap({
        ...log,
        result: "no write access to directory"
      })
    }

    if (gt0(minAvailableBytes) && this.availableBytes < minAvailableBytes) {
      return logger().tap({
        ...log,
        result: "volume has insufficient free space for a new library"
      })
    }

    return
  }

  get mountpoint() {
    return this.vol.mountpoint
  }

  get availableBytes() {
    return this.vol.available
  }

  get free() {
    return fmtBytes(this.vol.available, 2)
  }

  get label() {
    return this.vol.label
  }

  get uuid() {
    return this.vol.uuid
  }

  get remote() {
    return this.vol.remote
  }

  get remoteHost() {
    return this.vol.remoteHost
  }

  get remoteShare() {
    return this.vol.remoteShare
  }

  get wbrPath() {
    return wbrPath(this.nativePath)
  }
}

export const suggestedLibraryDirs = lazy(() => _suggestedLibraryDirs())

export async function _suggestedLibraryDirs(
  minAvailableBytes: number = Settings.minDiskFreeGb.valueOrDefault * GB,
  maxSuggestionCount: number = 6,
  maybeVolumes?: Promise<Volume[]>
): Promise<SuggestedLibraryDir[]> {
  const start = Date.now()
  const suggestedPaths = env()[SuggestedDirsEnvKey]
  if (!isProd && suggestedPaths != null) {
    return logger().tap({
      msg: "using ENV",
      result: compactBlanks(suggestedPaths.split(path.delimiter)).map(
        nativePath =>
          new SuggestedLibraryDir(nativePath, false, {
            mountpoint: "/test",
            available: GB
          } as Volume)
      )
    })
  }

  const priorLibraries: SuggestedLibraryDir[] = []
  const suggestions: SuggestedLibraryDir[] = []

  const vols = (await maybeVolumes) ?? (await volumes())
  if (vols == null || isEmpty(vols)) {
    logger().warn("volumes() is empty: they don't get any suggestions.")
    return []
  }

  async function addSuggestion(nativePath: Maybe<string>) {
    const s = await SuggestedLibraryDir.for(nativePath, vols)
    if (s == null) {
      return
    }

    if (s.isLibrary || s.isCurrentLibrary) {
      logger().info("addSuggestion(): path is a library", s)
      priorLibraries.push(s)
      return
    }

    if (null == (await s.whyExcluded(minAvailableBytes))) {
      suggestions.push(s)
    }
  }

  await addSuggestion(Settings.libraryDir.value)

  if (isDocker()) return [...priorLibraries, ...suggestions]

  await addSuggestion(await picturesDir())

  const dirsToCheck = [
    process.cwd(),
    ...(await childDirectories_(process.cwd()))
  ]
  if (!isDocker()) {
    dirsToCheck.push(...(await childDirectories_(homeDir())))
  }

  filterInPlace(dirsToCheck, ea => isNotHiddenPosixPath(ea))

  // are there any child dirs that are libraries?
  for (const dir of dirsToCheck) {
    if (await isLibraryDir(dir)) {
      logger().info("current working dir is a library", dir)
      await addSuggestion(dir)
    }
  }

  function shouldTimeout() {
    if (Date.now() - start > ShortCommandTimeoutMs - 500) {
      logger().debug("...ran out of time, stopping.")
      return true
    }
    return false
  }

  async function addVolume(vol: Volume) {
    try {
      // add only the mountpoint if it's already a library:
      if (await isLibraryDir(vol.mountpoint)) {
        logger().info(
          "addVolume(): vol.mountpoint is a library",
          vol.mountpoint
        )
        await addSuggestion(vol.mountpoint)
        return
      }
      // are there any child dirs that are libraries?
      for (const dir of await childDirectories_(vol.mountpoint)) {
        if (shouldTimeout()) break
        if (await isLibraryDir(dir)) {
          logger().info("addVolume(): dir is a library", dir)
          await addSuggestion(vol.mountpoint)
        } else {
          for (const child of await childDirectories_(dir)) {
            if (shouldTimeout()) break
            if (await isLibraryDir(child)) {
              logger().info("addVolume(): dir is a library", dir)
              await addSuggestion(vol.mountpoint)
            }
          }
        }
      }

      // Maybe add a default suggestion:
      const defaultPath = path.join(vol.mountpoint, AppName())
      logger().info("addVolume(): adding default suggestion", defaultPath)
      await addSuggestion(defaultPath)
    } catch (err) {
      logger().warn("addVolume(): Failed to add suggestion", { err, vol })
    }
  }

  if (!isDocker()) {
    await addSuggestion(path.join(homeDir(), AppName()))
  }

  await mapAsync({
    name: "suggestedLibraryDirs.addVolume",
    arr: vols,
    f: vol => addVolume(vol),
    timeoutMs: ShortCommandTimeoutMs
  })

  logger().debug("Finished gathering suggestions. Filtering...", {
    priorLibraries,
    suggestions
  })

  const result = priorLibraries
  for (const s of sortBy(suggestions, ea => [
    -ea.availableBytes,
    -ea.nativePath.length
  ])) {
    if (result.length >= maxSuggestionCount) break
    if (
      !result.some(
        ea => ea.nativePath === s.nativePath || ea.mountpoint === s.mountpoint
      )
    ) {
      logger().debug("adding new path", s)
      result.push(s)
    }
  }
  return uniqBy(
    sortBy(result, ea => [
      !ea.isCurrentLibrary,
      !ea.isLibrary,
      -ea.free,
      ea.nativePath
    ]),
    ea => ea.nativePath
  )
}
