import { childEnv } from "../../core/child/ChildEnv"
import { Settings } from "../../core/settings/Settings"
import { mountpoints } from "../../core/volumes/Mountpoints"
import { isTrue } from "../../fe/Boolean"
import { values } from "../../fe/Object"

export async function workerEnv() {
  const overrides = {}
  for (const s of values(Settings)) {
    if (s.hasValue() && !isTrue(s.opts.transient)) {
      s.addToEnvMaybe(overrides)
    }
  }
  Settings.libraryDir.addToEnv(overrides)
  Settings.mountpoints.addToEnv(overrides, await mountpoints())
  return childEnv({ overrides, forWorker: true })
}
