import { NextFunction, Request, Response } from "express"
import helmet from "helmet"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { Settings } from "../../core/settings/Settings"
import { isHttpsRequest } from "../../core/web/HttpHeaders"
import { compactBlanks } from "../../fe/Array"

const logger = lazy(() => mkLogger("web.WebSecurity"))

export function cspHandler(req: Request, res: Response, next: NextFunction) {
  if (req.secure || isHttpsRequest(req.headers)) {
    if (Settings.upgradeInsecureRequests.value == null) {
      logger().warn(
        `Secure https request detected: setting "upgradeInsecureRequests" to true.`
      )
      Settings.upgradeInsecureRequests.tmpValue = true
    }
  }

  const selfAndCspDirective = compactBlanks([
    "'self'",
    Settings.cspDirective.valueOrDefault
  ])

  const directives = compactBlanks([
    "'self'",
    "'strict-dynamic'",
    // "'unsafe-inline'",
    `'nonce-${res.locals.cspNonce}'`,
    // https://csp-evaluator.withgoogle.com/: Consider adding https: and http:
    // url schemes (ignored by browsers supporting 'strict-dynamic') to be
    // backward compatible with older browsers.
    Settings.cspDirective.valueOrDefault
  ])

  const cspMiddleware = helmet.contentSecurityPolicy({
    directives: {
      defaultSrc: ["'self'"],
      scriptSrc: directives,
      styleSrc: selfAndCspDirective,
      baseUri: selfAndCspDirective,
      formAction: ["'self'", "https://account.photostructure.com"],
      objectSrc: ["'none'"],

      // TODO SITS: helmet types are wrong:
      upgradeInsecureRequests: (Settings.upgradeInsecureRequests.valueOrDefault
        ? []
        : null) as any
    },
    reportOnly: Settings.cspReportOnly.valueOrDefault
  })

  cspMiddleware(req, res, next)
}
