import e from "express"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { extractInt } from "../../core/Number"
import { StdoutWrite } from "../../core/StdoutWrite"
import {
  broadcastPause,
  broadcastResume
} from "../../core/event/BroadcastEvent"
import { submitTask_ } from "../../core/event/SharedState"
import { wrap } from "../../core/express/Handlers"
import {
  requireEnabled,
  requirePlus,
  sendServerToast,
  sendWarningServerToast,
  wrapWithToast
} from "../../core/express/ServerToastHelpers"
import { SimpleFileOrString } from "../../core/fs/SimpleFile"
import { exists } from "../../core/fs/Stat"
import { syncReportReadme } from "../../core/fs/SyncReport"
import { Settings } from "../../core/settings/Settings"
import { isPaused } from "../../core/work/IsPaused"
import { isNotEmpty } from "../../fe/Array"
import { notBlank } from "../../fe/Blank"
import { mapTrue } from "../../fe/Boolean"
import { isEmailMaybe } from "../../fe/Email"
import { stringify } from "../../fe/JSON"
import { toInt } from "../../fe/Number"
import { fmt } from "../../fe/Units"
import { SystemState } from "../../fe/api/SystemState"
import { toS, toStr } from "../../fe/toS"
import { forceRebuildLibrary } from "../ForceRebuildLibrary"
import { shutdown } from "../Shutdown"
import { Asset, RemovableColumn } from "../model/Asset"
import { Operation, OperationName, OperationNames } from "../model/Operation"

const logger = lazy(() => mkLogger("web.ApiSystemRouter"))

export class ApiSystemRouter {
  constructor(readonly openFileService: (path: SimpleFileOrString) => any) {}

  router() {
    return e
      .Router()
      .get("/api/system/state", wrap("state", this.getState))
      .get("/api/system/hasEmail", wrap("hasEmail", this.getHasEmail))
      .post("/api/system", (req, res) =>
        wrapWithToast({
          response: res,
          f: () => this.postSystem(req, res)
        })
      )
      .post(
        "/api/system/empty-trash",
        requirePlus,
        requireEnabled(Settings.enableEmptyTrash),
        validatePostCounts("deletedAt"),
        postEmptyTrash
      )
      .post(
        "/api/system/remove-assets",
        requirePlus,
        requireEnabled(Settings.enableRemoveAssets),
        validatePostCounts("excludedAt"),
        postRemoveAssets
      )
  }

  readonly getState = (_req: e.Request, res: e.Response) => {
    const state: SystemState = {
      paused: isPaused()
    }
    return res.json(state)
  }

  readonly getHasEmail = (_req: e.Request, res: e.Response) => {
    res.json({ hasEmail: isEmailMaybe(Settings.email.value) })
  }

  readonly postSystem = async (req: e.Request, res: e.Response) => {
    const b = req.body
    const msgs: string[] = []
    logger().info("post()", { body: req.body })

    mapTrue(b.pause, () => {
      void broadcastPause()
      msgs.push("Sync was paused")
    })
    mapTrue(b.resume, () => {
      void broadcastResume()
      msgs.push("Sync was resumed")
    })
    await mapTrue(b.restartSync, async () => {
      StdoutWrite.restartSync()
      msgs.push("Sync restart was requested")
    })
    await mapTrue(b.forceRestartSync, async () => {
      Operation.markOpCompleted(
        { name: "forceRestartSync" },
        { value: "canceled (force-restarted)" }
      )
      Operation.ensurePendingOp({ name: "forceRestartSync" })
      StdoutWrite.restartSync()
      msgs.push("Sync force-restart was requested")
    })
    await mapTrue(b.rebuildLibrary, async () => {
      forceRebuildLibrary()
      StdoutWrite.restartSync()
      msgs.push("Library rebuild was requested")
    })

    mapTrue(b.showSyncReports, () => {
      void this.openFileService(syncReportReadme())
      msgs.push("Opened your sync reports directory")
    })

    const syncDir = toS(b.syncDir)
    if (notBlank(syncDir) && (await exists(syncDir))) {
      void submitTask_("syncDir", { nativePath: syncDir })
    }

    void mapTrue(b.shutdown, async () => {
      const msg = "requested by " + req.ip
      // Don't wait for shutdown!
      void shutdown(msg)
      msgs.push("Shutdown " + msg)
    })
    if (isNotEmpty(msgs)) {
      sendServerToast({
        response: res,
        toast: {
          type: "info",
          text: msgs.join(". ")
        }
      })
    } else {
      res.status(400)
      sendServerToast({
        response: res,
        toast: {
          text: "Nothing requested.",
          type: "warning"
        }
      })
    }
  }
}

async function countAssets(res: e.Response, col: RemovableColumn) {
  return (res.locals["countAssets-" + col] ??= await Asset.dbl.pluckFirst(
    Asset.shownRemovable(col).countDistinct("Asset.id")
  ))
}

async function countAssetFiles(res: e.Response, col: RemovableColumn) {
  return logger().tap({
    msg: `countAssetFiles(${col})`,
    result: (res.locals["countAssetFiles-" + col] ??=
      await Asset.dbl.pluckFirst(
        Asset.shownRemovable(col)
          .join("AssetFile", "Asset.id", "AssetFile.assetId")
          .countDistinct("AssetFile.id")
      ))
  })
}

function validatePostCounts(col: RemovableColumn) {
  return async (req: e.Request, res: e.Response, next: e.NextFunction) => {
    const expectedAssetFileCount = toStr(req.query.expectedAssetFileCount, " ")
    const answer = extractInt(req.body.answer)

    logger().debug("validatePostCounts", { expectedAssetFileCount, answer })

    // We have 3 numbers, and they all have to match:
    // 1: the prior asset file count for the query
    // 2: the current prior asset file count for the query
    // 3: the number the user entered

    const priorCount = toInt(toS(expectedAssetFileCount))
    if (priorCount == null) {
      return sendWarningServerToast({
        response: res,
        toast: {
          text: "Cancelled",
          details: "The expected number of files was missing from the request"
        }
      })
    }
    const userCount = toInt(answer)
    if (userCount == null) {
      return sendWarningServerToast({
        response: res,
        toast: {
          text: "Cancelled",
          details: "User answer was missing"
        }
      })
    }
    const assetFileCount: number = await countAssetFiles(res, col)

    if (priorCount !== assetFileCount) {
      return sendWarningServerToast({
        response: res,
        toast: {
          text: "Cancelled",
          details: `The number of files has changed from ${fmt(
            priorCount
          )} to ${fmt(assetFileCount)}. Reload this page and try again.`
        }
      })
    }

    if (userCount !== priorCount) {
      return sendWarningServerToast({
        response: res,
        toast: {
          text: "Cancelled",
          details: `You entered "${
            answer ?? ""
          }" and we expected "${priorCount}".`
        }
      })
    }

    return next()
  }
}

function columnToOperationName(col: RemovableColumn): OperationName {
  if (col === "deletedAt") return OperationNames.applyDeleted
  else if (col === "excludedAt") return OperationNames.applyExcluded
  throw new Error("invalid RemovableColumn " + col)
}

async function insertOperationAndUnshow(res: e.Response, col: RemovableColumn) {
  const assetCount = await countAssets(res, col)
  const assetFileCount = await countAssetFiles(res, col)
  logger().warn("setting " + col + " to unshown...", {
    assetCount,
    assetFileCount
  })

  const ops = Operation.ops().insertOne({
    name: columnToOperationName(col),
    value: stringify({ assetCount, assetFileCount })
  })
  logger().warn("inserted", { ops })
  const obj = { shown: 0, updatedAt: ops.createdAt }
  ;(obj as any)[col] = ops.createdAt
  const result = Asset.dbl.run(Asset.shownRemovable(col).update(obj))
  logger().warn("updated assets", { result, obj })
  await submitTask_("checkOperations")
}

async function postEmptyTrash(_req: e.Request, response: e.Response) {
  await insertOperationAndUnshow(response, "deletedAt")
  return sendServerToast({
    response,
    reload: true,
    toast: {
      text: "Emptied trash",
      details: "Files will be deleted soon.",
      type: "success"
    }
  })
}

async function postRemoveAssets(_req: e.Request, response: e.Response) {
  await insertOperationAndUnshow(response, "excludedAt")
  return sendServerToast({
    response,
    reload: true,
    toast: {
      text: "Assets have been removed from your library.",
      type: "success"
    }
  })
}
