import { blank } from "./Blank"
import { isExampleDomain } from "./Domains"
import { Maybe } from "./MaybeTypes"
import { toS } from "./toS"

export function isEmailMaybe(s: Maybe<string>): boolean {
  return parseEmail(s) != null
}

const MinimalEmailRE = /^(?<local>.+)@(?<domain>.[^@\n\r.\u2028\u2029]*\..+)$/

export function parseEmail(
  email: Maybe<string>
): Maybe<{ local: string; domain: string }> {
  const re = MinimalEmailRE.exec(toS(email).trim())
  if (re?.groups == null) return
  const { local, domain } = re.groups
  return blank(local) || blank(domain) || isExampleDomain(domain)
    ? undefined
    : { local, domain }
}
