import { MultiMap } from "../../fe/MultiMap"
import { Primitive } from "../../fe/Primitive"
import { Thunk } from "../../fe/Thunk"
import { toS } from "../../fe/toS"
import { maxCpus } from "../work/MaxCpus"
import { Promises } from "./Promises"

export class LastOneInWins<K extends Primitive> {
  readonly p: Promises
  readonly #id2ac = new MultiMap<K, AbortController>()

  constructor(
    readonly name: string,
    readonly maxConcurrency: Thunk<number> = maxCpus
  ) {
    this.p = new Promises(name, maxConcurrency)
  }

  vacuum() {
    this.#id2ac.filterInPlace((_, v) => !v.signal.aborted)
  }

  enqueue<V>(key: K, fn: (ac: AbortController) => Promise<V>) {
    for (const ea of this.#id2ac.get(key) ?? []) {
      ea.abort()
    }
    // vacuum after aborting prior work:
    this.vacuum()
    const ac = new AbortController()
    this.#id2ac.add(key, ac)
    return this.p.serial(toS(key), () => fn(ac))
  }
}
