import { lazy } from "../core/Lazy"
import { mkLogger } from "../core/Logger"
import { StdoutWrite } from "../core/StdoutWrite"
import { ending } from "../core/async/Endable"
import { exit } from "../core/async/Exit"
import { broadcastExit } from "../core/event/BroadcastEvent"
import { setUnrefTimeout } from "../fe/UnrefTimeout"

let shutdownReason = "unspecified"

export function shutdown(reason: string) {
  mkLogger("Shutdown").warn("shutdown()", { reason })
  shutdownReason = reason
  return _shutdown()
}

const _shutdown = lazy(async () => {
  StdoutWrite.shutdown()
  void broadcastExit()
  // TODO: replace the StdoutWrite signals with Events or Tasks?
  // Main should shut us down in a second. If not, we'll shut down ourselves.
  setUnrefTimeout(
    () => (ending() ? undefined : exit({ reason: shutdownReason, status: 0 })),
    3000 // wait for 3 seconds for the main service to tell us to shut down.
  )
})
