import express from "express"
import { Obj } from "../../fe/Object"
import { MaybeSyncOrAsync } from "../../fe/SyncOrAsync"
import { wrap } from "./Handlers"

export function viewRouter({
  path,
  view,
  modeler
}: {
  path: string
  view: string
  modeler?: (
    request: express.Request,
    response: express.Response
  ) => MaybeSyncOrAsync<Obj>
}): express.Router {
  return express.Router().get(
    path,
    wrap(view + "Router", async (request, response) => {
      const model = (await modeler?.(request, response)) ?? {}
      model.nonce = response.locals?.cspNonce
      return response.render(view, model)
    })
  )
}
