export function isImageHashComparison(obj: any): obj is ImageHashComparison {
  return (
    obj != null &&
    isImageHash(obj.a) &&
    isImageHash(obj.b) &&
    [obj.imageCorr, obj.colorCorr, obj.aRotation].every(isNumber)
  )
}

/** @return 1 for full color correlation, 0 for no color correlation */
export function labsCorr2(a: Triplet[], b: Triplet[]) {
  const diffs =
    sum(a, ea => diffCIECorr(ea, closestLab(b, ea))) +
    sum(b, ea => diffCIECorr(ea, closestLab(a, ea)))

  const len = Math.max(a.length, b.length, diffs)

  return (len - diffs) / len
}

const GreyscaleRE = /A{20}=?$/

export function isGreyscaleMeanHash(meanHash: Maybe<string>) {
  // 11 characters to encode 64 bits with base 64 (b64 encodes 6 bits per
  // character)
  return (
    meanHash != null &&
    (within(7, 12, meanHash.length) || meanHash?.match(GreyscaleRE) != null)
  )
}

/**
 * image hashes are produced by b64encodeBits(flatten(l[], a[], b[])): so the
 * lightness mean bits are the first dim^2 bits.
 *
 * @param b64 assumed to have been produced by b64encodeBits(flatten(l[], a[], b[])):
 *
 * @return number[] dim^2 long, ready to be rotated.
 */
function splitbits(
  b64: string,
  greyscale: boolean,
  dim: number = HashDim
): number[] {
  // We may be given a color meanhash but asked to render as greyscale.
  const bits = b64decode(b64)
    .toString(2)
    .split("")
    .map(ea => (ea === "1" ? 1 : 0)) // < binary parseInt

  const dim2 = dim * dim

  // left pad with 0s
  while (bits.length % dim2 !== 0) {
    bits.unshift(0)
  }

  // console.log("splitbits()", {
  //   b64,
  //   decoded: b64decode(b64),
  //   bitsLen: bits.length
  // })

  const only_l_bits = bits.length === dim2
  if (only_l_bits || greyscale) return bits.slice(0, dim2)

  const result = bits.slice(0, dim2).map((ea, idx) => {
    const l = ea
    const a = bits[idx + dim2]
    const b = bits[idx + dim2 * 2]
    return l * 4 + a * 2 + b
  })

  return result
}

export function meanHashVariants(
  a: string,
  greyscale = false,
  dim: number = HashDim
) {
  greyscale = greyscale || a.match(GreyscaleRE) != null
  const suffix = greyscale ? padding("0", 2 * dim * dim) : ""
  const m_a = splitbits(a, greyscale, dim)
  return Rotations.map(deg =>
    b64encode(
      BigInt(
        "0b0" +
          rotateSquareMatrix(m_a, deg)
            .map(ea => ea.toString(2))
            .join("") +
          suffix
      )
    )
  )
}

/**
 * @param a is a b64-encoded set of ModeBits^2 triple-bits (one bit for L*,
 * one for a*, one for b*).
 * @return bRotation is the number of degrees that `b` needs to be rotated to
 * match `a`.
 */
export function hammRatio({
  a,
  b,
  greyscale,
  dim = HashDim,
  rotationsToCheck
}: {
  a: Maybe<string>
  b: Maybe<string>
  greyscale: boolean
  dim?: number
  rotationsToCheck: MaybeReadonlyArray<Rotation>
}): { hammRatio: number; bRotation: number } {
  if (blank(a) || blank(b)) return { hammRatio: 0, bRotation: 0 }
  if (a === b) return { hammRatio: 1, bRotation: 0 }

  const m_a = splitbits(a, greyscale, dim)
  const m_b = splitbits(b, greyscale, dim)

  if (isEmpty(rotationsToCheck)) rotationsToCheck = [0]

  const results = rotationsToCheck.map(bRotation => ({
    bRotation,
    hammRatio: sigFigs(
      hammRatioIntArrays(
        m_a,
        rotateSquareMatrix(m_b, bRotation),
        greyscale ? 1 : 3
      ),
      2
    )
  }))

  logger().debug("hammRatio()", { results, rotationsToCheck })

  // If quality scores are similar, prefer no rotation, or 90 degrees left or
  // right:
  const result = greatestBy(results, ea => [
    sigFigs(ea.hammRatio, 1), // < hamming distance is only directionally accurate
    -Rotations.indexOf(ea.bRotation as any)
  ])!
  return {
    bRotation: result.bRotation,
    hammRatio: sigFigs(result.hammRatio, 2)
  }
}

export async function isSimilarImage(
  a: PosixFile,
  b: PosixFile,
  level: LogLevel = "debug",
  opts: ComparisonOptions = {}
): PromiseMaybe<boolean> {
  return logger().tap({
    msg: `isSimilarImage(${a},${b})`,
    level,
    result: isSimilarImageHash(await imageHash(a), await imageHash(b), opts)
  })
}

export interface ComparisonOptions {
  capturedAtIsFuzzy?: boolean
  forceColor?: boolean
  forceGreyscale?: boolean
  minImageCorr?: number
  minColorCorr?: number
  rotationsToCheck?: MaybeReadonlyArray<Rotation>
}

export function compareImageHashes(
  a_: Maybe<ImageHashish>,
  b_: Maybe<ImageHashish>,
  opts: ComparisonOptions = {}
) {
  // toImageHash will return null if the object is invalid (like if dominantColors is empty or meanHash is blank)
  const a = toImageHash(a_)
  const b = toImageHash(b_)
  if (a == null || b == null) return

  const capturedAtIsFuzzy = isTrue(opts.capturedAtIsFuzzy)
  const isGreyscale = isTrue(opts.forceColor)
    ? false
    : isTrue(opts.forceGreyscale) ||
      (a.isGreyscale ?? isGreyscaleMeanHash(a.meanHash)) ||
      (b.isGreyscale ?? isGreyscaleMeanHash(b.meanHash))

  const meanHashCorr = hammRatio({
    a: a.meanHash,
    b: b.meanHash,
    greyscale: isGreyscale,
    rotationsToCheck: opts.rotationsToCheck ?? validRotations(a, b)
  })

  const diffHashCorr = hammRatio({
    a: a.diffHash,
    b: b.diffHash,
    greyscale: isGreyscale,
    rotationsToCheck: [] // < dhash bits can't be rotated
  })

  const dctHashCorr = hammRatio({
    a: a.dctHash,
    b: b.dctHash,
    greyscale: isGreyscale, // dcthash is always greyscale
    rotationsToCheck: [] // < dcthash bits can't be rotated
  })

  const colorCorr = dominantColorCorrelation(a.dominantColors, b.dominantColors)

  if (colorCorr == null) {
    logger().debug("imageHashCompare(): missing color correlation")
    return
  }

  const mimeMatches = a.mimetype === b.mimetype

  const aAspectRatio = aspectRatio(a)
  const bAspectRatio = aspectRatio(maybeDimSwap(b, meanHashCorr.bRotation))

  // Only reject image hashes if they both include an aspect ratio, and they aren't rotated.
  const aspectRatioMismatches =
    aAspectRatio != null &&
    bAspectRatio != null &&
    !isCloseAspectRatio(aAspectRatio, bAspectRatio)

  let imageCoeffDelta = 0
  let colorCoeffDelta = 0

  if (capturedAtIsFuzzy) {
    colorCoeffDelta += Settings.imageHashFuzzyDateDelta.valueOrDefault
    imageCoeffDelta += Settings.imageHashFuzzyDateDelta.valueOrDefault
  }
  if (!mimeMatches) {
    colorCoeffDelta += Settings.imageHashDifferentMimetypesDelta.valueOrDefault
    imageCoeffDelta += Settings.imageHashDifferentMimetypesDelta.valueOrDefault
  }
  if (isGreyscale) {
    imageCoeffDelta += Settings.imageHashGreyscaleDelta.valueOrDefault
    colorCoeffDelta += Settings.imageHashGreyscaleDelta.valueOrDefault
  }

  const minImageCorr = clamp01(
    (imageCoeffDelta +
      (opts.minImageCorr ?? Settings.minImageCoeffPct.valueOrDefault)) /
      100
  )

  const minColorCorr = clamp01(
    (colorCoeffDelta +
      (opts.minColorCorr ?? Settings.minColorCoeffPct.valueOrDefault)) /
      100
  )

  const why: string[] = []

  if (aspectRatioMismatches) {
    why.push(
      `aspect ratio mismatch (${fmtAspectRatio(
        aAspectRatio
      )} vs ${fmtAspectRatio(bAspectRatio)})`
    )
  }

  const failingImageHashes: string[] = []
  const hashAvg = new Average()
  hashAvg.push(meanHashCorr.hammRatio)

  if (meanHashCorr.hammRatio < minImageCorr) {
    failingImageHashes.push("mean")
  }

  // We can't use these hashes if there's rotation involved. *tear*
  const onlyMeanHash = meanHashCorr.bRotation !== 0
  if (!onlyMeanHash) {
    map(diffHashCorr?.hammRatio, ea => hashAvg.push(ea))
    map(dctHashCorr?.hammRatio, ea => hashAvg.push(ea))

    if (lt(diffHashCorr?.hammRatio, minImageCorr)) {
      failingImageHashes.push("gradient")
    }
    if (lt(dctHashCorr?.hammRatio, minImageCorr)) {
      failingImageHashes.push("DCT")
    }
  }

  if (failingImageHashes.length > 0) {
    why.push("different image content (" + andList(failingImageHashes) + ")")
  }

  const colorPass = colorCorr >= minColorCorr
  // If any of the hashes are quite sure they are different, they will pull
  // down the average to reject the match.
  const isSimilar = colorPass && hashAvg.avg! >= minImageCorr

  if (!colorPass) {
    why.push("different dominant colors")
  }

  const result = {
    meanHashCorr: sigFigs(meanHashCorr.hammRatio, 2),
    diffHashCorr: onlyMeanHash
      ? undefined
      : map(diffHashCorr, ea => sigFigs(ea.hammRatio, 2)),
    dctHashCorr: onlyMeanHash
      ? undefined
      : map(dctHashCorr, ea => sigFigs(ea.hammRatio, 2)),
    hashCorrAvg: hashAvg.avg!,
    minImageCorr,
    imageCoeffDelta,
    colorCorr: sigFigs(colorCorr, 2),
    minColorCorr,
    colorCoeffDelta,
    isGreyscale,
    bRotation: meanHashCorr.bRotation,
    aAspectRatio,
    bAspectRatio,
    isSimilar,
    whyNotSimilar: isSimilar ? undefined : andList(why)
  }

  logger().info("compareImageHashes", {
    a: pickCompact(a as any, "uri", "id", "assetId"),
    b: pickCompact(b as any, "uri", "id", "assetId"),
    ...result
  })

  return {
    a,
    b,
    ...result
  }
}

export async function isVerySimilarImage(
  a: PosixFile,
  b: PosixFile,
  level: LogLevel = "debug",
  opts: ComparisonOptions = {}
): PromiseMaybe<boolean> {
  return isSimilarImage(a, b, level, {
    rotationsToCheck: [0],
    ...opts,
    minImageCorr: 0.92,
    minColorCorr: 0.92
  })
}

export function isSimilarImageHash(
  a: Maybe<ImageHashish>,
  b: Maybe<ImageHashish>,
  opts?: ComparisonOptions
): boolean {
  return compareImageHashes(a, b, opts)?.isSimilar ?? false
}

function clamp01(i: number): number {
  return sigFigs(clamp(0, 1, i), 3)
}

export function isVerySimilarImageHash(
  a: Maybe<ImageHashish>,
  b: Maybe<ImageHashish>,
  opts: ComparisonOptions = {}
): boolean {
  return isVerySimilarComparison(compareImageHashes(a, b, opts))
}

export function isVerySimilarComparison(
  c: Maybe<ImageHashComparison>
): boolean {
  if (c == null) return false
  return gte(c.meanHashCorr, 0.9) && gte(c.colorCorr, 0.9)
}

/**
 * @return 1 for perfect match, < .6 for different images
 */
export function imageHashSimilarity(
  a: Maybe<ImageHashish>,
  b: Maybe<ImageHashish>
): Maybe<number> {
  return map(compareImageHashes(a, b), c => avg([c.meanHashCorr, c.colorCorr]))
}
