import { lazy } from "../../core/Lazy"
import { compact, isEmpty, sum } from "../../fe/Array"
import { clamp } from "../../fe/Number"
import { leastBy } from "../Array"
import { Point, lerp2d } from "../math/Lerp"
import { avg } from "../math/Vector"
import { ciede2000_delta_e } from "./DeltaE"
import { DominantColor } from "./DominantColor"

export function dominantColorCorrelation(
  a: DominantColor[],
  b: DominantColor[]
): number {
  a = compact(a)
  b = compact(b)
  if (isEmpty(a) || isEmpty(b)) return 0
  return avg([_impl(a, b), _impl(b, a)])!
}

function _impl(a: DominantColor[], b: DominantColor[]): number {
  const totalCoverage = sum(a, ea => clamp(0, 100, ea.pct))
  const totalMatch = sum(a, ea => _match(ea, b))
  const result = totalMatch / totalCoverage
  // console.log("_impl()", { result, totalCoverage, totalMatch })
  return result
}

/**
 * For the given color in a, find the closest match in b
 */
function _match(a: DominantColor, b: DominantColor[]): number {
  const nearest = leastBy(b, ea => ciede2000_delta_e(a.lab, ea.lab))
  if (nearest == null) return 0
  const delta_e = ciede2000_delta_e(a.lab, nearest.lab)
  const pct = clamp(0, a.pct, avg([a.pct, nearest.pct])!)
  const scaled = scaled_delta_e(delta_e)
  const result = clamp(0, 100, pct) * scaled
  // console.log("_match()", {
  //   name: a.color?.name,
  //   rgb: a.rgbhex,
  //   result,
  //   nearest: nearest.rgbhex,
  //   pct,
  //   scaled,
  //   delta_e
  // })
  return result
}

const DeltaEMatchPoints = lazy<Point[]>(() => {
  return [
    { x: 0, y: 1 },
    { x: 9, y: 0.8 }, // < still pretty good, you get a B
    { x: 16, y: 0.3 }, // < oof those are pretty different, you get an F
    { x: 20, y: 0 } // delta_e of 20 or 30 are different colors. No credit.
  ]
})

export function scaled_delta_e(delta_e: number): number {
  return clamp(0, 1, lerp2d(delta_e, ...DeltaEMatchPoints()))
}
