import { IconName } from "../IconName"
import { strEnum, StrEnumKeys } from "../StrEnum"
import { toS } from "../toS"
import { AssetId } from "./Asset"
import { ServerToast } from "./ServerToast"

export const AssetQueryOrderByColumns = strEnum("capturedAt", "updatedAt")
export type AssetQueryOrderByColumn = StrEnumKeys<
  typeof AssetQueryOrderByColumns
>

export interface SearchQuery {
  q: string
  normalized: string
  orderBy: AssetQueryOrderByColumn
  asc: boolean
  offset: number
  nextOffset?: number // < opaque string used by router for paging
}

export interface SearchBanner {
  /** text rendered on the banner */
  description: string
  /** banner and modal icon */
  buttonIcon?: IconName
  /** banner and modal "OK" button text */
  buttonText?: string
  /** banner and modal "OK" button class */
  buttonClass?: string
  /** banner button alt/title */
  buttonTitle?: string | null
  /** modal title */
  promptTitle?: string | null
  /** modal text */
  prompt?: string | null
  /** modal input placeholder */
  promptPlaceholder?: string | null
  /** URL to post to if prompt is OK'ed */
  formAction?: string | null
  /** should this view be refreshed on re-activation (like trash or removed views?) */
  refreshOnActivation?: boolean
}

export interface SearchResults {
  query: SearchQuery
  banner?: SearchBanner
  assetIds: AssetId[]
  // this would be neato:
  // tagIds: ApiTag[]
  toast?: ServerToast
}

export function maybeQuote(s: string): string {
  s = toS(s).trim()
  return s.match(/\s/) != null ? `"${s}"` : s
}
