import { NextFunction, Request, RequestHandler, Response } from "express"
import { mapNotBlank } from "../../fe/Blank"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { toMap } from "../Map"
import { stripSuffix } from "../String"
import { FatalErrorFlag } from "../error/ErrorFlags"
import { BaseFile } from "../fs/BaseFile"
import { extMimetype } from "../tags/FileTypes"
import { wrap } from "./Handlers"

const nopeRE = /\.asar\/?/i

// HEY FUTURE ME: `brotli --best` did no better for the fonts and images
// directories than `gzip -9` :P

export async function serveStaticGz(
  rootPath: string | BaseFile
): Promise<RequestHandler> {
  const logger = mkLogger("serveStaticGz")
  const root = BaseFile.for(rootPath)
  const files = await root.descendants(ea => ea.ext.endsWith(".gz"))
  const path2file = toMap(files, ea =>
    mapNotBlank(extMimetype(stripSuffix(ea.ext, ".gz")), contentType => [
      "/" + stripSuffix(ea.posixPathFrom(root), ".gz"),
      { contentType, nativePath: ea.nativePath }
    ])
  )

  logger.debug("ready to serve", [...path2file.keys()])

  if (path2file.size === 0) {
    logger.error(
      "Can't read " +
        root +
        ". Please visit https://forum.photostructure.com/ for help." +
        FatalErrorFlag
    )
  }

  return wrap(
    "serveStaticGz",
    async (req: Request, res: Response, next: NextFunction) => {
      // This fixes devtool from crashing when not packaged: (!!):
      if (null != req.path.match(nopeRE)) {
        return res.sendStatus(404)
      }
      const result = path2file.get(toS(req.path))
      if (result == null) {
        return next()
      }
      logger.debug("get", {
        path: req.path,
        result
      })
      res.set("Content-Encoding", "gzip")
      res.set("Content-Type", result.contentType)
      // deepcode ignore PT: <we only serve known files from path2file>
      return res.sendFile(result.nativePath)
    }
  )
}
