import e from "express"
import { getSeedOrRedirect, wrap } from "../../core/express/Handlers"
import { requestedUri } from "../../core/express/RequestMeta"
import { getAssetOpenGraph } from "./AssetOpenGraph"
import { render } from "./PugHelpers"

export class SpaRouter {
  router(): e.Router {
    const r = wrap("SpaRouter", this.get.bind(this))
    // TODO: change this to /tag/*
    return e
      .Router()
      .get("/tag*", r)
      .get("/asset/:assetId", wrap("SpaRouter.Asset", this.getAsset.bind(this)))
      .get("/search", r)
  }

  async getAsset(req: e.Request, res: e.Response, _next: e.NextFunction) {
    const meta = await getAssetOpenGraph(requestedUri(req), req.params.assetId)
    return render(req, res, "app", meta == null ? {} : { meta })
  }

  get(req: e.Request, res: e.Response, _next: e.NextFunction) {
    // TODO: change this to /tag/*
    // Don't add a seed to /search or other paths:
    if (req.path?.startsWith("/tag") && null == getSeedOrRedirect(req, res)) {
      return
    }
    return render(req, res, "app", {})
  }
}
