import { SimpleParser } from "batch-cluster"
import { spawn } from "child_process"
import { lazy } from "../../core/Lazy"
import { isNotEmpty } from "../../fe/Array"
import { mkLogger } from "../Logger"
import { isLinux, isMac, isWin } from "../platform/Platform"
import { PowerShell } from "../pwsh/PowerShell"
import { Settings } from "../settings/Settings"
import { PosixFile } from "./PosixFile"

const logger = lazy(() => mkLogger("fs.ShowFileInFolder"))

export async function showFileInFolder(file: PosixFile) {
  if (await file.isEmpty()) return false

  if (Settings.showFileInFolderUsesThunar.valueOrDefault && isLinux) {
    return thunarSelect(file)
  }

  if (isNotEmpty(Settings.showFileInFolderCommand.values)) {
    const path = Settings.showFileInFolderUsesFileUri.valueOrDefault
      ? file.fileuri()
      : file.nativePath
    const args = [...Settings.showFileInFolderCommand.values, path]
    const cmd = args.shift()!
    logger().info("custom showFileInFolderCommand: running", { cmd, args })
    return run(cmd, args)
  }

  return isLinux
    ? nautilusSelect(file)
    : isWin
    ? explorerSelect(file)
    : isMac
    ? openSelect(file)
    : false
}

async function run(cmd: string, args: string[]) {
  try {
    // we spawn here and unref() to let the process continue indefinitely. If
    // not, the finder/explorer/gnome window may time out and disappear after
    // a while.
    spawn(cmd, args, {
      detached: true,
      stdio: "ignore"
    }).unref()
  } catch (err) {
    logger().warn("spawn() failed", { err, cmd, args })
  }
}

async function explorerSelect(file: PosixFile) {
  const path = file.nativePath
    // Double-quotes are not allowed in windows paths, but just to be safe:
    .replace(/"/g, "")
    // PowerShell supports escaping single quotes with two single quotes.
    // See https://docs.microsoft.com/en-us/powershell/module/microsoft.powershell.core/about/about_quoting_rules
    .replace(/'/g, "''")
  try {
    await PowerShell.instance().execute(
      `start explorer.exe -ArgumentList '/select,"${path}"'`,
      SimpleParser
    )
    return true
  } catch (err) {
    logger().warn("explorerSelect(" + file + ") failed", err)
    return false
  }
}

async function nautilusSelect(file: PosixFile) {
  return run("nautilus", ["-s", file.fileuri()])
}

async function thunarSelect(file: PosixFile) {
  return run("dbus-send", [
    "--type=method_call",
    "--dest=org.xfce.Thunar",
    "/org/xfce/FileManager org.xfce.FileManager.DisplayFolderAndSelect",
    `string:"${file.dir}"`,
    `string:"${file.base}"`,
    `string:""`,
    `string:""`
  ])
}

async function openSelect(file: PosixFile) {
  return run("open", ["-R", file.nativePath])
}
