import e from "express"
import { ShortCommandTimeoutMs } from "../../core/ShortCommandTimeoutMs"
import { wbrPath } from "../../core/String"
import { fmtMs } from "../../core/date/Date"
import { wrap } from "../../core/express/Handlers"
import { b, t } from "../../core/licensing/Licensing"
import { Settings } from "../../core/settings/Settings"
import { volsha } from "../../core/uri/volsha"
import { volumes } from "../../core/volumes/Volumes"
import { isTrue } from "../../fe/Boolean"
import { fmtIsoDate, secondMs } from "../../fe/Date"
import { fmtDuration } from "../../fe/FmtDuration"
import { map } from "../../fe/Maybe"
import { gt0, lt } from "../../fe/Number"
import { thenMap } from "../../fe/Promise"
import { Timeout, thenOrTimeout, thenOrTimeoutAs } from "../../fe/ThenOrTimeout"
import { GB, fmtBytes } from "../../fe/Units"
import { systemInformation } from "../SystemInfo"
import { Progress } from "../model/Progress"
import { syncPathsForUI } from "../sync/SyncPaths"
import { render } from "./PugHelpers"

export class AboutRouter {
  router(): e.Router {
    return e.Router().get("/about", wrap("AboutRouter", this.about.bind(this)))
  }
  async about(req: e.Request, res: e.Response, _next: e.NextFunction) {
    const timeoutMs = ShortCommandTimeoutMs + secondMs
    const systemInfo = await thenOrTimeoutAs({
      f: systemInformation(),
      timeoutMs,
      as: [
        {
          term: "System information",
          defn:
            "Failed to fetch system information: timeout after " +
            fmtMs(timeoutMs),
          defnClass: "fail"
        }
      ]
    })
    const vols = await thenOrTimeout(
      thenMap(volumes(), arr =>
        arr
          .map(vol => ({
            ignorable: vol.ignorable,
            mountpoint: vol.mountpoint,
            uuid: vol.uuid,
            volsha: map(vol.uuid, volsha),
            size: fmtBytes(vol.size, 2),
            free: fmtBytes(vol.available, 2),
            full:
              gt0(Settings.minDiskFreeGb.valueOrDefault) &&
              lt(vol.available, Settings.minDiskFreeGb.valueOrDefault * GB),
            label: vol.label,
            remote:
              true === vol.remote
                ? {
                    remote: vol.remote,
                    host: vol.remoteHost,
                    share: vol.remoteShare
                  }
                : undefined
          }))
          .filter(ea => ea.ignorable !== true)
      ),
      timeoutMs
    )
    if (vols === Timeout) {
      systemInfo.push({
        term: "volumes() timeout",
        defn:
          "Failed to fetch volume metadata: timeout after " + fmtMs(timeoutMs),
        defnClass: "fail"
      })
    }
    const syncPaths =
      Progress.db() == null
        ? undefined
        : await thenOrTimeout(syncPathsForUI(), ShortCommandTimeoutMs)

    if (syncPaths === Timeout) {
      systemInfo.push({
        term: "sync paths timeout",
        defn:
          "Failed to fetch sync path metadata: timeout after " +
          fmtMs(ShortCommandTimeoutMs),
        defnClass: "fail"
      })
    }

    const best = await b()

    return render(req, res, "about", {
      title: "About PhotoStructure",
      systemInfo,
      volumes: Array.isArray(vols)
        ? vols.map(vol => ({
            ...vol,
            mountpoint: wbrPath(vol.mountpoint)
          }))
        : undefined,
      syncPaths: Array.isArray(syncPaths) ? syncPaths : undefined,
      subTier: await t(),
      subTrial: isTrue(best?.l?.trial),
      subEmail: best?.l?.sub,
      subExpiresInDuration: map(best?.l?.exp, exp =>
        fmtDuration(exp.getTime() - Date.now(), 1)
      ),
      subExpiresAt: map(best?.l?.exp, fmtIsoDate)
    })
  }
}
