import e from "express"
import { Knex } from "knex"
import { mkLogger } from "../../core/Logger"
import { wrap } from "../../core/express/Handlers"
import { requestedUri } from "../../core/express/RequestMeta"
import { Settings } from "../../core/settings/Settings"
import { mountpointToVolsha } from "../../core/volumes/Volumes"
import { compact } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { Maybe } from "../../fe/MaybeTypes"
import { toGt0, toInt } from "../../fe/Number"
import { URLSearchParamable } from "../../fe/URLSearchParams"
import { fmt, plur } from "../../fe/Units"
import {
  AssetQueryOrderByColumns,
  SearchBanner,
  SearchResults
} from "../../fe/api/AssetQuery"
import { toS, toStr } from "../../fe/toS"
import { Asset } from "../model/Asset"
import { toAssetId } from "../model/AssetId"
import {
  NormieQuerySymbols,
  Query,
  findTerm,
  flatTerms,
  isTrueTerm,
  queryToString
} from "../query/Query"
import { AssetTermBoolNs, normalizeQuery } from "../query/QueryNormalizer"
import { parseQuery_ } from "../query/QueryParser"
import { defaultAssetQuery, queryToSql } from "../query/QueryToSql"

const logger = mkLogger("web.ApiSearchRouter")

export class ApiSearchRouter {
  router(): e.Router {
    return e
      .Router()
      .get("/api/search", wrap("ApiSearchRouter.get", this.get.bind(this)))
  }

  async get(req: e.Request, res: e.Response) {
    const q = toStr(req.query.q, " ")
    const orderbyToken = AssetQueryOrderByColumns.validOrElse(
      toStr(req.query.orderby, ","),
      AssetQueryOrderByColumns.capturedAt
    )
    const asc = isTrue(req.query.asc)
    const limit = 512
    const orderBy = {
      column:
        orderbyToken === AssetQueryOrderByColumns.capturedAt
          ? "Asset.capturedAtLocal"
          : "Asset.updatedAt",
      order: asc ? "asc" : "desc"
    }
    const offset = toGt0(toStr(req.query.offset))

    logger.debug("get()", { q, orderBy, offset })
    if (blank(q)) {
      return res.json({})
    }

    const query = normalizeQuery(parseQuery_(q))

    let qb = defaultAssetQuery()
      .select({
        id: "Asset.id",
        capturedAtLocal: "Asset.capturedAtLocal",
        updateCount: "Asset.updateCount",
        durationMs: "Asset.durationMs"
      })
      .orderBy([orderBy, "Asset.id"])
      .limit(limit)

    const hasFs = findTerm(query, t => t.ns === "fs")
    const mp2vs = hasFs ? await mountpointToVolsha() : undefined

    if (offset != null) {
      qb = qb.offset(offset)
    }

    const banner =
      offset == null ? await mkBanner(req, query, mp2vs) : undefined

    qb = queryToSql(query, qb, mp2vs).distinct()
    const assetIds = compact(Asset.dbl.all(qb).map(toAssetId))
    const result: SearchResults = {
      query: {
        q,
        normalized: queryToString(query, NormieQuerySymbols),
        orderBy: orderbyToken,
        asc,
        offset: offset ?? 0,
        nextOffset:
          assetIds.length < limit ? undefined : assetIds.length + (offset ?? 0)
      },
      banner,
      assetIds
    }
    logger.debug(".get(" + queryToString(query) + ") result", result)
    return res.json(result)
  }

  validatePostRequest(req: e.Request, qf: () => Knex.QueryBuilder) {
    logger.debug("validatePostRequest", {
      req_query: req.query,
      req_body: req.body
    })
    return this.validatePost(
      toStr(req.query.expectedAssetFileCount, " "),
      req.body.answer,
      qf
    )
  }

  async validatePost(
    expectedAssetFileCount: string | undefined,
    answer: any,
    qf: () => Knex.QueryBuilder
  ) {
    logger.debug("validatePost", { expectedAssetFileCount, answer })

    // We have 3 numbers, and they all have to match:
    // 1: the prior asset file count for the query
    // 2: the current prior asset file count for the query
    // 3: the number the user entered

    const priorCount = toInt(toS(expectedAssetFileCount))
    if (priorCount == null) {
      return `Cancelled: the expected number of files was missing from the request.`
    }
    const userCount = toInt(answer)
    if (userCount == null) {
      return `Cancelled: missing user input.`
    }
    const assetFileCount: number = await Asset.dbl.pluckFirst(
      qf()
        .join("AssetFile", "Asset.id", "AssetFile.assetId")
        .countDistinct("AssetFile.id")
    )

    if (priorCount !== assetFileCount) {
      return `Cancelled: the number of files has changed from ${fmt(
        priorCount
      )} to ${fmt(assetFileCount)}. Reload this page and try again.`
    }

    if (userCount !== priorCount) {
      return `Cancelled: you entered "${
        answer ?? ""
      }" and we expected "${priorCount}".`
    }

    return { assetFileCount }
  }
}

function categorizeQuery(query: Query) {
  const onlyOneTerm = flatTerms(query).length === 1
  const emptyTrashQuery =
    onlyOneTerm &&
    isTrueTerm(findTerm(query, t => t.ns === AssetTermBoolNs.deleted))
  const removeAssetsQuery =
    !emptyTrashQuery &&
    onlyOneTerm &&
    isTrueTerm(findTerm(query, t => t.ns === AssetTermBoolNs.excluded))

  return { emptyTrashQuery, removeAssetsQuery }
}

async function mkBanner(
  req: e.Request,
  query: Query,
  mp2vs: Maybe<Map<string, string>>
): Promise<SearchBanner> {
  const assetCount: number = await getAssetCount(query, mp2vs)
  const { emptyTrashQuery, removeAssetsQuery } = categorizeQuery(query)

  if (assetCount === 0) {
    return {
      description: emptyTrashQuery
        ? "No assets are marked for trash."
        : removeAssetsQuery
        ? "No assets are marked for removal."
        : "No matching assets."
    }
  }

  const assetCountDesc = "Found " + plur(assetCount, "asset") + ""

  if (!emptyTrashQuery && !removeAssetsQuery) {
    return { description: assetCountDesc }
  }

  // count the number of deletable assets:
  const assetFileCount: number = await getAssetFileCount(query, mp2vs)

  const description = assetCountDesc + " (" + plur(assetFileCount, "file") + ")"

  const formQuery: URLSearchParamable = {
    expectedAssetFileCount: String(assetFileCount)
  }

  if (emptyTrashQuery) {
    const txt = "Empty trash"
    return {
      description,
      buttonText: txt,
      buttonIcon: "trash_forever",
      promptTitle: txt,
      refreshOnActivation: true,
      ...(Settings.enableEmptyTrash.valueOrDefault
        ? {
            buttonClass: "error",
            buttonTitle: null,
            prompt: [
              `<p>Are you sure you want to <b>permanently delete</b> ${plur(
                assetFileCount,
                "file"
              )} comprising ${plur(assetCount, "asset")}?</p>`,
              "<ul>",
              "  <li><b>This cannot be undone.</b></li>",
              "  <li>Files on disks that are not currently mounted will not be deleted (now or in the future).</li>",
              `  <li> See <a href="https://photostructure.com/faq/archive-remove-delete/#delete" target="_blank">photostructure.com/faq/archive-remove-delete</a> for more details.</li>`,
              "</ul>",
              "<p>To proceed, please enter the number of files that will be deleted:</p>"
            ].join("\n"),
            // promptPlaceholder: "Number of files",
            formAction: requestedUri(req)
              .with({ path: "/api/system/empty-trash", query: formQuery })
              .toString()
          }
        : {
            buttonClass: "disabled",
            buttonTitle: `This is disabled. See the "enableEmptyTrash" setting.`,
            prompt: null,
            formAction: null
          })
    }
  } else if (removeAssetsQuery) {
    const txt = "Remove assets"
    return {
      description,
      buttonText: txt,
      buttonIcon: "warning",
      promptTitle: txt,
      refreshOnActivation: true,
      ...(Settings.enableRemoveAssets.valueOrDefault
        ? {
            buttonClass: "warning",
            buttonTitle: null,
            prompt: [
              "<p>Are you sure you want to remove " +
                plur(assetCount, "asset") +
                " (comprised of " +
                plur(assetFileCount, "file") +
                ") from your library?</p>",
              "<ul>",
              "  <li>No files will be deleted from disk.</li>",
              "  <li>These files will be blocked from being re-imported later.</li>",
              `  <li> See <a href="https://photostructure.com/faq/archive-remove-delete/#remove" target="_blank">photostructure.com/faq/archive-remove-delete</a> for more details.</li>`,
              "</ul>",
              "<p>To proceed, please enter the number of files that will be removed:</p>"
            ].join("\n"),
            // promptPlaceholder: "Number of files",
            formAction: requestedUri(req)
              .with({ path: "/api/system/remove-assets", query: formQuery })
              .toString()
          }
        : {
            buttonClass: "disabled",
            buttonTitle: `This is disabled. See the "enableRemoveAssets" setting.`,
            prompt: null,
            formAction: null
          })
    }
  } else {
    throw new Error("internal error (mkBanner)")
  }
}

function getAssetCount(
  query: Query,
  mp2vs: Maybe<Map<string, string>>
): Promise<number> {
  return Asset.dbl.pluckFirst(
    queryToSql(query, defaultAssetQuery().countDistinct("Asset.id"), mp2vs)
  )
}

function getAssetFileCount(
  query: Query,
  mp2vs: Maybe<Map<string, string>>
): Promise<number> {
  return Asset.dbl.pluckFirst(
    queryToSql(
      query,
      defaultAssetQuery()
        .join("AssetFile", "AssetFile.assetId", "Asset.id")
        .countDistinct("AssetFile.id"),
      mp2vs
    )
  )
}
