import e from "express"
import { sep } from "path"
import sharp from "sharp"
import { leastBy } from "../../core/Array"
import { mkLogger } from "../../core/Logger"
import { ShortCommandTimeoutMs } from "../../core/ShortCommandTimeoutMs"
import { ensureSuffixIgnoreCase, splitEvery } from "../../core/String"
import { stringShaToBuffer } from "../../core/StringHash"
import { wrap } from "../../core/express/Handlers"
import { PosixFile } from "../../core/fs/PosixFile"
import { imageCacheDir_ } from "../../core/img/ImageCache"
import { Previews } from "../../core/img/Previews"
import { Settings } from "../../core/settings/Settings"
import {
  isImageMimeType,
  isMimeTypeSupportedByBrowser
} from "../../core/tags/MimeTypes"
import { uriToBasename } from "../../core/uri/URI"
import { HttpStatus } from "../../core/web/HttpStatus"
import { ReducerNames } from "../../fe/ImageReducers"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt, gt0, toInt } from "../../fe/Number"
import { toS } from "../../fe/toS"
import { AssetFile } from "../model/AssetFile"

const logger = mkLogger("web.ImgRouter")

export class ImgRouter {
  constructor(readonly previews: Previews) {}

  router(): e.Router {
    const handler = wrap("ImgRouter", this.streamImage.bind(this))
    // Keep in sync with AssetUrls:
    return e.Router().get("/img/:assetId/:reducer/:width", handler)
  }

  async streamImage(
    req: e.Request,
    res: e.Response,
    _next: e.NextFunction
  ): Promise<any> {
    const result = await this.getFileFromRequest(req, res)
    if (result != null) {
      if ("setHeader" in res && gt(result.width, 500)) {
        // don't bother to figure out the basename for thumbnails
        const shownUri =
          AssetFile.dbl.pluckFirstf<string>(q =>
            q.where({ assetId: result.assetId, shown: 1 }).select("uri")
          ) ?? "/asset-" + result.assetId + "." + result.file.ext
        const basename = map(shownUri, uriToBasename)
        logger.debug("streamImage(): AssetFile URI", { shownUri, basename })
        if (basename != null) {
          try {
            res.setHeader(
              "X-basename",
              ensureSuffixIgnoreCase(basename, "." + result.file.ext)
            )
          } catch (error) {
            logger.warn("streamImage(): setHeader error", { error })
          }
        } else {
          logger.warn("streamImage(): failed to extract basename", { result })
        }
      }
      // file deepcode ignore PT: <the filename is sanitized properly by ap.fileForWidth>
      return res.sendFile(result.file.nativePath)
    }
    return
  }

  async getFileFromRequest(
    req: e.Request,
    res: e.Response
  ): Promise<Maybe<{ assetId: number; width?: number; file: PosixFile }>> {
    const params = req.params

    const assetId = toInt(params.assetId)
    const reducer = ReducerNames.getCI(params.reducer) ?? "fit"
    const width = toInt(params.width)
    if (assetId == null || (reducer == null && width == null)) {
      logger.warn("getFileFromRequest(): bad request", {
        assetId,
        reducer,
        width
      })
      res.sendStatus(HttpStatus.BadRequest)
      return
    }
    const ap = this.previews.ap(assetId)
    const f = ap.fileForWidth(reducer!, width!)
    if (await f.exists()) {
      return { assetId, width, file: f }
    }

    if (Settings.placeholderThumbs.valueOrDefault) {
      // stable and well-distributed hex for an asset id:
      const hex = stringShaToBuffer(toS(assetId)).toString("hex").slice(0, 6)
      const dir = await imageCacheDir_()
      if (dir == null) return
      const file = dir.join(splitEvery(hex, 3).join(sep) + "-8w.png")
      await file.applyIfEmpty_({
        fn_: ea =>
          sharp({
            create: {
              width: 8,
              height: 8,
              channels: 3,
              background: "#" + hex
            }
          })
            .png()
            .toFile(ea.nativePath),
        timeoutMs: ShortCommandTimeoutMs
      })
      return { assetId, width: 8, file }
    }

    // Try the nearest size to the one requested
    const widths = (await ap.widths(reducer!)) ?? []
    const info = await ap.readInfo()
    const origWidth =
      isImageMimeType(info?.mimetype) &&
      isMimeTypeSupportedByBrowser(info?.mimetype)
        ? info?.width
        : undefined

    const existingWidth = leastBy(
      [...widths, origWidth],
      ea => gt0(ea) && Math.abs(width! - ea)
    )

    if (existingWidth == null || existingWidth === width) {
      logger.warn("Missing file, and no other widths found", {
        path: f.nativePath,
        assetId,
        reducer,
        requestedwidth: width,
        existingWidth
      })
      res.sendStatus(HttpStatus.NotFound)
      return
    }

    if (existingWidth === origWidth) {
      logger.debug("redirecting to image actual", {
        assetId,
        reducer,
        width,
        f,
        existingWidth
      })
      res.redirect(["/img", assetId, "actual"].join("/"))
      return
    }

    logger.debug("replacing requested width", {
      assetId,
      reducer,
      width,
      f,
      existingWidth
    })
    res.redirect(["/img", assetId, reducer, existingWidth].join("/"))
    return
  }
}
