import { Knex } from "knex"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { datedToMillis } from "../../core/date/DatedToMillis"
import { maybeDatedToLocal } from "../../core/date/Localtime"
import { max } from "../../core/math/Vector"
import { Settings } from "../../core/settings/Settings"
import { TagSep } from "../../core/tags/TagPaths"
import { blank, notBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { stringify } from "../../fe/JSON"
import { Defined } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { knex } from "../db/Knex"
import { Asset } from "../model/Asset"
import { queryTermToFuzzyDate } from "./DateQuery"
import {
  NormieQuerySymbols,
  Query,
  Term,
  hasNsTerm,
  isAndClause,
  isTerm,
  queryToString
} from "./Query"
import {
  AssetTerm,
  AssetTermBoolNs,
  AssetTermNamespace,
  AssetTermNs,
  isAssetTerm,
  normalizeQuery
} from "./QueryNormalizer"
import { parseQuery_ } from "./QueryParser"

const logger = lazy(() => mkLogger("query.QueryToSql"))

/**
 * @param qb NOTE: SELECT CLAUSES MUST BE SET ON QB ALREADY. I may have to clone and union
 * the heck out of this poor thing if q is an OrClause.
 * @param q MUST BE NORMALIZED.
 */
function applyQuery(
  qb: Knex.QueryBuilder,
  q: Query,
  mp2volsha?: Map<string, string>
) {
  if (isTerm(q)) {
    return applyTerm(qb, q, mp2volsha)
  } else {
    for (const ea of q.queries) {
      qb = isAndClause(q)
        ? qb.andWhere(qb2 => applyQuery(qb2, ea, mp2volsha))
        : qb.orWhere(qb2 => applyQuery(qb2, ea, mp2volsha))
    }
  }
  return qb
}

function applyTerm(
  qb: Knex.QueryBuilder,
  t: Term,
  mp2volsha?: Map<string, string>
) {
  return isAssetTerm(t) ? applyAssetTerm(qb, t) : applyTagTerm(qb, t, mp2volsha)
}

function toBoolean(t: Term) {
  const b = isTrue(t.term)
  const not = t.not === true
  return not ? !b : b
}

function toBool(t: Term) {
  return toBoolean(t) ? 1 : 0
}

function esc(s: string): string {
  return s.includes("*")
    ? s.split("*").map(esc).join(" * ")
    : `"${toS(s).replace(/"/g, '""')}"`
}

function andOrMaybeNull({
  qb,
  column,
  whereNull
}: {
  qb: Knex.QueryBuilder
  column: string
  whereNull: boolean
}) {
  return whereNull ? qb.whereNull(column) : qb.whereNotNull(column)
}

function applyAssetDateTerm(qb: Knex.QueryBuilder, t: AssetTerm) {
  const op: Defined<Term["op"]> = t.op ?? "="

  const colName =
    t.ns === "date"
      ? "Asset.capturedAtLocal"
      : t.ns === "updated"
      ? "Asset.updatedAt"
      : undefined
  if (colName == null) {
    throw new Error("internal error: unexpected namespace for " + stringify(t))
  }

  // Does this column use "local centiseconds", or a simple millis timestamp?
  const timeFunc =
    colName === "Asset.updatedAt" ? datedToMillis : maybeDatedToLocal

  const fd = queryTermToFuzzyDate(t.term)
  const a = timeFunc(fd)
  const nextDT = fd?.following()?.toDateTime()
  const next = timeFunc(nextDT?.minus({ second: 1 }))
  const b = max([a, next])

  if (fd == null || a == null || b == null) {
    throw new Error(
      queryToString(t, NormieQuerySymbols) + " can't be parsed as a date"
    )
  }

  // "fd" is a fuzzy date, which is only precise to at best a day, and as most as a year.
  // "a" is the beginning moment of what "fd" represents. b is the end moment.

  if (op === "=") {
    // we want the entire range of times that match the given prefix.
    return qb.whereBetween(colName, [a, b])
  } else if (op === "<") {
    return qb.where(colName, op, a)
  } else if (op === "<=") {
    return qb.where(colName, op, b)
  } else if (op === ">=") {
    return qb.where(colName, op, a)
  } else if (op === ">") {
    return qb.where(colName, op, b) //
  } else {
    throw new Error("internal error: unexpected op for " + stringify(t))
  }
}

function applyAssetTerm(
  qb: Knex.QueryBuilder,
  t: AssetTerm
): Knex.QueryBuilder {
  switch (t.ns as AssetTermNamespace) {
    case AssetTermNs.date:
    case AssetTermNs.updated:
      return applyAssetDateTerm(qb, t)
    case AssetTermNs.hidden:
      return qb.where("Asset.hidden", toBool(t))
    case AssetTermNs.excluded:
      return andOrMaybeNull({
        qb,
        column: "Asset.excludedAt",
        whereNull: toBool(t) === 0
      })
    case AssetTermNs.deleted:
      return andOrMaybeNull({
        qb,
        column: "Asset.deletedAt",
        // trash:true  -> 1 -> where NOT NULL
        // trash:false -> 0 -> where NULL
        whereNull: toBool(t) === 0
      })
    case AssetTermNs.liked:
      return qb.where(
        "Asset.rating",
        toBoolean(t) ? ">=" : "<",
        Settings.likeRating.valueOrDefault
      )
    default:
      throw new Error("not asset term:" + queryToString(t))
  }
}

function handleFsTagTerm(t: Term, mp2volsha?: Map<string, string>): string {
  if (mp2volsha == null || t.ns !== "fs") return t.term
  const arr = t.term.split("/")
  const firstIndex = arr.findIndex(notBlank)
  if (firstIndex === -1) return t.term // wtf
  const first = arr[firstIndex]
  arr[firstIndex] = mp2volsha.get(first) ?? first
  return arr.join("/")
}

function applyTagTerm(
  qb: Knex.QueryBuilder,
  t: Term,
  mp2volsha?: Map<string, string>
): Knex.QueryBuilder {
  const startsWith = t.term.startsWith("/")
  const endsWith = t.op === "="

  const term = handleFsTagTerm(t, mp2volsha)

  if (startsWith || endsWith) {
    // no FTS: just query Tag._path:

    return qb.whereIn(
      "Asset.id",
      knex()("AssetTag")
        .join("Tag", "Tag.id", "AssetTag.tagId")
        .select("assetId")
        .whereLike(
          "Tag._path",
          t.ns +
            (startsWith ? "" : "%") +
            term.replace(/\//g, TagSep).replace(/\*/g, "%") +
            TagSep +
            (endsWith ? "" : "%")
        )
    )
  }

  let subquery = knex()("AssetTag")
    .join("tag_fts", "tag_fts.rowid", "AssetTag.tagId")
    .select("assetId")
  // Note: we don't have to do anything special for globs: see
  // https://sqlite.org/fts5.html#fts5_prefix_queries
  const path = "path:" + esc(term.replace(/\//g, " + "))
  if (blank(t.ns)) {
    if (isTrue(t.not)) {
      throw new Error(
        "Cannot negate without a namespace (" + queryToString(t) + ")"
      )
    } else {
      subquery = subquery.where("tag_fts", "MATCH", path)
    }
  } else {
    subquery = subquery.where(
      "tag_fts",
      "MATCH",
      "(root:" + esc(t.ns) + (isTrue(t.not) ? " NOT " : " AND ") + path + ")"
    )
  }
  return qb.whereIn("Asset.id", subquery)
}

export function defaultAssetQuery() {
  return Asset.ops()
    .query()
    .where("Asset.shown", 1)
    .join("AssetTag", "AssetTag.assetId", "Asset.id")
}

/**
 * @param qb must WHERE the Asset table
 */
export function queryStringToSql_(
  q: string,
  qb: Knex.QueryBuilder = defaultAssetQuery()
): Knex.QueryBuilder<any, any> {
  const query = parseQuery_(q)
  if (query == null) {
    logger().warn("assetQuery(" + q + "): parsed to null")
    return qb
  }
  return queryToSql(query, qb)
}

export function queryToSql(
  q: Query,
  qb: Knex.QueryBuilder = defaultAssetQuery(),
  mp2volsha?: Maybe<Map<string, string>>
): Knex.QueryBuilder<any, any> {
  q = normalizeQuery(q)
  // If we have an excluded or deleted term, we don't want excluded AND deleted assets to be invisible
  if (
    !hasNsTerm(q, AssetTermBoolNs.hidden) &&
    !hasNsTerm(q, AssetTermBoolNs.excluded) &&
    !hasNsTerm(q, AssetTermBoolNs.deleted)
  ) {
    qb = qb.andWhere({
      "Asset.hidden": 0,
      "Asset.excludedAt": null,
      "Asset.deletedAt": null
    })
  }
  return qb.andWhere(and => applyQuery(and, q, mp2volsha))
}
