import e from "express"
import { compact, isNotEmpty, removeSame } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { EndableRanks } from "../async/Endable"
import { EndableWrapper } from "../async/EndableWrapper"
import { endStream, remoteDesc } from "../fs/Streams"
import { ServerSentEvent, mkEventStream } from "../web/EventSource"
import { wrap } from "./Handlers"

export abstract class ServerSentEventsRouter<
  E extends string | undefined
> extends EndableWrapper {
  // HEY FUTURE ME this is vacuumed automatically by "close" and "error" events.
  readonly streams: e.Response[] = []

  constructor(readonly path: string) {
    super("SSERouter(" + path + ")", () => this.close(), EndableRanks.first)
  }

  router(): e.Router {
    return e.Router().get(this.path, wrap(this.name, this.updates.bind(this)))
  }

  close() {
    this.streams.map(endStream)
    this.streams.length = 0
  }

  async updates(req: e.Request, res: e.Response) {
    if (this.ended) {
      res.sendStatus(503)
      return
    }
    this.logger.info("Sending updates to " + (req.ip ?? req.ips))
    const desc = remoteDesc(req.socket)
    res.writeHead(200, {
      "Content-Type": "text/event-stream; charset=utf-8",
      "Cache-Control": "no-cache",
      Connection: "keep-alive",
      "X-Accel-Buffering": "no"
    })
    this.streams.push(res)
    res.once("error", error => {
      removeSame(this.streams, res)
      this.logger.warn("onError(" + desc + ")", { error })
    })
    res.once("close", () => {
      removeSame(this.streams, res)
      this.logger.debug(
        "stream to " + desc + " closed. (" + this.streams.length + " remain)"
      )
    })
    const firstEvents = compact(this.firstEvents(req, res))
    if (isNotEmpty(firstEvents)) {
      this.logger.info("firstEvents", { firstEvents })
      res.write(mkEventStream(...firstEvents))
    }
  }

  endStream(res: e.Response) {
    void endStream(res)
    removeSame(this.streams, res)
  }

  writeToStreams(...events: Maybe<ServerSentEvent<E>>[]) {
    const payload = mkEventStream(...events)
    this.logger.info("writeToStreams", { events, payload })
    if (!blank(payload)) {
      for (const ea of this.streams) {
        if (true === ea?.closed || ea?.socket == null) {
          this.endStream(ea)
        } else {
          try {
            ea.write(payload)
          } catch (error) {
            if (ea.socket == null || ea.closed) {
              this.endStream(ea)
            } else {
              this.logger.warn("writeToStreams failed to write to stream", {
                stream: remoteDesc(ea.socket),
                error
              })
            }
          }
        }
      }
    }
  }

  // HEY FUTURE ME: if you make this async, and implementations write
  // additional events to the response (like the health router), be sure to
  // have them delay a bit before writing, or else they may be written before
  // the first events are sent.
  abstract firstEvents(
    req: e.Request,
    res: e.Response
  ): Maybe<ServerSentEvent<E>>[]
}
