import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { stringify } from "../../fe/JSON"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { KB } from "../../fe/Units"
import { mkLogger } from "../Logger"
import { pathToDb } from "../db/Schemas"
import { errorToS } from "../error/Error"
import { PosixFile } from "../fs/PosixFile"
import { SimpleFile, toNativePath_ } from "../fs/SimpleFile"
import { stat_ } from "../fs/Stat"
import { librarySettingsFile } from "../settings/SettingsIO"
import {
  libraryDataDirPosixFile,
  libraryPreviewsDirPosixFile
} from "./LibraryDirs"

const logger = lazy(() => mkLogger("dir.IsLibraryDir"))

function pathToS(f: Maybe<string | SimpleFile>) {
  return f == null ? "(null)" : stringify(toNativePath_(f))
}

export async function isLibraryDir(
  dir: Maybe<string | PosixFile>
): Promise<boolean> {
  return null == (await whyNotLibraryDir(dir))
}

export async function whyNotLibraryDir(
  dir: Maybe<string | PosixFile>
): PromiseMaybe<string> {
  return logger().tap({
    msg: "whyNotLibraryDir(" + dir + ")",
    result: await _whyNotLibraryDir(dir)
  })
}

async function _whyNotLibraryDir(
  dir: Maybe<string | PosixFile>
): PromiseMaybe<string> {
  if (blank(dir)) return "blank path"
  const nativePath = toNativePath_(dir)
  try {
    const s = await stat_(nativePath)
    if (s == null || !s.isDirectory()) {
      return "not a directory"
    }
  } catch (error) {
    return "cannot read: " + errorToS(error)
  }

  // We need at least a model db file and a settings.toml:
  const dataDir = libraryDataDirPosixFile(nativePath)!
  // calling .clear() on the library directory may purge all cached preview
  // PosixFiles, which we should avoid if possible.
  if (
    true !== (await dataDir?.isDirectory({ refresh: true, logLevel: "trace" }))
  ) {
    return (
      "library data directory, " + pathToS(dataDir) + ", is not a directory"
    )
  }

  const settingsToml = librarySettingsFile(nativePath)?.clear()
  if (settingsToml == null || !(await settingsToml.isNonEmptyFile())) {
    return (
      "library settings, " + pathToS(settingsToml) + ", is empty or missing"
    )
  }

  const modelDb = pathToDb(dataDir, "models")?.clear()
  if (modelDb == null || !(await modelDb?.isNonEmptyFile(KB))) {
    return "library database, " + pathToS(modelDb) + ", is empty or missing"
  }

  const previews = libraryPreviewsDirPosixFile(nativePath)
  if (
    true !== (await previews?.isDirectory({ refresh: true, logLevel: "trace" }))
  ) {
    return "library previews, " + pathToS(previews) + ", is empty or missing"
  }

  return
}
