import bodyParser = require("body-parser")

function setHeaders(res: express.Response, path: string) {
  // TODO: why isn't `serve-static` doing this for us?
  if (path.endsWith(".js")) {
    res.setHeader("Content-Type", "application/javascript")
  }
}

const logger = mkLogger("web.WebService")

const openFileService = (ea: SimpleFileOrString) =>
  showFileInFolder(PosixFile.for(ea))

/**
 * Class that handles both initial "welcome" mode that lets users choose their
 * library home, and "normal" mode which exposes a given library.
 */
export class WebService {
  private readonly service: Service
  private readonly app: express.Application

  constructor() {
    this.app = express()
    this.service = new Service({
      name: "web"
    })
    void this.setup()
  }

  private readonly setup = lazy(async () => {
    logger.debug("setup(): started")

    // Prevent Firefox (which doesn't seem to do very nice socket hygiene, and
    // causes SIGPIPES) from killing the webservice:
    process.on("SIGPIPE", err => {
      logger.info("Caught SIGPIPE", err)
    })

    Settings.libraryDir.watchLater(() => libraryRouter.unset())

    if (await rejected(this.service.ready)) {
      logger.error("setup(): failed, service.ready was rejected")
      return undefined
    }

    await setupShimDelegates()

    const app = this.app

    app.use(new RequestLogger().router)

    app.use((_, res, next) => {
      res.locals.cspNonce = crypto.randomBytes(16).toString("hex")
      next()
    })

    if (Settings.trustProxy.hasValue()) {
      const str = Settings.trustProxy.value!
      const value =
        str === "true"
          ? true
          : str === "false"
          ? false
          : mapGte0Or<string | number>(str, i => i, str)
      app.set("trust proxy", value)
      logger.info("trust proxy set", { value })
    }

    app.use(expressErrorHandlerWithToast)

    // The Sentry requestHandler must be the first middleware on the app to
    // catch all errors.
    if (sentryEnabled()) {
      try {
        app.use(require("@sentry/node").Handlers.requestHandler())
      } catch (error) {
        logger.warn("Failed to initialize Sentry requestHandler", { error })
      }
    }

    if (
      Settings.disabledHelmetMiddleware.valueOrDefault.includes(
        HelmetPlugins.all
      )
    ) {
      logger.warn(
        "DANGER: disabledHelmetMiddleware includes 'all': not enabling any Helmet middleware."
      )
    } else {
      if (
        !Settings.disabledHelmetMiddleware.valueOrDefault.includes(
          HelmetPlugins.contentSecurityPolicy
        )
      ) {
        app.use(cspHandler)
      }
      const helmetOptions: HelmetOptions = { contentSecurityPolicy: false }
      for (const ea of Settings.disabledHelmetMiddleware.valueOrDefault.filter(
        str => str !== HelmetPlugins.all
      )) {
        ;(helmetOptions as any)[ea] = false
      }
      app.use(helmet(helmetOptions))
    }

    app.use(addXSettingsHandler)

    // app.use(helmet.referrerPolicy({ policy: "origin" }))
    app.use(bodyParser.json())
    app.use(bodyParser.urlencoded({ extended: false }))

    const publicRoot = ProjectPath.Public()
    const viewsRoot = ProjectPath.Views()

    logger.info("setup()", {
      libraryDir: Settings.libraryDir.valueOrDefault,
      publicRoot,
      viewsRoot
    })

    // http://expressjs.com/en/5x/api.html#etag.options.table
    libraryHasSettings.watch(() => app.set("etag", libraryHasSettings()))

    app.set("views", viewsRoot)
    app.set("view engine", "pug")

    // ping, splash, and status should always be mounted:
    app.use(new PingRouter().router())

    app.use(viewRouter({ path: "/splash", view: "splash" }))
    app.use(new ApiSplashRouter().router())

    app.use(viewRouter({ path: "/health", view: "health" }))
    app.use(new ApiHealthRouter().router())
    app.use(new AdminPostRouter().router())

    app.get("/status", (_req, res) => res.redirect("/health"))

    // Support when packaged:
    app.use(await serveStaticGz(publicRoot))
    // Support during development:
    app.use(express.static(publicRoot, { setHeaders }))

    app.use(new AboutRouter().router())
    app.use(new WelcomeRouter().router())
    app.use(new PlansRouter().router())
    app.use(settingsRouter())

    // Only in testing:
    if (!isPacked()) {
      app.use("/die", (_req: express.Request, res: express.Response) => {
        logger.info("/die was requested", { url: _req.originalUrl })
        res.sendStatus(HttpStatus.Accepted) // accepted
        void exit({ reason: "/die", status: 1 })
      })
      app.use(
        "/test-sentry",
        (_req: express.Request, res: express.Response) => {
          sendToSentry(
            new WrappedError("test message sent to sentry at " + new Date())
          )
          res.sendStatus(HttpStatus.Accepted)
        }
      )
    }

    app.use(
      (
        req: express.Request,
        res: express.Response,
        next: express.NextFunction
      ) => {
        if (ending()) {
          logger.info("ending(): returning 500", {
            requestUri: requestedUri(req)
          })
          return res.sendStatus(500)
        }
        return routerForRunState()(req, res, next)
      }
    )

    // The error handler must be before any other error middleware
    if (sentryEnabled()) {
      try {
        app.use(require("@sentry/node").Handlers.errorHandler())
      } catch (error) {
        logger.warn("Failed to initialize Sentry error handling", { error })
      }
    }

    // If the library exists, we must wait for the db to spin up before
    // accepting web requests.

    const httpPort = Settings.httpPort.valueOrDefault
    const expose = Settings.exposeNetworkWithoutAuth.valueOrDefault

    const library = Library.instance()
    if (library != null) {
      HealthCheck.addLoadingMsg("Loading library...")
    } else {
      HealthCheck.addLoadingMsg("Starting...")
    }

    const cb = async () => {
      logger.info("setup(): http server callback", { httpPort, expose })

      const arr: Setting[] = [Settings.httpPort]
      if (library != null) arr.push(Settings.libraryDir)
      stdoutWriteSettings(...arr)

      // At this point we're not in a rush--we got the splash router up and
      // running, and can complete other setup steps.

      if (library == null) {
        // warm these up for the settings router:
        void sids(commandTimeoutMs()).catch(err =>
          logger.warn("failed to prefetch sids", err)
        )
        void volumes().catch(err =>
          logger.warn("failed to prefetch volumes", err)
        )
      } else {
        await library.ready
        libraryRouter.unset()
      }

      // Always set up all library health checks-they will auto-disable if
      // we're starting with no library.
      await libraryHealthCheckSetup()
      await HealthCheck.awaitSettled()

      if (library != null) {
        StdoutWrite.restartSync()
      }
    }
    const s = expose
      ? app.listen(httpPort, cb)
      : app.listen(httpPort, Settings.localhost.valueOrDefault, cb)

    s.on("error", (err: any) => {
      logger.warn("http.server.onError()", err)
      if (err.code === "EADDRINUSE") {
        void exit({
          reason:
            "Port " +
            httpPort +
            " is already in use (is PhotoStructure already running?)" +
            FatalErrorFlag +
            DoNotSendErrorFlag,
          status: 1
        })
      }
    })

    return new EndableServer("http.WebService", s)
  })
}

