const splashRedirectRouter = lazy(() => redirectRouter("/splash"))
const healthRedirectRouter = lazy(() => redirectRouter("/health"))
const welcomeRedirectRouter = lazy(() => redirectRouter("/welcome"))

// function stateRouter(): express.Router {
//   const runState = HealthCheck.runState()
//   console.log("stateRouter()", { runState })
//   return routers[runState]?.() ?? healthRedirectRouter()
// }

const settingsRouter = lazy(() => new SettingsRouter().router())

const libraryRouter = lazy(() => {
  const library = Library.instance()

  if (library == null) {
    logger.warn("libraryRouter invoked before library is set up.")
    setImmediate(() => libraryRouter.unset())
    return
  }
  logger.info("Setting up library router for " + map(library, ea => ea.rootDir))

  const previews = library.previews()
  const updatesProvider = new ProgressProvider()
  const app = express.Router()

  app.use(new ManifestRouter().router())

  app.use(new ApiSystemRouter(openFileService).router())

  // /api/tag/
  // /api/t/:tagId/assets
  // /api/t/:tagId/child-tags
  app.use(new ApiTagRouter().router())

  // /api/asset/:assetId
  // /api/asset/:assetId/info
  // /api/asset/:assetId/streams
  app.use(new ApiAssetRouter(previews).router())

  // /api/search?query&orderBy&desc
  app.use(new ApiSearchRouter().router())

  // /tag/*
  // /asset/*
  app.use(new SpaRouter().router())

  // /openAssetFile/:assetFileId
  app.use(new OpenAssetFileRouter(openFileService).router())

  // ImgActualRouter must be before ImgRouter:
  // /img/:assetId/actual
  // /dl/:assetId/:assetFileId
  app.use(new ImgActualRouter(previews).router())

  // /img/:assetId/:reducer/:width
  app.use(new ImgRouter(previews).router())

  // /video/:assetId
  app.use(new VideoRouter(previews).router())

  // /updates
  app.use(new ProgressRouter(updatesProvider).router())

  app.use("/", (req: express.Request, res: express.Response) => {
    logger.debug("in root route", {
      res_headersSent: res.headersSent,
      res_headers: res.getHeaders()
    })
    res.redirect(302, mkHomeFullPath(req.query as any))
  })
  return app
})

later(() => {
  Settings.libraryDir.watchLater(() => libraryRouter.unset())
})

function routerForRunState(): Router {
  const runState = HealthCheck.runState()
  switch (runState) {
    case RunStates.loading:
      return splashRedirectRouter()
    case RunStates.welcome:
      return welcomeRedirectRouter()
    case RunStates.ready:
      return (
        libraryRouter() ?? libraryRouter.refresh() ?? healthRedirectRouter()
      )
    case RunStates.failed:
      return healthRedirectRouter()
    default:
      throw new UnreachableCaseError(runState)
  }
}
