import { Request } from "express"
import { toS } from "../../fe/toS"

const xhrRE = /^XMLHttpRequest$/i

/**
 * @return true if the request is from XMLHttpRequest or our call to fetch in login-code.ts
 */
export function isXhr(req: Request) {
  // console.log("isXhr:", {
  //   req_xhr: req.xhr,
  //   xrw: req.header("x-requested-with"),
  //   XRH: req.header("X-Requested-With")
  // })
  return req.xhr || xhrRE.exec(toS(req.header("X-Requested-With"))) != null
}

export function referrer(req: Request) {
  return req.header("Referer")
}
