import { pidExists } from "batch-cluster"
import { env } from "process"
import { BoundedList } from "../../core/BoundedList"
import { FifoCache } from "../../core/FifoCache"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { thenMap } from "../../core/async/Promise"
import { HealthCheck } from "../../core/health/HealthCheck"
import { isElectron, isWin } from "../../core/platform/Platform"
import { isPaused } from "../../core/work/IsPaused"
import { filterInPlace, isEmpty, isNotEmpty, uniq } from "../../fe/Array"
import { isTrue } from "../../fe/Boolean"
import { minuteMs, secondMs } from "../../fe/Date"
import { flatten } from "../../fe/Flatten"
import { getOrSet } from "../../fe/Map"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import {
  ProgressStateWithAssets,
  SyncStatus,
  isRebuildProgress,
  noRecentAssetIdsProgress
} from "../../fe/api/ProgressState"
import { toS } from "../../fe/toS"
import { Asset } from "../model/Asset"
import { AssetFile } from "../model/AssetFile"
import { Progress } from "../model/Progress"

const logger = lazy(() => mkLogger("web.ProgressWithAssets"))

export class ProgressProvider {
  // indexed by progress ID:
  readonly priorRecentAssets = new FifoCache<number[]>(15, 15 * secondMs)
  constructor(readonly maxLen = 10) {}

  async updates(): PromiseMaybe<{ states: ProgressStateWithAssets[] }> {
    return thenMap(this.states(), states => ({
      states
    }))
  }

  async states(): PromiseMaybe<ProgressStateWithAssets[]> {
    if (isPaused()) {
      return [
        {
          volume: "",
          state: "paused",
          hed: "Paused",
          dek: [
            "Processing is paused. Resume via the navigation menu" +
              (isElectron ? " or the system tray menu" : "") +
              "."
          ],
          recentAssetIds: []
        }
      ]
    }

    const errors = await HealthCheck.errorResults()

    if (isNotEmpty(errors)) {
      logger().warn("health check is failing", { errors })
      return [
        {
          volume: "",
          state: "paused",
          hed: "Health checks failed. Click for details.",
          dek: uniq(flatten(errors.map(ea => ea.msg))),
          recentAssetIds: []
        }
      ]
    }
    const result = this.recentProgress()
    logger().debug("recentProgressWithAssets()", result)

    if (isEmpty(result)) {
      if (isTrue(env.TEST_PROGRESS)) {
        return this.testProgress()
      }
      // if (!isPaused() && isRecentMs(Library.instance()?.start, cmdTimeoutMs())) {
      //   return [
      //     {
      //       volume: "",
      //       hed: "Starting synchronization...",
      //       recentAssetIds: []
      //     }
      //   ]
      // }
    }
    return result
  }

  recentProgress() {
    const progresses = Progress.ops().allf(
      q =>
        q
          .where("updatedAt", ">", Date.now() - minuteMs)
          .whereNotNull("state")
          .orderBy("volume") // just for stable sort
    )
    filterInPlace(progresses, ea => {
      if (!pidExists(ea.pid) && ea.state !== "done") {
        // crashed progress!
        logger().warn("Incomplete progress is from a dead process", {
          progress: ea
        })
        ea.upsert({ state: null as any })
        return false
      }
      return true
    })
    const result: ProgressStateWithAssets[] = []
    // Only show assets in at most one progress report:
    const seenAssetIds = new Set<number>()

    for (const progress of progresses) {
      const isDone = progress.state === "done"
      const ss = progress.toSyncState()
      const recentAssetIds = this.priorRecentAssets.get(toS(progress.id)) ?? []
      if (!isDone) {
        // don't add recent assets to done progress!
        const recent = noRecentAssetIdsProgress(ss)
          ? []
          : AssetFile.recentAssetIdsByUriRoot(
              isRebuildProgress(ss) ? undefined : ss.uri,
              minuteMs,
              this.maxLen + seenAssetIds.size
            )
        const newRecentAssetIds = recent.filter(
          ea => !seenAssetIds.has(ea) && !recentAssetIds.includes(ea)
        )
        if (
          isNotEmpty(recentAssetIds) &&
          newRecentAssetIds.length >= this.maxLen
        ) {
          // The UI looks wonky when all assets change, so keep the last one
          // in the next one:
          newRecentAssetIds.splice(0, this.maxLen - 1) // delete all but the first item
        }
        recentAssetIds.unshift(...newRecentAssetIds)
        recentAssetIds.length = this.maxLen
        this.priorRecentAssets.set(toS(progress.id), recentAssetIds)
        for (const ea of recentAssetIds) seenAssetIds.add(ea)
      }
      result.push({ ...ss, recentAssetIds })
    }

    return result
  }

  //
  // used for testing
  //

  private readonly testProgressStatesByVolume = new Map<
    string,
    BoundedList<number>
  >()

  async testAssetIds(uri: string) {
    const l = getOrSet(
      this.testProgressStatesByVolume,
      uri,
      () => new BoundedList(this.maxLen)
    )
    const assetId = Asset.dbl.pluckFirstf(q =>
      Asset.shownUnhidden(q).select("id").orderByRaw("random()").first()
    ) as Maybe<number>
    if (assetId != null) l.push(assetId)
    return l.toA()
  }

  async testProgress() {
    const index = Math.round(Date.now() / (30 * secondMs)) % 3
    const volume = (
      isWin ? ["C:", "D:", "E:"] : ["/", "/home/bob/pictures", "/mnt/nas"]
    )[index]
    return [
      {
        volume,
        state: "processing" as SyncStatus,
        hed: "Processing, about 32 minutes remaining",
        dek: [
          "Processing C:\\Users\\mrm\\Pictures\\20161214-pixel\\DCIM\\Camera\\IMG_20160903_090736.jpg",
          "Processed 445 images and videos. 1,386 remain to be processed."
        ],
        completePct: 40,
        incompletePct: 25,
        scanningPct: 35,
        recentAssetIds: await this.testAssetIds(volume)
      }
    ]
  }
}
