import e from "express"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { wrap } from "../../core/express/Handlers"
import { requestedUri } from "../../core/express/RequestMeta"
import { LibraryUIDStore } from "../../core/fs/UIDStore"
import { Settings } from "../../core/settings/Settings"
import { URI } from "../../core/uri/URI"
import { Maybe } from "../../fe/MaybeTypes"

const logger = lazy(() => mkLogger("web.ManifestRouter"))

/**
 * @see https://developer.mozilla.org/en-US/docs/Web/Manifest
 * @see https://web.dev/learn/pwa/web-app-manifest/
 */
export class ManifestRouter {
  router(): e.Router {
    return e
      .Router()
      .get(
        "/site.webmanifest",
        wrap("SiteManifest", this.siteManifest.bind(this))
      )
  }

  async siteManifest(req: e.Request, res: e.Response, _next: e.NextFunction) {
    const id = await LibraryUIDStore()
      ?.readUid_()
      .catch(() => undefined)
    const requestUri = requestedUri(req, {
      protocol: "http",
      host: Settings.hostname.value
    })
    const json = await siteManifest({ id, requestUri })
    logger().debug("siteManifest", json)
    res.type("application/manifest+json").json(json)
  }
}

export async function siteManifest({
  requestUri,
  id
}: {
  requestUri: URI
  id: Maybe<string>
}) {
  const start_url = requestUri
    .with({
      path: Settings.defaultHomepagePath.valueOrDefault,
      query: null,
      fragment: null
    })
    .toString()

  return {
    id: id ?? "com.photostructure",
    name: "PhotoStructure",
    start_url,
    display: "fullscreen",
    icons: [
      {
        src: "/192.png",
        sizes: "192x192",
        type: "image/png"
      },
      {
        src: "/512.png",
        sizes: "512x512",
        type: "image/png"
      },
      {
        src: "/images/logo-gradient.svg",
        sizes: "any",
        type: "image/svg+xml"
      }
    ],
    theme_color: "#845BAC",
    background_color: "#845BAC"
  }
}
