import { Task } from "batch-cluster"
import { parseJSON } from "../../core/JSON"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { ee } from "../../core/event/EventEmitter"
import { splitLines } from "../../core/fs/CRLF"
import { isProgressEvt } from "../../core/progress/ProgressEvt"
import { compactBlanks } from "../../fe/Array"
import { errorFromJson } from "../../fe/Error"
import { stringify } from "../../fe/JSON"
import { isNumber } from "../../fe/Number"
import { WorkRequest, WorkRequestMethod } from "./WorkRequests"

const logger = lazy(() => mkLogger("worker.RequestTask"))

export class RequestTask<N extends WorkRequestMethod> extends Task<any> {
  constructor(readonly request: WorkRequest<N>) {
    super(stringify(request), input => this.#parse(input))
  }

  // MUST BE KEPT IN SYNC WITH ./Worker.onStdinData!

  #parse(buff: string) {
    // logger().debug("Got data from worker", { request: this.request })
    for (const line of compactBlanks(splitLines(buff))) {
      const response = parseJSON(line)
      if (response == null) {
        logger().warn("ignoring non-json line from worker", { line })
      } else if (response.error != null) {
        logger().warn("worker threw error", { response })
        throw errorFromJson(response.error)
      } else if (isNumber(response.id)) {
        if (response.id !== this.request.id) {
          return logger().throw(
            `bad request: #parse given mismatching requests (${response.id} != ${this.request.id})`,
            {
              response,
              request: this.request
            }
          )
        } else {
          return response.response
        }
      } else if (isProgressEvt(response)) {
        ee().emit("progress", response)
      } else {
        logger().warn("ignoring json line from worker", { response })
      }
    }

    return logger().throw("bad request: #parse missing valid response")
  }
}
