import { normalizeRotation } from "../../fe/Rotation"

export function rotateSquareMatrix<T>(m: T[], degrees: number) {
  const d = normalizeRotation(degrees)
  // clone m so we don't cause side-effects:
  return d === 0 ? m : new SquareMatrix([...m]).rotate(degrees).m
}

export class SquareMatrix<T> {
  readonly rows: number
  constructor(
    readonly m: T[],
    readonly ary = 1
  ) {
    this.rows = Math.sqrt(m.length / ary)
    if (this.rows !== Math.floor(this.rows)) {
      throw new Error(
        `Only square tuple matrices are supported: ${
          this.ary === 1 ? "" : this.ary
        }(${this.rows}${this.rows}) != ${m.length}`
      )
    }
  }

  index(row: number, col: number) {
    return this.ary * (col + row * this.rows)
  }

  swap(index_a: number, index_b: number) {
    for (let i = 0; i < this.ary; i++) {
      const n = this.m[index_a + i]
      this.m[index_a + i] = this.m[index_b + i]
      this.m[index_b + i] = n
    }
  }

  transpose(): this {
    for (let row = 0; row < this.rows; row++) {
      for (let col = row + 1; col < this.rows; col++) {
        this.swap(this.index(row, col), this.index(col, row))
      }
    }
    return this
  }

  reverseRows(): this {
    for (let col = 0; col < this.rows; col++) {
      for (let row = 0; row < this.rows / 2; row++) {
        this.swap(this.index(row, col), this.index(this.rows - 1 - row, col))
      }
    }
    return this
  }

  reverseCols(): this {
    for (let row = 0; row < this.rows; row++) {
      for (let col = 0; col < this.rows / 2; col++) {
        this.swap(this.index(row, col), this.index(row, this.rows - 1 - col))
      }
    }
    return this
  }

  reverse(): this {
    const l = this.rows ** 2
    for (let i = 0; i < l / 2; i++) {
      this.swap(i, l - 1 - i)
    }
    return this
  }

  rotate(degrees: number): this {
    switch (normalizeRotation(degrees)) {
      case 0:
        break
      case 90:
        this.transpose()
        this.reverseCols()
        break
      case 180:
        this.reverse()
        break
      case 270:
        this.transpose()
        this.reverseRows()
        break
      default:
        throw new Error("unsupported rotate(" + degrees + ")")
    }
    return this
  }
}
