import e from "express"
import { ending } from "../../core/async/Endable"
import { HttpStatus } from "../../core/web/HttpStatus"

export class PingRouter {
  router(): e.Router {
    // If we can bind to the port, we're up. We may not be _happy_, but we're
    // up at least!
    return e
      .Router()
      .get("/ping", (_, res) =>
        res.sendStatus(ending() ? HttpStatus.ServiceUnavailable : HttpStatus.OK)
      )
  }
}
