import e from "express"
import { AppName } from "../../core/AppName"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { isDev } from "../../core/NodeEnv"
import { p } from "../../core/licensing/Licensing"
import { isLocalhost } from "../../core/net/ip"
import { isLinux, isMac, isWin } from "../../core/platform/Platform"
import { isDocker } from "../../core/platform/isDocker"
import { Settings } from "../../core/settings/Settings"
import { isPaused } from "../../core/work/IsPaused"
import { isTrue } from "../../fe/Boolean"
import { thenOrTimeout } from "../../fe/ThenOrTimeout"
import {
  isChrome,
  isFirefox,
  isIpad,
  isIphone,
  isSafari
} from "../../fe/UserAgents"
import { Tag } from "../model/Tag"

function defaults() {
  return {
    // Don't hit the db if we're looking at the splash or welcome page:
    rootTags: Tag.db() == null ? undefined : Tag.roots(),
    title: AppName()
  }
}

const logger = lazy(() => mkLogger("web.PugHelpers"))

export type TemplateName =
  | "about"
  | "app"
  | "settings"
  | "welcome"
  | "splash"
  | "health"

export async function render(
  req: e.Request,
  res: e.Response,
  template: TemplateName,
  model: any = {}
) {
  const ua = req.headers["user-agent"]
  res.setHeader("Feature-Policy", "autoplay 'self'")
  const bodyClass: string[] = []

  const isElectron =
    (true === ua?.toLowerCase().includes("electron") && isLocalhost(req.ip)) ||
    isTrue(req.query.electron)
  if (isElectron) {
    bodyClass.push("electron")
    if (isMac) bodyClass.push("mac")
    else if (isWin) bodyClass.push("win")
    else if (isLinux) bodyClass.push("linux")
  }

  if (isDev) bodyClass.push("is-dev")

  // file deepcode ignore reDOS: <these RE can't cause a DOS>

  if (isChrome(ua)) {
    bodyClass.push("chrome")
  } else if (isSafari(ua)) {
    bodyClass.push("safari")
  } else if (isFirefox(ua)) {
    bodyClass.push("firefox")
  }

  if (isIphone(ua)) bodyClass.push("iphone")
  else if (isIpad(ua)) bodyClass.push("ipad")

  if (isPaused()) {
    bodyClass.push("is-paused")
  }

  // req.ip will be the actual ip (not the proxy):
  if (isLocalhost(req.ip) && !isDocker()) {
    bodyClass.push("is-localhost")
  }

  try {
    if (true === (await thenOrTimeout(p(), 250))) {
      bodyClass.push("is-plus")
    }
  } catch {
    //
  }
  if (Settings.enableArchive.valueOrDefault) bodyClass.push("enable-archive")
  if (Settings.enableDelete.valueOrDefault) bodyClass.push("enable-delete")
  if (Settings.enableRemove.valueOrDefault) bodyClass.push("enable-remove")

  const options = {
    bodyClass: bodyClass.join(" "),
    isDocker: isDocker(),
    isElectron,
    isDev,
    nonce: res.locals.cspNonce,
    hasEmail: Settings.email.hasValue(),
    ...model
  }

  logger().debug("render()", { ua, template, options, headers: req.headers })
  res.render(template, {
    ...defaults(),
    ...options
  })
  return
}
