import { IncomingMessage, OutgoingMessage } from "http"

export function onFinished<T extends IncomingMessage | OutgoingMessage>(
  msg: T,
  listener: (err: Error | null, message: T) => void
): T {
  let handled = false
  msg.on("error", err => {
    if (!handled) {
      handled = true
      listener(err, msg)
    }
  })
  const l = () => {
    if (!handled) {
      handled = true
      listener(null, msg)
    }
  }
  for (const eventName of ["end", "finish", "close"]) {
    msg.on(eventName, l)
  }
  return msg
}
